.cs {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &_wrap {
        @media screen and (max-width: 800px) {
            flex-direction: column;
            row-gap: 1.2rem;
        }

        // .mona-content h2,h3,h4,h5,h6 {
        //     margin: 0;
        // }

        #ez-toc-container {
            background: transparent;
            padding: 0;
            box-shadow: none;
            border: none;
            border-radius: 0;
            width: 100%;
            margin-left: 2.4rem;

            @media screen and (max-width: 800px) {
                margin-left: 0;
            }
        }

        .ez-toc-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }
    }

    &_lf {
        width: calc(100% * 3.5 / 12);
        @media screen and (max-width: 800px) {
            width: 100%;
        }

        &--wrap {
            height: 100%;
            border-right: 0.1rem solid $color-gray4;

            @media screen and (max-width: 800px) {
                padding-bottom: var(--rowContent1);
                border-right: 0;
                border-bottom: 0.1rem solid $color-gray4;
            }
        }

        &--content {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
            position: sticky;
            top: 14rem;

            .menu-list {
                margin: 0;
                flex-direction: column;
                row-gap: var(--rowContent1);
                @media screen and (max-width: 800px) {
                    flex-direction: row;
                    flex-wrap: wrap;
                    column-gap: 1.2rem;
                }
                .menu-item {
                    display: flex;
                    flex-direction: column;
                    row-gap: 0.8rem;

                    @media screen and (max-width: 800px) {
                        width: calc(100% / 3 - 1.2rem);
                        justify-content: space-between;
                        row-gap: 0.6rem;
                    }

                    @media screen and (max-width: 600px) {
                        width: calc(100% / 2 - 1.2rem);
                        justify-content: space-between;
                        row-gap: 0.4rem;
                    }

                    @media screen and (max-width: 360px) {
                        width: 100%;
                        row-gap: 0.2rem;
                        justify-content: space-between;
                    }

                    &.current-menu-item {
                        .menu-link {
                            background: $color-gray4;
                        }
                    }

                    .menu-ic {
                        top: -0.1rem;
                        width: 1.6rem;
                        &.actived {
                            &::after {
                                transform: translate(-50%, -50%) rotate(180deg);
                            }
                        }
                    }

                    .menu-link {
                        color: $color-black-2;
                        font-weight: 700;
                        @include fz-16;
                        // align-items: start;
                        width: 100%;
                        padding: 0.8rem 0;
                        border-radius: 0.4rem;
                        // justify-content: space-between;
                        justify-content: start;
                        flex-direction: row-reverse;
                        white-space: wrap;
                        &.actived {
                            padding: 1.2rem 1.6rem;
                            background: $color-gray4;
                        }

                        @media screen and (max-width: 800px) {
                            justify-content: start;
                            padding: 0.4rem 0;
                        }

                        @media screen and (max-width: 460px) {
                            justify-content: start;
                            padding: 0.2rem 0;
                        }
                        @include hover {
                            padding: 1.2rem 1.6rem;
                            background: $color-gray4;
                        }

                        &::before {
                            display: none;
                        }
                    }

                    & > .menu-list {
                        position: relative;
                        top: unset;
                        left: unset;
                        opacity: 1;
                        transform: unset;
                        visibility: visible;
                        box-shadow: unset;
                        // margin: 0 3.2rem;
                        pointer-events: visible;
                        width: 100%;
                        height: unset;
                        row-gap: 0;
                        display: none;
                        transition: none;
                        min-width: unset;
                        // display: flex;
                        // flex-direction: column;
                        // row-gap: var(--rowContent1);
                        &::before {
                            display: none;
                        }
                        .menu-item {
                            transition: none;
                            height: unset;
                            width: 100%;
                            .menu-link {
                                @include fz-14;
                                color: $color-text;
                                font-weight: 400;
                                // padding: 0;
                                padding: 0.8rem 1.6rem;
                                transition: none;
                                height: unset;

                                @media screen and (max-width: 800px) {
                                    padding: 0.8rem 0rem;
                                }

                                &::before {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    &_rt {
        width: 100%;
        flex: 1;
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);

            .btn {
                margin: 0 auto;
                box-shadow: none !important;
            }
        }

        &--title {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &--content {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &--box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            &.mona-content{
                row-gap: 0;
                h2,h1,h3,h3,h4,h6{
                    margin-bottom: 0;
                }
            }
        }
    }
}
