.events {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_item {
        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            height: 100%;
            justify-content: space-between;
            @include hover {
                // transform: scale(1.03);

                .events_item--img {
                    &::after {
                        opacity: 1;
                    }

                    &::before {
                        opacity: 1;
                        transform: translate(-50%, -50%) scale(1);
                        animation: likeHover 0.8s linear infinite alternate;
                    }

                    img {
                        transform: translate(-50%, -50%) scale(1.1);
                    }
                }
            }
        }

        &--top {
            position: relative;

            .box-dm {
                position: absolute;
                top: 1rem;
                left: 1rem;
                z-index: 3;

                @media screen and (max-width: 1200px) {
                    top: 0.5rem;
                    left: 0.5rem;
                }
            }

            .btn-like {
                position: absolute;
                top: 1rem;
                right: 1rem;
                z-index: 3;

                .ic {
                    color: $color-white;
                }
            }
        }

        &--img {
            padding-top: calc(100% * 161 / 274);
            width: 100%;
            height: 100%;
            overflow: hidden;
            position: relative;
            border-radius: var(--radius);

            &::after {
                content: "";
                position: absolute;
                inset: 0;
                width: 100%;
                height: 100%;
                background: rgba($color: #000000, $alpha: 0.6);
                opacity: 0;
                transition: all 0.3s linear;
            }

            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0);
                width: 4.8rem;
                height: 4.8rem;
                background-image: url(../assets/images/Arrow.svg);
                background-repeat: no-repeat;
                background-size: 100% 100%;
                opacity: 0;
                transition: all 0.3s linear;
                z-index: 4;
            }

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 100%;
                height: 100%;
                object-fit: cover;
                transform: translate(-50%, -50%);
                transition: all 0.5s ease;
            }
        }

        &--bottom {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
            height: 100%;
            justify-content: space-between;
        }

        &--title {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            .title {
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;

                a {
                    display: block;
                }
            }

            .content {
                display: -webkit-box;
                -webkit-line-clamp: 4;
                -webkit-box-orient: vertical;
                overflow: hidden;
                @include fz-16;
            }
        }

        &--content {
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &--item {
            display: flex;
            position: relative;
            // &::before{
            //     content: "|";
            //     top: 0;
            //     left: 23%;
            //     position: absolute;
            //     color: $color-gray4;
            // }

            &.tc {
                .content {
                    text-decoration: underline;
                }
            }

            &.gv {
                .content {
                    font-weight: 700;
                    color: $color-pri;
                }
            }

            .title {
                flex-shrink: 0;
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                width: 6.6rem;
                border-right: 0.1rem solid $color-gray4;

                @media screen and (max-width: 800px) {
                    @include fz-12;
                    width: 6rem;
                }
            }

            .content {
                @media screen and (max-width: 800px) {
                    @include fz-12;
                }
            }
            column-gap: 0.8rem;
        }
    }

    &_content {
        .events_item--wrap {
            flex-direction: row;
            column-gap: var(--columnContent);

            @media screen and (max-width: 600px) {
                flex-direction: column;
            }
        }

        .events_item--top {
            width: 50%;
            flex-shrink: 0;
            .events_item--img {
                padding-top: (100% * 340 / 580);
            }

            @media screen and (max-width: 600px) {
                width: 100%;
            }
        }

        .events_item--bottom {
            width: calc(100% - var(--columnContent));
            display: flex;
            flex-direction: column;
            justify-content: center;
            height: unset;
            .events_item--title {
                .title {
                    @include fz-24;
                    font-weight: 700;
                }
            }

            // .events_item--item .title{
            //     width: 12%;
            // }
        }
    }

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }
}

.events_nb {
    padding-top: var(--size-pdl);
    padding-bottom: var(--size-pdl);
    margin-top: var(--size-pd);
    margin-bottom: var(--size-pd);
    background: $color-white2;

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
        align-items: center;

       
    }

    &--list {
        position: relative;
        width: 100%;
        @media screen and (max-width: 1370px) {
            max-width: 92%;
        }

        @media screen and (max-width: 800px) {
            max-width: 90%;
        }

        @media screen and (max-width: 600px) {
            max-width: none;
        }

        &-wrap{

        }

        .swiper-slide {
            height: auto;
        }

        .box-navi {
            padding-top: calc(100% * 161 / 1200);
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;

            @media screen and (max-width: 1024px) {
                padding-top: calc(100% * 225 / 1200);
            }

            @media screen and (max-width: 600px) {
                display: none;
            }

            .btn-navi {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                &.prev {
                    left: -6.4rem;
                    @media screen and (max-width: 1370px) {
                        left: -6rem;
                    }
    
                    @media screen and (max-width: 1230px) {
                        left: -5.8rem;
                    }
    
                    @media screen and (max-width: 1200px) {
                        left: -5.2rem;
                    }
    
                    @media screen and (max-width: 1024px) {
                        left: -4.6rem;
                    }
    
                    @media screen and (max-width: 900px) {
                        left: -4.5rem;
                    }
                }
    
                &.next {
                    right: -6.4rem;
    
                    @media screen and (max-width: 1370px) {
                        right: -6rem;
                    }
    
                    @media screen and (max-width: 1230px) {
                        right: -5.8rem;
                    }
    
                    @media screen and (max-width: 1200px) {
                        right: -5.2rem;
                    }
    
                    @media screen and (max-width: 1024px) {
                        right: -4.6rem;
                    }
    
                    @media screen and (max-width: 900px) {
                        right: -4.5rem;
                    }
                }
            }
        }
    }

    &--item {
        height: 100%;

        .events_item--wrap {
            height: 100%;
        }

        .events_item--title {
            .content {
                display: none;
            }
        }
    }
}

.events_new {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--list {
        row-gap: var(--rowContent3);
    }

    &--box {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .btn {
            margin: 0 auto;
        }
    }

    &--item {
        // height: 100%;

        .events_item--title {
            .content {
                display: none;
            }
        }
    }
}
