.sign {
    position: relative;
    overflow: hidden;
    &_title {
        display: flex;
        flex-direction: column;
        row-gap: 0.4rem;
    }

    &_ok {
        width: fit-content;
        margin: 0 auto;
        .note-text {
            a {
                color: $color-pri;
                text-decoration: underline;
                @include hover {
                    color: #ff0032 !important;
                }
            }
        }
    }

    &_form {
        &--list {
            display: flex;
            flex-direction: column;
            row-gap: 1.6rem;

            @media screen and (max-width: 1200px) {
                row-gap: 1.4rem;
            }
            @media screen and (max-width: 800px) {
                row-gap: 1.2rem;
            }

            @media screen and (max-width: 600px) {
                row-gap: 1rem;
            }
        }

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: 1.6rem;

            // .btn {
            //     width: 100%;
            //     margin-top: 2.4rem;
            // }
        }
    }

    &_ic {
        width: 16rem;
        height: 16rem;
        margin: 0 auto;

        @media screen and (max-width:1200px){
            width: 14rem;
            height: 14rem;
        }

        @media screen and (max-width:800px){
            width: 12rem;
            height: 12rem;
        }

        @media screen and (max-width:600px){
            width: 10rem;
            height: 10rem;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &.success {
        // .sign_lf--wrap{
        //     max-width: unset;
        // }
        .sign_title {
            .title-lg {
                text-align: center;
            }

            .note-md {
                text-align: center;
            }
        }

        .sign_btn {
            margin-top: 0;
        }
    }

    &.cus {
        .sign_lf--wrap {
            max-width: 70%;
            width: 100%;
            @media screen and (max-width: 1400px) {
                max-width: 80%;

                margin: 0 auto;
            }

             @media screen and (max-width: 800px) {
                max-width: unset;
                margin: 0 auto;
            }
        }

        .sign_btn {
            margin-top: 0;
        }

        .sign_ok {
            margin: unset;
        }
    }

    &_wrap {
        min-height: calc(100vh - var(--size-hd));

        // @media screen and (max-width: 600px) {
        //     min-height: unset;
        // }
    }

    &_lf {
        @media screen and (max-width: 1024px) {
            width: 45%;
        }

        @media screen and (max-width: 600px) {
            width: 100%;
            position: relative;
            z-index: 1;
        }
        display: flex;
        flex-direction: column;
        justify-content: center;
        &--wrap {
            padding-top: var(--size-pd);
            padding-bottom: var(--size-pd);
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);
            max-width: 95%;

            @media screen and (max-width: 1200px) {
                max-width: none;
            }

            @media screen and (max-width: 1200px) {
                padding-top: var(--size-pdl);
                padding-bottom: var(--size-pdl);
                // max-width: 80%;
                // margin: 0 auto;
            }
        }
    }

    &_rt {
        width: 100%;
        flex: 1;
        position: relative;

        @media screen and (max-width: 600px) {
            display: none;
        }
        &--wrap {
            width: calc(100% + ((100vw - 120rem) / 2));
            height: 100%;
            position: absolute;
            margin-right: calc((100vw - 120rem) / -2);

            @media screen and (max-width: 1200px) {
                width: 100%;
                margin-right: -1.5rem;
            }
        }

        &--img {
            padding-top: calc(100% * 596 / 624);
            width: 100%;
            // border-radius: 1.6rem;
            overflow: hidden;
            position: relative;
            height: 100%;
            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }

    &_control {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 1.2rem;
        .note-sm {
            text-decoration: underline;
        }
    }

    &_save {
        &--label {
            cursor: pointer;
            user-select: none;
            .note-sm {
                line-height: 100%;
                text-decoration: none;
            }

            input {
                display: none;

                &:checked {
                    & ~ .box {
                        background: $color-pri;
                        border-color: $color-pri;

                        &::before {
                            opacity: 1;
                        }
                    }
                }
            }
            display: flex;
            // align-items: center;
            column-gap: 0.8rem;

            .box {
                width: 1.6rem;
                height: 1.6rem;
                display: block;
                border: 0.1rem solid $color-pri;
                border-radius: 0.4rem;
                overflow: hidden;
                position: relative;
                background: transparent;
                transition: all 0.3s linear;
                flex-shrink: 0;
                top: -0.15rem;
                &::before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 1.2rem;
                    height: 1.2rem;
                    background-image: url(../assets/images/check.svg);
                    background-repeat: no-repeat;
                    background-size: 100% 100%;
                    opacity: 0;
                    transition: all 0.3s linear;
                }
            }
        }
    }

    &_cs {
        &--label {
            cursor: pointer;
            user-select: none;
            .note-sm {
                line-height: 100%;
                text-decoration: none;

                a {
                    text-decoration: underline;
                }
            }

            input {
                display: none;

                &:checked {
                    & ~ .box {
                        background: $color-pri;
                        border-color: $color-pri;

                        &::before {
                            opacity: 1;
                        }
                    }
                }
            }
            display: flex;
            // align-items: center;
            column-gap: 0.8rem;

            .box {
                width: 1.6rem;
                height: 1.6rem;
                display: block;
                border: 0.1rem solid $color-pri;
                border-radius: 0.4rem;
                overflow: hidden;
                position: relative;
                background: transparent;
                transition: all 0.3s linear;
                flex-shrink: 0;
                top: -0.15rem;
                &::before {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 1.2rem;
                    height: 1.2rem;
                    background-image: url(../assets/images/check.svg);
                    background-repeat: no-repeat;
                    background-size: 100% 100%;
                    opacity: 0;
                    transition: all 0.3s linear;
                }
            }
        }
    }

    &_btn {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
        margin-top: 2.4rem;

        @media screen and (max-width: 1200px) {
            margin-top: 2rem;
        }

        @media screen and (max-width: 800px) {
            margin-top: 1.6rem;
        }

        @media screen and (max-width: 600px) {
            margin-top: 1.2rem;
        }

        .btn {
            width: 100%;
        }
    }

    &_order {
        display: flex;
        flex-direction: column;
        row-gap: 1.6rem;

        &--title {
            text-align: center;
            position: relative;

            &::before {
                content: "";
                width: 100%;
                height: 0.1rem;
                background: #c2c2c2;
                top: 50%;
                left: 0;
                position: absolute;
            }

            .note-text {
                background: $color-white;
                padding: 0 1.6rem;
                position: relative;
                z-index: 2;
                width: fit-content;
                margin: 0 auto;
            }
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: 1.2rem;
        }

        &--item {
        }

        // &--link{
        //     width: 100%;
        //     padding: 1.2rem;
        //     display: flex;
        //    justify-content: center;
        //     border:0.1rem solid $color-gray4;
        //     border-radius: 10rem;
        //     align-items: center;
        //     transition: all 0.5s ease;
        //     @include hover{
        //         box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.3);
        //         background: $color-second;

        //         .sign_order--link-wrap{
        //             .note-text{
        //                 color:$color-white;
        //             }
        //         }
        //     }

        //     &-wrap{
        //         display: flex;
        //         align-items: center;
        //         column-gap: 1.2rem;

        //         .ic{
        //             width: 1.8rem;
        //             height: 1.8rem;
        //             flex-shrink: 0;
        //             display: flex;
        //             justify-content: center;
        //             align-items: center;
        //             transform: translateY(-0.1rem);
        //             img{
        //                 width: 100%;
        //                 height: 100%;
        //                 object-fit: contain;
        //             }

        //         }

        //         .note-text{
        //             transition: all 0.5s ease;
        //         }
        //     }
        // }
    }

    &_code {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);
        align-items: center;

        width: 100%;
        &--list {
            width: 100%;
            display: flex;
            align-items: center;
            column-gap: var(--columnContent);
            justify-content: space-between;
            &.error {
                .sign_code--item {
                    input {
                        border-color: #cc6168;
                    }
                }
            }

            &.pass {
                .sign_code--item {
                    input {
                        border-color: #63cc61;
                    }
                }
            }
        }

        &--item {
            width: 7.2rem;
            height: 7.2rem;

            input {
                padding: 1.2rem;
                width: 100%;
                height: 100%;
                @include fz-32;
                font-weight: 600;
                text-align: center;
                border: 0.1rem solid $color-gray4;
                transition: 0.3s linear;
                border-radius: calc(var(--radius) / 2);
                &:focus {
                    border-color: $color-pri;
                }
            }
        }

        &--time {
            display: flex;
            align-items: center;
            column-gap: 0.1rem;

            .note-text {
                color: #f2451c;
            }
        }
    }
}
