.home_tabs {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .title-box {
            flex-direction: row !important;
        }
    }

    &--list {
        width: 100%;
        position: relative;
        &.d-wrap {
            margin-left: -0.8rem;
            margin-right: -0.8rem;

            @media screen and (max-width: 1200px) {
                margin-left: -0.6rem;
                margin-right: -0.6rem;
            }

            @media screen and (max-width: 800px) {
                margin-left: -0.4rem;
                margin-right: -0.4rem;
            }

            @media screen and (max-width: 600px) {
                margin-left: -0.2rem;
                margin-right: -0.2rem;
            }
        }

        .swiper-slide {
            padding-left: 0.8rem;
            padding-right: 0.8rem;
            width: fit-content;

            @media screen and (max-width: 1200px) {
                padding-left: 0.6rem;
                padding-right: 0.6rem;
            }

            @media screen and (max-width: 800px) {
                padding-left: 0.4rem;
                padding-right: 0.4rem;
            }

            @media screen and (max-width: 600px) {
                padding-left: 0.2rem;
                padding-right: 0.2rem;
            }
        }
    }

    &--item {
        display: flex;
        column-gap: 1rem;
        padding: 0.8rem 2.4rem;
        border: 0.1rem solid $color-gray4;
        width: 100%;
        border-radius: 0.6rem;
        transition: 0.3s ease;
        align-items: center;

        @media screen and (max-width: 1200px) {
            padding: 0.8rem 2rem;
            column-gap: 0.8rem;
        }

        @media screen and (max-width: 800px) {
            padding: 0.8rem 1.6rem;
            column-gap: 0.6rem;
        }

        @media screen and (max-width: 600px) {
            padding: 0.8rem 1.2rem;
            column-gap: 0.4rem;
        }

        @media screen and (max-width: 460px) {
            padding: 0.6rem 1rem;
            column-gap: 0.4rem;
        }

        &.actived {
            background: $color-pri;
            border-color: $color-pri;
            .ic {
                img {
                    filter: brightness(0) saturate(100%) invert(96%) sepia(94%) saturate(4997%) hue-rotate(185deg)
                        brightness(130%) contrast(93%);
                }
            }

            .title {
                color: $color-white;
                @include fz-16;
            }
        }

        @include hover {
            background: $color-pri;
            border-color: $color-pri;
            .ic {
                img {
                    filter: brightness(0) saturate(100%) invert(96%) sepia(94%) saturate(4997%) hue-rotate(185deg)
                        brightness(130%) contrast(93%);
                }
            }

            .title {
                color: $color-white;
            }
        }
        .ic {
            width: 1.6rem;
            height: 1.6rem;
            display: flex;
            justify-content: center;
            align-items: center;
            line-height: 100%;
            flex-shrink: 0;
            // transform: translateY(0.3rem);

            @media screen and (max-width: 800px) {
                width: 1.4rem;
                height: 1.4rem;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }

        .title {
            color: $color-gray3;
            @include fz-16;
        }
    }

    &--content {
        .news_lf--lf-item .like_item--img {
            padding-top: calc(100% * 382 / 592);
        }

        .news_lf--rt-item .like_item--img {
            padding-top: calc(100% * 164 / 280);
        }

        .news_ct--rt-item .like_item--wrap {
            flex-direction: column;
        }

        .news_ct--rt-item .like_item--top {
            width: 100%;
            display: none;
        }

        .news_ct--rt-item .like_item--img {
            display: none;
        }
        .news_ct--rt-item .like_item--dm {
            position: static;
            transform: unset;
        }

        .news_ct--rt-item .like_item--info .btn-like {
            display: none;
        }

        .news_ct--rt-box {
            row-gap: var(--rowContent1);
            position: sticky;
            top: calc(12rem + 2rem);
            height: auto;
        }
    }
}

.home {
    &_title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 1.2rem;
        overflow: hidden;

        @media screen and (max-width: 600px) {
            flex-direction: column;
            row-gap: 1rem;
        }

        .title-sm2 {
            flex-shrink: 0;
        }

        .swiper-scrollbar {
            margin-top: 0;
            @media screen and (max-width: 1200px) {
                margin-left: 0.6rem;
                margin-right: 0.6rem;
                margin-top: 0.5rem;
            }

            @media screen and (max-width: 800px) {
                margin-left: 0.4rem;
                margin-right: 0.4rem;
                margin-top: 0.3rem;
            }
        }

        &--list {
            width: fit-content;

            &.d-wrap {
                margin-left: -0.8rem;
                margin-right: -0.8rem;

                @media screen and (max-width: 1200px) {
                    margin-left: -0.6rem;
                    margin-right: -0.6rem;
                }

                @media screen and (max-width: 800px) {
                    margin-left: -0.4rem;
                    margin-right: -0.4rem;
                }
            }

            .swiper-slide {
                width: fit-content;

                padding-left: 0.8rem;
                padding-right: 0.8rem;

                @media screen and (max-width: 1200px) {
                    padding-left: 0.6rem;
                    padding-right: 0.6rem;
                }

                @media screen and (max-width: 800px) {
                    padding-left: 0.4rem;
                    padding-right: 0.4rem;
                }
            }
        }

        &--item {
            &.actived {
                .home_title--link {
                    color: $color-pri;
                    &::before {
                        width: 100%;
                        left: 0;
                    }
                }
            }
        }

        &--link {
            font-weight: 600;
            color: $color-text2;
            @include fz-16;
            position: relative;

            &::before {
                content: "";
                bottom: 0;
                width: 0;
                background: $color-pri;
                height: 0.1rem;
                right: 0;
                position: absolute;
                transition: 0.3s ease;
            }

            @include hover {
                color: $color-pri;
                &::before {
                    width: 100%;
                    left: 0;
                }
            }
        }
    }
}

.home_sport {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
        justify-content: center;
        align-items: center;
    }

    &--list {
        width: 100%;
        position: relative;

        &-wrap {
        }

        .swiper-slide {
            height: auto;

            @media screen and (max-width: 360px) {
                width: 100% !important;
            }
        }

        position: relative;

        @media screen and (max-width: 1360px) {
            max-width: 92%;
        }

        @media screen and (max-width: 1200px) {
            max-width: unset;
        }

        // @media screen and (max-width: 800px) {
        //     max-width: 90%;
        // }

        // @media screen and (max-width: 600px) {
        //     max-width: none;
        // }

        .box-navi {
            padding-top: calc(100% * 400 / 1200);
            position: absolute;
            top: 0;
            left: 0;

            // @media screen and (max-width: 1024px) {
            //     padding-top: calc(100% * 400 / 860);
            // }

            @media screen and (max-width: 1200px) {
                position: static !important;
                transform: unset !important;
                padding-top: 0;
                justify-content: center;
                margin-top: var(--rowContent3);
            }
        }

        .btn-navi {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);

            @media screen and (max-width: 1200px) {
                position: static !important;
                transform: unset !important;
            }

            &.prev {
                left: -6.4rem;
                @media screen and (max-width: 1370px) {
                    left: -6.2rem;
                }

                @media screen and (max-width: 1230px) {
                    left: -5.8rem;
                }

                @media screen and (max-width: 1200px) {
                    left: -5.2rem;
                }

                @media screen and (max-width: 1024px) {
                    left: -4.6rem;
                }

                @media screen and (max-width: 900px) {
                    left: -4.5rem;
                }
            }

            &.next {
                right: -6.4rem;

                @media screen and (max-width: 1370px) {
                    right: -6.2rem;
                }

                @media screen and (max-width: 1230px) {
                    right: -5.8rem;
                }

                @media screen and (max-width: 1200px) {
                    right: -5.2rem;
                }

                @media screen and (max-width: 1024px) {
                    right: -4.6rem;
                }

                @media screen and (max-width: 900px) {
                    right: -4.5rem;
                }
            }
        }
    }

    &--content {
        position: absolute;
        bottom: 2rem;
        left: 2rem;
        right: 2rem;
        max-width: calc(100% - 4rem);
        width: 100%;
        // transform: translateX(-50%);
        z-index: 3;
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);
        transform: translateY(100%);
        opacity: 0;
        transition: 0.5s linear;

        @media screen and (max-width: 1200px) {
            transform: translateY(0%);
            opacity: 1;
        }

        @media screen and (max-width: 800px) {
            bottom: 1rem;
            right: 1rem;
            left: 1rem;
            max-width: calc(100% - 2rem);
        }

        .box-info {
            &-author {
                .note-sm {
                    color: $color-white;
                }
            }

            &-date {
                .note-sm {
                    color: $color-white;
                }
            }
        }

        .note-text {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;

            @include hover {
                color: $color-purple2;
            }
        }
    }

    &--img {
        padding-top: calc(100% * 400 / 274);
        width: 100%;
        position: relative;
        overflow: hidden;
        border-radius: var(--radius);
        display: block;

        @media screen and (max-width: 360px) {
            padding-top: calc(100% * 320 / 280);
        }

        &::before {
            content: "";
            position: absolute;
            inset: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(180deg, rgba(0, 0, 0, 0) 57.25%, rgba(0, 0, 0, 0.8) 89.69%);
            z-index: 1;
        }

        &::after {
            content: "";
            position: absolute;
            inset: 0;
            width: 100%;
            height: 100%;
            z-index: 2;
            background: rgba(0, 0, 0, 0.6);
            opacity: 0;
            transition: opacity 0.3s linear;
        }

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &--item {
        &-wrap {
            display: block;
            position: relative;
            cursor: pointer;
            @include hover {
                .btn-play {
                    transform: translate(-50%, -50%) scale(1);
                }

                .home_sport--img {
                    &::after {
                        opacity: 1;
                    }
                }

                .home_sport--content {
                    opacity: 1;
                    transform: translateY(0);
                }
            }

            @media screen and (max-width: 1200px) {
                .btn-play {
                    transform: translate(-50%, -50%) scale(1) !important;
                }

                .home_sport--img {
                    &::after {
                        opacity: 1;
                    }
                }
            }

            // .box-dm {
            //     position: absolute;
            //     top: 2rem;
            //     left: 2rem;
            //     z-index: 5;

            //     @media screen and (max-width: 800px) {
            //         top: 1rem;
            //         left: 1rem;
            //     }
            // }

            .btn-play {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0);
                z-index: 5;
                transition: 0.3s linear;
                // pointer-events: none;
            }
        }
    }
}

.home_finance {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .news_rt {
            .title-box {
                flex-direction: row !important;
            }
        }
    }

    &--content {
        .news_lf--wrap {
            flex-direction: column;
            row-gap: var(--rowContent3);
        }

        // .news_lf{
        //     display: flex;
        //     flex-direction: column;
        //     row-gap: var(--rowContent);
        // }

        .news_lf--lf {
            width: 100%;

            .like_item--wrap {
                flex-direction: row;
                column-gap: var(--columnContent);

                @media screen and (max-width: 1024px) {
                    flex-direction: column;
                }
            }

            .like_item--top {
                width: 48%;
                flex-shrink: 0;

                @media screen and (max-width: 1200px) {
                    width: 49%;
                }

                @media screen and (max-width: 1024px) {
                    width: 100%;
                }
            }

            .like_item--bottom {
                width: calc(100% - var(--columnContent));
                flex: 1;
                display: flex;
                flex-direction: column;
                justify-content: center;
            }
        }

        .news_lf--rt {
            width: 100%;
            .news_lf--rt-list {
                flex-direction: row;
                flex-wrap: wrap;
                column-gap: var(--columnContent);
                justify-content: space-between;
            }

            .news_lf--rt-item {
                width: calc(50% - (var(--columnContent) / 2));

                @media screen and (max-width: 360px) {
                    width: 100%;
                }
            }

            .like_item--wrap {
                flex-direction: row-reverse;
                column-gap: var(--columnContent1);

                @media screen and (max-width: 1024px) {
                    flex-direction: column;
                }
            }

            .like_item--top {
                width: 35%;
                flex-shrink: 0;

                @media screen and (max-width: 1024px) {
                    width: 100%;
                }

                .like_item--img {
                    padding-top: calc(100% * 120 / 160);

                    @media screen and (max-width: 1024px) {
                        padding-top: calc(100% * 120 / 260);
                    }

                    @media screen and (max-width: 800px) {
                        padding-top: calc(100% * 120 / 220);
                    }

                    @media screen and (max-width: 360px) {
                        padding-top: calc(100% * 150 / 300);
                    }

                    &::before {
                        width: 2.4rem;
                        height: 2.4rem;
                    }
                }

                .box-dm {
                    @include fz-12;
                    padding: 0.4rem 0.6rem;
                    top: 0.8rem;
                    left: 0.8rem;
                }
            }

            .like_item--bottom {
                display: flex;
                flex-direction: column;
                justify-content: center;
                flex: 1;
                width: calc(100% - var(--columnContent1));
            }
        }
    }
}

.home_care {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
        align-items: center;
    }

    &--box {
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
    }

    &--list {
        position: relative;
        // width: 100%;
        @media screen and (max-width: 1360px) {
            max-width: 94.5%;
        }

        @media screen and (max-width: 800px) {
            max-width: 93%;
        }

        @media screen and (max-width: 600px) {
            max-width: none;
        }

        @media screen and (max-width: 460px) {
            width: 100%;
        }

        .box-navi {
            @media screen and (max-width: 600px) {
                display: none;
            }
        }

        .btn-navi {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);

            &.prev {
                left: -4.8rem;
                @media screen and (max-width: 1360px) {
                    left: -4.6rem;
                }

                @media screen and (max-width: 1200px) {
                    left: -3.6rem;
                }

                @media screen and (max-width: 900px) {
                    left: -3.4rem;
                }
            }

            &.next {
                right: -4.8rem;

                @media screen and (max-width: 1360px) {
                    right: -4.6rem;
                }

                @media screen and (max-width: 1200px) {
                    right: -3.6rem;
                }

                @media screen and (max-width: 900px) {
                    right: -3.4rem;
                }
            }
        }

        .swiper-slide {
            height: auto;

            @media screen and (max-width: 360px) {
                width: 100% !important;
            }
        }
    }

    &--img {
        // padding-top: calc(100% * 164 / 376);
        width: 100%;
        height: 100%;
        position: absolute;
        inset: 0;
        display: block;
        overflow: hidden;

        &::before {
            content: "";
            width: 100%;
            height: 100%;
            inset: 0;
            position: absolute;
            background: rgba(0, 0, 0, 0.4);
            z-index: 1;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &--top {
        position: relative;
        padding-top: 2rem;
        padding-bottom: 2rem;
        min-height: 14rem;

        @media screen and (max-width: 1200px) {
            min-height: 12rem;
        }

        @media screen and (max-width: 600px) {
            min-height: 10rem;
        }

        &-title {
            padding-top: 2rem;
            padding-bottom: 2rem;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: 2rem;
            z-index: 2;

            @media screen and (max-width: 1200px) {
                left: 1.4rem;
            }

            @media screen and (max-width: 800px) {
                left: 1.2rem;
            }

            @media screen and (max-width: 600px) {
                left: 1rem;
            }

            @media screen and (max-width: 460px) {
                left: 0.8rem;
            }
        }

        .follow {
            display: flex;
            justify-content: center;
            align-items: center;
            background: $color-pri;
            border-radius: 3rem;
            padding: 0.4rem 3.2rem;
            color: $color-white;
            transition: 0.3s linear;

            z-index: 3;
            @include fz-16;
            @include hover {
                background: $color-second;

                color: $color-white;
            }

            @media screen and (max-width: 1200px) {
                padding: 0.4rem 2.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 0.4rem 2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0.4rem 1.2rem;
            }
        }
    }

    &--bottom {
        margin-top: auto;
        &-list {
            max-height: 33rem;
            overflow-y: auto;
        }

        &-item {
            &:not(:last-child) {
                .home_care--bottom-link {
                    border-bottom: 0.1rem solid $color-gray4;
                }
            }
        }

        &-link {
            padding: 1.6rem;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);

            @media screen and (max-width: 1200px) {
                padding: 1.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1rem;
            }

            @media screen and (max-width: 460px) {
                padding: 0.8rem;
            }

            @include hover {
                .note-text {
                    color: $color-pri;
                }
            }

            .note-text {
                transition: all 0.3s linear;

                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }
        }
    }

    &--item {
        height: 100%;
        &-wrap {
            border: 0.1rem solid $color-gray4;
            border-radius: var(--radius);
            overflow: hidden;
            height: 100%;
        }
    }
}

