        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        max-width: 48rem;
        width: 100%;

        @media screen and (max-width: 800px) {
            max-width: none;
        }

        .sign_form--item {
            width: 100%;
            max-width: 48rem;
            position: relative;
            @media screen and (max-width: 800px) {
                max-width: none;
            }

            .form-ip-ip input {
                border-radius: 2rem;
                padding: 1.2rem 1.6rem;
                padding-right: 16rem;
                background: #3c3a40;
                border-color: $color-text;
                color: $color-white;

                @media screen and (max-width: 800px) {
                    padding-right: 14rem;
                }

                @media screen and (max-width: 600px) {
                    padding-right: 12rem;
                }

                @media screen and (max-width: 460px) {
                    padding-right: 11rem;
                }
                &::placeholder {
                    color: $color-white;

                    @media screen and (max-width: 800px) {
                        color: $color-black;
                    }
                }

                &:focus {
                    border-color: $color-white;
                }

                @media screen and (max-width: 800px) {
                    background: $color-white;

                    color: $color-black;
                }
            }
        }

        .btn {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: 0.3rem;
            height: 88%;
            border-radius: 1.6rem;
            @media screen and (max-width: 800px) {
                background: $color-second;
                right: 0.4rem;
                height: 80%;
            }

            &-text {
                @media screen and (max-width: 800px) {
                    color: $color-white;
                }
            }
        }

        @media screen and (max-width: 460px) {
            flex-direction: column;
            row-gap: var(--rowContent1);
            align-items: center;
        }

        .note-mn {
            @media screen and (max-width: 800px) {
                color: $color-white2;
            }

            @media screen and (max-width: 460px) {
                font-size: 1rem;
            }
        }
    }
}

.home_nb {
    padding-top: 10rem;
    padding-bottom: 10rem;
    margin: var(--size-pd) 4rem;
    border-radius: var(--radius);
    overflow: hidden;
    position: relative;

    @media screen and (max-width: 1200px) {
        margin: var(--size-pd) 0rem;
        padding-top: 6rem;
        padding-bottom: 6rem;
        border-radius: 0;
    }

    @media screen and (max-width: 800px) {
        padding-top: 4rem;
        padding-bottom: 4rem;
    }

    @media screen and (max-width: 600px) {
        padding-top: 3rem;
        padding-bottom: 3rem;
    }

    &::before {
        content: "";
        position: absolute;
        background-image: url(../assets/images/nbbg.png);
        width: 100%;
        height: 100%;
        inset: 0;
        background-size: 100% 100%;
        z-index: -1;
        pointer-events: none;
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);
        .title-md {
            text-align: center;
        }

    }

    &--list {
        .box-info {
            .box-info-author {
                .note-mn {
                    color: $color-white2;
                }

                .note-sm {
                    color: $color-white2;
                }
            }

            .box-info-date {
                .note-mn {
                    color: $color-white2;
                }

                .note-sm {
                    color: $color-white2;
                }
            }
        }

        .like_item--img {
            @media screen and (max-width: 350px) {
                padding-top: calc(100% * 140 / 280);
            }
        }

        .like_item--des {
            .note-text.title {
                color: $color-white;
                a {
                    @include hover {
                        color: $color-purple2;
                    }
                }
            }

            .note-text.content {
                display: none;
            }
        }

        .like_item--tt{
            display: none;
        }

        &-wrap {
            position: relative;

            .box-navi {
                @media screen and (max-width: 1200px) {
                    margin-top: var(--rowContent3);
                    justify-content: center;
                }
                .btn-navi {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);

                    @media screen and (max-width: 1200px) {
                        position: static;
                        transform: unset;
                    }

                    &.prev {
                        left: -4.8rem;
                    }

                    &.next {
                        right: -4.8rem;
                    }
                }
            }

            @media screen and (max-width: 1460px) {
                max-width: 95%;
                margin: 0 auto;
            }

            @media screen and (max-width: 1360px) {
                max-width: 92%;
                margin: 0 auto;
            }

            @media screen and (max-width: 1200px) {
                max-width: none;
                margin: 0 auto;
                width: 100%;
            }
        }
    }
}

.home_strong {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);
    }

    &--wrap {
        @media screen and (max-width: 600px) {
            flex-direction: column-reverse;
            row-gap: var(--rowContent2);
        }
    }

    &--lf {
        @media screen and (max-width: 600px) {
            width: 100%;
        }
        &-wrap {
        }

        &-list {
            row-gap: var(--rowContent2);
        }

        &-item {
            .like_item--des .note-text.content {
                display: none;
            }

            @media screen and (max-width: 800px) {
                width: 100%;

                .like_item--wrap {
                    flex-direction: row;
                    column-gap: var(--columnContent0);
                }

                .like_item--top {
                    width: 40%;
                    flex-shrink: 0;
                }

                .like_item--dm {
                    position: static;
                    transform: unset;
                    padding: 0;
                }
            }

            @media screen and (max-width: 600px) {
                width: 50%;

                .like_item--wrap {
                    flex-direction: column;
                    column-gap: var(--columnContent0);
                }

                .like_item--top {
                    width: 100%;
                    flex-shrink: 0;
                }

                .like_item--dm {
                    position: absolute;
                    transform: translateX(-50%);
                    top: 0.6rem;
                    left: 50%;
                    padding: 0 0.6rem;
                }
            }

            @media screen and (max-width: 360px) {
                width: 100%;

                .like_item--img {
                    padding-top: calc(100% * 140 / 280);
                }
            }
        }
    }

    &--rt {
        @media screen and (max-width: 600px) {
            width: 100%;
        }
        &-wrap {
            .like_item--img {
                padding-top: calc(100% * 382 / 592);
            }

            @media screen and (max-width: 800px) {
                height: 100%;

                .like_item--wrap {
                    position: sticky;
                    top: calc(var(--size-hd) + 2rem);
                }
            }
        }
    }
}

.home_suggest {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);

        .title-box {
            flex-direction: row !important;
        }
    }
    &--list {
        row-gap: var(--rowContent3);

        // @media screen and (max-width: 800px) {
        //     row-gap: var(--rowContent2);
        // }
    }

    &--item {
        .like_item--dm {
            position: static;
            transform: unset;
            padding: 0;
        }

        .like_item--top {
            display: none;
        }

        .btn-like {
            display: none;
        }

        .like_item--des .note-text.content {
            display: none;
        }
    }
}

.home_future {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);
    }

    .news_rt--wrap {
        .title-box {
            flex-direction: row !important;
        }
    }
}

.home_day {
    // padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);
    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);

        .title-md {
            text-align: center;
        }
    }

    &--box {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);

        // .like_item--tt{
        //     left: unset;
        //     right: 0;
        // }

        .btn {
            margin: 0 auto;
        }
    }

    &--list {
        row-gap: var(--rowContent3);
    }

    &--item {
        @media screen and (max-width: 800px) {
            width: calc(100% / 3) !important;
        }

        @media screen and (max-width: 600px) {
            width: calc(100% / 2) !important;
        }
        @media screen and (max-width: 320px) {
            width: 100% !important;
        }

        .like_item--des {
            .content {
                display: none;
            }
        }

        .like_item--wrap {
            row-gap: var(--rowContent0);
            flex-direction: row;
            column-gap: var(--columnContent);

            @media screen and (max-width: 800px) {
                flex-direction: column;
            }
        }

        .like_item--top {
            width: 40%;
            flex-shrink: 0;

            @media screen and (max-width: 1200px) {
                width: 45%;
            }

            @media screen and (max-width: 800px) {
                width: 100%;
            }
        }

        .like_item--info {
        }

        .box-dm {
        }

        .like_item--dm {
            top: unset;
            left: unset;
            transform: unset;
            position: static;
            padding: 0;

            @media screen and (max-width: 800px) {
                position: absolute;
                top: 0.6rem;
                left: 50%;
                transform: translateX(-50%);
                padding: 0 0.6rem;
            }
        }

        .like_item--bottom {
            flex: 1;
            width: calc(100% - (var(--columnContent0) / 2));
            justify-content: center;
            row-gap: var(--rowContent0);
        }

        .like_item--img {
            // display: none;
            padding-top: calc(100% * 164 / 280);

            @media screen and (max-width: 400px) {
                padding-top: calc(100% * 200 / 280);
            }

            @media screen and (max-width: 320px) {
                padding-top: calc(100% * 140 / 280);
            }
        }
    }
}
