.comment {
    row-gap: 1.6rem;

  

    &_cmt {
        display: flex;
        flex-direction: column;
        row-gap: 2.8rem;
        width: 100%;
        --commentRow: 0.8rem;
        --widthImg: 5rem;
        --commentCol: 0.8rem;
        --commentPd: 7.6rem;

        .btn {
            box-shadow: none !important;
        }

        @media screen and (max-width: 1400px) {
            row-gap: 2.4rem;
        }

        @media screen and (max-width: 1200px) {
            --widthImg: 3.8rem;
            --commentCol: 0.6rem;
            --commentRow: 0.6rem;
            row-gap: 1.6rem;
        }

        @media screen and (max-width: 600px) {
            --widthImg: 3.6rem;
        }

        @media screen and (max-width: 460px) {
            --widthImg: 3.2rem;
        }

        &-filter {
            &-wrap {
                max-width: 20rem;
                width: 100%;
                .note-text {
                    flex-shrink: 0;
                }
                display: flex;
                column-gap: 0.6rem;
                align-items: end;
            }
        }

        &--img {
            &-list {
                display: none;

                &.showed {
                    display: flex;
                }

                &.d-wrap {
                    margin-left: -0.6rem;
                    margin-right: -0.6rem;
                }

                .d-item {
                    padding-left: 0.6rem;
                    padding-right: 0.6rem;
                }
            }

            &-item {
                // display: ;
                // @media screen and (max-width: 1200px) {
                //     width: calc(100% / 4);

                //     &:nth-child(4) {
                //         display: none !important;
                //     }
                // }

                // @media screen and (max-width: 460px) {
                //     width: calc(100% / 3);

                //     &:nth-child(4) {
                //         display: none !important;
                //     }

                //     &:nth-child(3) {
                //         display: none !important;
                //     }
                // }

                // &:first-child {
                //     display: block;
                // }

                // &:nth-child(2) {
                //     display: block;
                // }

                // &:nth-child(3) {
                //     display: block;
                // }

                // &:nth-child(4) {
                //     display: block;
                // }

                // &:last-child {
                //     display: block;
                //     .comment_cmt--img-sl {
                //         display: flex;
                //     }

                //     .comment_cmt--img-bg {
                //         &::after {
                //             display: block;
                //         }
                //     }
                // }
            }

            &-wrap {
                // max-width: 20rem;
                width: 100%;
                position: relative;
            }

            &-sl {
                display: flex;
                align-items: center;
                column-gap: 0.1rem;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 2;
                display: none;
                user-select: none;
                pointer-events: none;
                .note-text {
                }
            }

            &-bg {
                padding-top: 100%;
                position: relative;
                overflow: hidden;
                display: block;

                &::after {
                    content: "";
                    inset: 0;
                    width: 100%;
                    height: 100%;
                    background: rgba($color: #000000, $alpha: 0.5);
                    position: absolute;
                    display: none;
                    z-index: 2;
                }

                img {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: 4rem;

            @media screen and (max-width: 1200px) {
                row-gap: 3.2rem;
            }

            @media screen and (max-width: 860px) {
                row-gap: 2.4rem;
            }

            @media screen and (max-width: 600px) {
                row-gap: 1.8rem;
            }
        }

        &--item {
        --commentRow: 0.8rem;

            display: flex;
            flex-direction: column;
            row-gap: var(--commentRow);

            &.ad {
                .comment_cmt--item-delete {
                    display: block !important;
                }
            }

            &-delete {
                display: none;
                .note-text {
                    @include fz-12;
                    transition: 0.3s linear;

                    @include hover {
                        color: $color-red !important;
                    }
                }
            }

            &-wrap {
                display: flex;
                flex-direction: column;
                row-gap: var(--commentRow);

                position: relative;
            }

            & > .comment_cmt--list {
                padding-left: calc(var(--widthImg) + var(--commentCol));

                .comment_cmt--item {
                    border-bottom: none !important;

                    &.ad {
                        display: block !important;
                    }

                    .comment_cmt--item-delete {
                        display: none !important;
                    }

                    // &::before{
                    //     height: calc(100% - (var(--widthImg) + 2rem));
                    // }
                    .comment_cmt--item-ava {
                        position: relative;

                        &::before {
                            content: "";
                            position: absolute;
                            left: -4rem;
                            top: 50%;
                            width: calc(var(--widthImg) - 1.8rem);
                            height: 0.1rem;
                            background: $color-gray;
                            display: none;

                            @media screen and (max-width: 1200px) {
                                left: -3rem;
                            }

                            @media screen and (max-width: 460px) {
                                left: -2.2rem;
                            }
                        }
                    }

                    & > .comment_cmt--list {
                        .comment_cmt--item {
                            margin-bottom: 1.6rem;
                            &:first-child {
                                .comment_cmt--item-ava {
                                    &::before {
                                        // display: block;
                                        display: none;
                                    }
                                }
                            }

                            &:last-child {
                                margin-bottom: 0;
                            }

                            .comment_cmt--item-ava {
                                &::before {
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }

            &-title {
                display: flex;
                // align-items: center;
                column-gap: var(--commentCol);
                align-items: center;
                row-gap: var(--rowContent0);
            }

            &-ava {
                position: relative;
                flex-shrink: 0;
                width: var(--widthImg);
                height: 5rem;
                border-radius: 50%;
                overflow: hidden;
                display: flex;
                justify-content: center;
                align-items: center;
                &.border{
                   

                    .comment_cmt--item-img{
                        width: calc(100% - 1.5rem);
                        height: calc(100% - 1.5rem);

                    }

                    .admin_info--ava-border{
                        display: block;
                    }
                }
            }

            &-name {
                display: flex;
                align-items: center;
                column-gap: 0.6rem;
                flex-wrap: wrap;
                row-gap: var(--rowContent0);
                &.other {
                    .panaco-alert {
                        display: none;
                    }
                }

                .ic {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    width: 1.6rem;
                    height: 1.6rem;
                    border-radius: 50%;
                    overflow: hidden;

                    @media screen and (max-width: 1200px) {
                        width: 1.4rem;
                        height: 1.4rem;
                    }

                    @media screen and (max-width: 600px) {
                        width: 1.2rem;
                        height: 1.2rem;
                    }

                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }

            &-date {
                .note-sm {
                    position: relative;
                    padding-left: 2.6rem;
                    position: relative;
                    // top: 0.2rem;
                    line-height: 100%;
                    @media screen and (max-width: 1200px) {
                        top: 0.2rem;
                    }

                    @media screen and (max-width: 800px) {
                        padding-left: 2rem;
                    }

                    @media screen and (max-width: 600px) {
                        top: 0rem;
                        padding-left: 1.8rem;
                    }

                    &::before {
                        content: "";
                        background-image: url(../assets/images/Clock.svg);
                        width: 2rem;
                        height: 2rem;
                        position: absolute;
                        top: 50%;
                        left: 0;
                        background-size: 100% 100%;
                        background-repeat: no-repeat;
                        transform: translateY(-50%);

                        @media screen and (max-width: 800px) {
                            width: 1.6rem;
                            height: 1.6rem;
                        }
                    }
                }
            }

            &-info {
                width: 100%;
                display: flex;
                flex-direction: column;
                row-gap: 0.4rem;

                @media screen and (max-width: 1200px) {
                    row-gap: 1rem;
                    // margin-top: -0.5rem;
                }

                &-top {
                    width: 100%;
                    display: flex;
                    align-items: center;
                    column-gap: var(--columnContent1);
                    justify-content: space-between;
                    row-gap: var(--rowContent0);
                    @media screen and (max-width: 1200px) {
                        column-gap: var(--columnContent1);
                        flex-wrap: wrap;
                    }

                    @media screen and (max-width: 600px) {
                        // flex-direction: column;
                        // align-items: start;
                        // row-gap: 0.8rem;
                    }
                }

                @media screen and (max-width: 1200px) {
                    .note-mn {
                        &:first-child {
                            transform: translateY(0.3rem);
                        }
                    }

                    .note-mn {
                        &:last-child {
                            transform: translateY(-0.3rem);
                        }
                    }
                }
            }

            &-img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                overflow: hidden;
                @media screen and (max-width: 1200px) {
                    height: 3.8rem;
                }

                @media screen and (max-width: 600px) {
                    height: 3.6rem;
                }

                @media screen and (max-width: 460px) {
                    height: 3.2rem;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &-ic {
                width: 2rem;
                height: 2rem;
                border-radius: 50%;
                overflow: hidden;
                position: absolute;
                top: -0.5rem;
                right: -0.5rem;

                @media screen and (max-width: 1200px) {
                    width: 1.6rem;
                    height: 1.6rem;
                }

                @media screen and (max-width: 460px) {
                    width: 1.5rem;
                    height: 1.5rem;
                }

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }

            &-box {
                display: flex;
                flex-direction: column;
                row-gap: 0.4rem;
                // padding-left: var(--commentPd);
                // padding-right: 1.2rem;

                // @media screen and (max-width: 1200px) {
                //     padding-left: var(--commentPd);
                //     padding-right: 1.2rem;
                // }

                // @media screen and (max-width: 600px) {
                //     padding-left: var(--commentPd);
                //     padding-right: 0rem;
                // }
            }

            &-content {
                display: flex;
                flex-direction: column;
                row-gap: 0.6rem;

                .note-text {
                    &.rg {
                        overflow: hidden;
                        display: -webkit-box;
                        -webkit-box-orient: vertical;
                        -webkit-line-clamp: 7;
                    }

                    @media screen and (max-width: 460px) {
                        @include fz-12;
                    }

                    transition: all 0.3s linear;
                    // &.showed {
                    //     display: block;
                    // }
                }

                .btn-see {
                    width: fit-content;
                    // transition: all 0.3s linear;
                    display: none;

                    &.showed {
                        display: block;
                    }

                    @include hover {
                        color: $color-second;
                    }
                }
            }

            &-control {
                display: flex;
                flex-direction: column;
                row-gap: 1.2rem;

                &-btn {
                    .note-text {
                        animation: anHienBt 0.5s linear forwards;
                    }

                    @keyframes anHienBt {
                        0% {
                            opacity: 0;
                        }
                        50% {
                            opacity: 0.5;
                        }
                        100% {
                            opacity: 1;
                        }
                    }
                }

                &.actived {
                    // z-index: 1100;

                    // .comment_cmt--item-control-bottom {
                    //     position: relative;
                    //     z-index: 2100;
                    // }
                }

                &-top {
                    display: flex;
                    align-items: center;
                    column-gap: 1.6rem;
                    flex-wrap: wrap;
                    justify-content: space-between;
                    @media screen and (max-width: 600px) {
                        justify-content: space-between;
                    }

                    .note-sm {
                        @media screen and (max-width: 600px) {
                            width: 100%;
                        }
                        @media screen and (max-width: 460px) {
                            @include fz-12;
                        }
                    }

                    .tron {
                        position: relative;

                        &:not(:first-child) {
                            &::before {
                                content: "";
                                position: absolute;
                                top: 50%;
                                transform: translateY(-50%);
                                left: -1rem;
                                width: 0.4rem;
                                height: 0.4rem;
                                background: $color-black;
                                border-radius: 50%;

                                @media screen and (max-width: 600px) {
                                    display: none;
                                }
                            }
                        }
                    }

                    .note-text {
                        cursor: pointer;
                        transition: all 0.3s linear;
                        @include fz-14;
                        @include hover {
                            color: $color-pri;
                        }

                        &.showed {
                            display: block !important;
                        }

                        &.btnControlRep {
                            display: none;
                        }

                        &.btnControlClose {
                            display: none;
                        }
                    }
                }

                &-bottom {
                    display: none;
                    // opacity: 0;
                    // clip-path: inset(0 100% 0 0);

                    // &.showed {
                    //     display: block;
                    //     animation: anHienBt 0.8s linear forwards;
                    // }
                }

                &-modal {
                    position: fixed;
                    inset: 0;
                    z-index: -1;
                    background: transparent;
                    visibility: hidden;
                    pointer-events: none;
                    width: 100%;
                    height: 100%;
                    &.actived {
                        visibility: visible;
                        pointer-events: visible;
                    }
                }
            }
        }

        &--bl {
            padding: 2rem;
            border: 0.1rem solid $color-pri;
            border-radius: var(--radius);

            @media screen and (max-width:1200px) {
                padding: 1.6rem;
            }

            @media screen and (max-width:800px) {
                padding: 1.4rem;
            }

            @media screen and (max-width:600px) {
                padding: 1.2rem;
            }

            .comment_cmt--item-control-bottom {
                display: block;
            }
            .comment_cmt--item-wrap {
                row-gap: var(--rowContent2);
            }

            .comment_box {
                padding: 0;
                border: none;
            }
        }

        .btn {
            // margin: 0 auto;
        }
    }

    &_box {
        display: flex;
        flex-direction: column;
        row-gap: 0.6rem;
        padding: 2rem;
        border: 0.1rem solid $color-gray4;
        border-radius: 0.8rem;

        @media screen and (max-width:1200px) {
            padding: 1.6rem;
        }

        @media screen and (max-width:800px) {
            padding: 1.4rem;
        }

        @media screen and (max-width:600px) {
            padding: 1.2rem;
        }

        & > .btn {
            width: fit-content;
            min-width: unset;
            margin-left: auto !important;
            margin-right: unset !important;
            height: 3.6rem;
            padding: 0 2rem;

            &-text {
                @include fz-14;
            }
        }
        &--input {
            &-wrap {
            }

            &-top {
            }

            &-ip {
                textarea {
                    width: 100%;
                    background: $color-white;
                    min-height: 8rem;
                    // padding: 1.2rem;
                    @include fz-14;
                    border-bottom: 0.1rem solid $color-gray4;

                    @media screen and (max-width: 800px) {
                        padding: 0.6rem;
                        @include fz-12;
                    }
                }
            }
        }
    }
}

.cmt-main{
    display: flex;
    flex-direction: column;
    row-gap: var(--rowContent1);
}

.like {
    cursor: pointer;
    input {
        display: none;

        &:checked {
            & ~ .like-text {
                color: $color-blue !important;
            }

            & ~ .like-sl {
                color: $color-blue !important;
            }
        }
    }
    display: flex;
    align-items: center;
    column-gap: 0.2rem;
    &-ic {
        width: 2.4rem;
        height: 2.4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        i {
            @include fz-20;
            line-height: 100%;
            transform: translateY(-0.2rem);
            color: $color-pri;
        }
    }

    &-sl {
        color: $color-gray3;
        @include fz-14;
    }

    &-text {
        color: $color-gray3;
        @include fz-14;
    }
}
