.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    column-gap: 0.8rem;
    height: 4.8rem;
    width: fit-content;
    padding: 0 2.4rem;
    min-width: 13.5rem;
    // border-radius: 0.8rem;
    background: $color-pri;
    position: relative;
    overflow: hidden;
    // flex-direction: row-reverse;
    border-radius: var(--radius);
    transition: 0.5s ease;
    box-shadow: 1rem 1rem 3.1rem 0rem #3800a633;

    @media screen and (max-width: 1200px) {
        column-gap: 0.6rem;
        min-width: 12rem;
        height: 4.2rem;
    }

    @media screen and (max-width: 800px) {
        height: 4rem;
        padding: 0 1.2rem;
        column-gap: 0.4rem;
    }

    @media screen and (max-width: 600px) {
        height: 3.6rem;
        padding: 0 1.2rem;
        min-width: 10rem;
    }

    &.cl-2 {
        background: $color-second;

        .btn-text {
            color: $color-white;
        }

        &::before {
            background: $color-pri;
        }

        @include hover {
            background: $color-second;
            // box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.8);
            .btn-text {
                color: $color-white;
            }

            .btn-ic {
                i {
                    color: $color-white;
                }
            }
        }
    }

    &.cl-3 {
        background: $color-white2;
        box-shadow: none;
        .btn-text {
            color: $color-black-2;
        }

        .btn-ic {
            i {
                color: $color-black-2;
            }
        }

        &::before {
            background: $color-white2;
        }

        @include hover {
            // box-shadow: 0 0 1rem rgba($color: #000000, $alpha: 0.8);
            box-shadow: none;
            background: $color-black-2;

            &::before {
                background: $color-black-2;
            }
            .btn-text {
                color: $color-white;
            }

            .btn-ic {
                i {
                    color: $color-white;
                }
            }
        }
    }

    &.trans {
        background: transparent;
        border: 0.1rem solid $color-gray4;
        box-shadow: none;

        .btn-text {
            color: $color-black-2;
            font-weight: 400;
        }

        &::before {
            background: $color-pri;
        }

        @include hover {
            border-color: $color-pri;
            background: $color-pri;
            box-shadow: none;

            .btn-text {
                color: $color-white;
            }
        }
    }

    &-text {
        font-weight: 400;
        color: $color-white2;
        position: relative;
        z-index: 2;
        transition: all 0.3s ease;
        white-space: nowrap;
        display: flex;
        align-items: center;
        justify-content: center;
        z-index: 10;
        @include fz-16;
        @media screen and (max-width: 460px) {
            font-size: 1.2rem !important;
        }
    }

    &-ic {
        position: relative;
        z-index: 1;
        display: flex;
        align-items: center;
        i {
            font-size: 1.6rem;
            color: $color-white;
            transition: all 0.3s linear;

            @media screen and (max-width: 800px) {
                position: relative;
                top: 0.08rem;
                font-size: 1.2rem;
            }
        }

        img {
            width: 1.8rem;
            height: 1.8rem;
            object-fit: contain;
            transform: translateY(-0.1rem);
        }
    }

    &::before {
        content: "";
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
        background: $color-second;
        border-radius: var(--radius);
        z-index: 1;
        overflow: hidden;
        transition: all 0.5s ease;
        opacity: 0;
    }

    @include hover {
        // transition: 0.3s linear;
        background: $color-second;
        box-shadow: 1rem 1rem 3.1rem 1rem rgba($color: $color-second, $alpha: 0.1);
        &::before {
            opacity: 1;
        }

        .btn-text {
            color: $color-white;
        }

        .btn-ic {
            i {
                color: $color-white;
            }
        }
    }
}

.box-navi {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: end;
    column-gap: 1rem;

    &.cus {
        .btn-navi {
            border: 0.1rem solid $color-white;

            i {
                color: $color-white;
            }

            @include hover {
                background: $color-second;
                border-color: $color-second;
                i {
                    color: $color-white;
                }
            }
        }
    }
    .btn-navi {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 4.8rem;
        height: 4.8rem;
        background: $color-white;
        // position: absolute;
        border-radius: 50%;
        border: 0.1rem solid $color-pri;
        z-index: 5;
        cursor: pointer;
        transition: all 0.3s linear;
        background: transparent;
        @media screen and (max-width: 1200px) {
            width: 4rem;
            height: 4rem;
        }

        @media screen and (max-width: 1024px) {
            width: 3.6rem;
            height: 3.6rem;
        }

        @media screen and (max-width: 800px) {
            width: 3.4rem;
            height: 3.4rem;
        }

        @media screen and (max-width: 600px) {
            width: 3rem;
            height: 3rem;
        }
        &.prev {
            // top: 45%;
            // left: 0rem;
        }

        &.next {
            // top: 45%;
            // right: 0rem;
        }

        @include hover {
            background: $color-pri;
            border-color: $color-pri;
            i {
                color: $color-white;
            }
        }

        i {
            font-size: 2rem;
            color: $color-pri;
            transition: all 0.3s linear;

            @media screen and (max-width: 1200px) {
                font-size: 1.8rem;
            }
        }
    }
}

.box-navi-pagi {
    display: flex;
    align-items: center;
    column-gap: 2.4rem;
    width: fit-content;
    padding: 0.8rem;
    margin-top: 3rem;
    margin-left: 1rem;

    &.cl-2 {
        .btn-navi-2 {
            i {
                color: #f5d360;
            }
        }

        .swiper-pagination-bullet-active {
            background: #f5d360;
        }
    }

    .swiper-pagination {
        margin-top: 0;
    }

    .btn-navi-2 {
        cursor: pointer;
        i {
            font-size: 2rem;
            color: $color-pri;
        }
    }
}

.page-pagi {
    display: flex;
    align-items: center;
    width: fit-content;
    margin: 4rem auto 0;
    column-gap: 0.5rem;
    &-wrap {
        display: flex;
        align-items: center;
        column-gap: 0.5rem;
    }
}

.number-pagi {
    cursor: pointer;
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    font-weight: 600;
    border-radius: 50%;
    color: $color-text;
    transition: all 0.3s linear;

    &.current {
        color: $color-pri;
    }

    @include hover {
        color: $color-pri;
    }

    &-ic.prev {
        background-color: $color-pri;
        cursor: pointer;
        width: 4rem;
        height: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;

        font-weight: 600;
        border-radius: 50%;
        transition: all 0.3s linear;

        i {
            color: #e9bc59;
            transition: all 0.3s linear;
        }
        @include hover {
            background: linear-gradient(180deg, #f5d461 54.62%, #fdc202 72.84%, #cb983f 88.46%);

            i {
                color: $color-white;
            }
        }
    }

    &-ic.next {
        cursor: pointer;
        width: 4rem;
        height: 4rem;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $color-pri;
        font-weight: 600;
        border-radius: 50%;

        transition: all 0.3s linear;

        i {
            color: #e9bc59;
            transition: all 0.3s linear;
        }

        @include hover {
            background: linear-gradient(180deg, #f5d461 54.62%, #fdc202 72.84%, #cb983f 88.46%);

            i {
                color: $color-white;
            }
        }
    }
}

.box-navi-2 {
    display: flex;
    width: fit-content;
    // margin: 3rem 1.2rem 0 1.2rem;
    align-items: center;
    column-gap: 2rem;
    .btn-navi-2 {
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 0.2rem solid $color-pri;
        transition: all 0.3s linear;
        cursor: pointer;
        position: absolute;
        z-index: 5;
        i {
            font-size: 2rem;
            color: $color-pri;
            transition: all 0.3s linear;
        }

        @include hover {
            background: $color-pri;

            i {
                color: #fdc202;
            }
        }

        @media screen and (max-width: 1200px) {
            width: 4rem;
            height: 4rem;

            i {
                font-size: 1.6rem;
            }
        }

        &.prev {
            top: 50%;
            transform: translateY(-50%);
            left: -5rem;

            @media screen and (max-width: 1380px) {
                left: -2rem;
            }

            @media screen and (max-width: 1300px) {
                left: 1rem;
            }

            @media screen and (max-width: 600px) {
                left: 0rem;
            }

            @media screen and (max-width: 460px) {
                left: -1rem;
            }
        }

        &.next {
            top: 50%;
            transform: translateY(-50%);
            right: -5rem;

            @media screen and (max-width: 1380px) {
                right: -2rem;
            }

            @media screen and (max-width: 1300px) {
                right: 1rem;
            }

            @media screen and (max-width: 600px) {
                right: 0rem;
            }

            @media screen and (max-width: 460px) {
                right: -1rem;
            }
        }
    }
}

.btn-filter {
    height: 4rem;
    width: 4rem;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background: $color-pri;
    cursor: pointer;
    display: none;
    @media screen and (max-width: 1024px) {
        display: flex;
    }

    @media screen and (max-width: 700px) {
        position: absolute;
        right: 0;
        top: 0;
    }

    i {
        color: $color-white;
        font-size: 2rem;
    }
}

.btn-exits {
    width: 4rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $color-white;
    cursor: pointer;
    border-radius: 0.5rem;
    i {
        font-size: 3rem;
        color: $color-pri;
    }
}

.btn-al-ic {
    padding: 1.6rem;
    background: $color-pri;
    transition: all 0.3s linear;
    width: 4.8rem;
    height: 4.8rem;
    display: flex;
    justify-content: center;
    align-items: center;

    // @media screen and (max-width:800px) {
    //     width: 4rem;
    //     height: 4rem;
    // }

    @media screen and (max-width: 460px) {
        width: 3.6rem;
        height: 3.6rem;
    }

    &.cl-2 {
        background: $color-white;

        i {
            color: $color-pri;
            @include fz-18;
        }

        @include hover {
            background: $color-second;

            i {
                color: $color-white;
                @include fz-18;
            }
        }
    }

    i {
        color: $color-white;
        @include fz-18;
    }

    @include hover {
        background: $color-second;
    }
}

.btn-play {
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.2rem solid $color-white;
    position: relative;
    background-color: transparent;

    @media screen and (max-width: 800px) {
        width: 3.2rem;
        height: 3.2rem;
    }

    i {
        @include fz-16;
        color: $color-white;

        @media screen and (max-width: 800px) {
            @include fz-12;
        }
    }

    @keyframes lanToa {
        0% {
            transform: translate(-50%, -50%) scale(1);
            opacity: 0;
        }

        50% {
            transform: translate(-50%, -50%) scale(1.5);
            opacity: 0.5;
        }

        100% {
            transform: translate(-50%, -50%) scale(2);
            opacity: 0;
        }
    }

    &::before {
        content: "";
        width: 100%;
        height: 100%;
        border-radius: 50%;
        overflow: hidden;
        border: 0.1rem solid $color-white;
        background-color: transparent;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        animation: lanToa 2s linear infinite forwards;
    }

    &::after {
        content: "";
        width: 100%;
        height: 100%;
        border-radius: 50%;
        overflow: hidden;
        border: 0.1rem solid $color-white;
        background-color: transparent;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        opacity: 0;
        animation: lanToa 2s 1.2s linear infinite forwards;
    }
}

.btn-like2 {
    padding: 1.2rem 2.4rem;
    background: $color-pri;
    border-radius: var(--radius);
    display: flex;
    align-items: center;
    column-gap: var(--columnContent0);
    cursor: pointer;
    user-select: none;
    transition: 0.3s linear;
    @include hover {
        background: $color-second;
    }

    @media screen and (max-width: 1200px) {
        padding: 1rem 2rem;
    }

    @media screen and (max-width: 800px) {
        padding: 0.8rem 1.6rem;
    }

    @media screen and (max-width: 600px) {
        padding: 0.6rem 1.2rem;
    }

    input {
        display: none;

        &:checked {
            & ~ .ic {
                i {
                    font-weight: 900;
                    color: $color-white;
                }
            }
        }
    }

    .note-text {
        user-select: none;
    }

    .ic {
        @include fz-16;
        i {
            color: $color-white;
        }
        transition: all 0.3s ease;
    }
}
