
.links-main2 {
    position: fixed;
    bottom: 5rem;
    right: 3%;
    z-index: 800;
    opacity: 0;
    transition: 0.5s ease;

    &.actived {
        opacity: 1;
    }

    &-list {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
    }

    &-item {
    }

    &-ic {
        width: 2rem;
        height: 2rem;
        display: flex;
        justify-content: center;
        align-items: center;

        @media screen and (max-width: 1200px) {
            width: 1.8rem;
            height: 1.8rem;
        }

        @media screen and (max-width: 800px) {
            width: 1.6rem;
            height: 1.6rem;
        }

        @media screen and (max-width: 600px) {
            width: 1.4rem;
            height: 1.4rem;
        }

        i {
            @include fz-16;
            line-height: 100%;
            transition: 0.3s ease;
            position: relative;
            z-index: 5;

            @media screen and (max-width: 600px) {
                @include fz-12;
            }
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
            position: relative;
            z-index: 5;
        }
    }

    &-link {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 4.8rem;
        height: 4.8rem;
        border-radius: 50%;
        position: relative;
        transition: 0.3s ease;
        cursor: pointer;

        @media screen and (max-width: 1200px) {
            width: 4rem;
            height: 4rem;
        }

        @media screen and (max-width: 800px) {
            width: 3.6rem;
            height: 3.6rem;
        }

        @media screen and (max-width: 600px) {
            width: 3.2rem;
            height: 3.2rem;
        }

        &.btnTop {
            background: $color-white;
            border: 0.1rem solid $color-text;

            @include hover {
                background: $color-second;
                border-color: $color-second;

                i {
                    color: $color-white;
                }
            }

            @keyframes lenXuong {
                0% {
                    transform: translateY(0);
                }
                30% {
                    transform: translateY(-10%);
                }

                50% {
                    transform: translateY(0%);
                }

                70% {
                    transform: translateY(10%);
                }

                100% {
                    transform: translateY(0%);
                }
            }

            i {
                position: relative;
                // top: 0.1rem;
                transform: translateY(0);

                animation: lenXuong 1.2s linear infinite forwards;
            }
        }

        &.btnFollow {
            background: $color-pri;
            position: relative;

            @include hover {
                background: $color-black;

                &::before {
                    background: $color-black;
                }

                &::after {
                    background: $color-black;
                }
            }

            @keyframes lanToa2 {
                0% {
                    transform: translate(-50%, -50%) scale(1);
                    opacity: 0;
                }

                50% {
                    transform: translate(-50%, -50%) scale(1.3);
                    opacity: 0.5;
                }

                100% {
                    transform: translate(-50%, -50%) scale(1.4);
                    opacity: 0;
                }
            }

            &::before {
                content: "";
                width: 100%;
                height: 100%;
                border-radius: 50%;
                position: absolute;
                top: 50%;
                left: 50%;
                background: $color-pri;
                transform: translate(-50%, -50%) scale(1);
                opacity: 0;
                animation: lanToa2 1.2s linear infinite forwards;
                z-index: -1;
                transition: 0.3s linear;
            }

            &::after {
                content: "";
                width: 100%;
                height: 100%;
                border-radius: 50%;
                position: absolute;
                top: 50%;
                left: 50%;
                background: $color-pri;
                transform: translate(-50%, -50%) scale(1);
                opacity: 0;
                z-index: -1;
                animation: lanToa2 1.2s 0.6s linear infinite forwards;
                transition: 0.3s linear;
            }

            @keyframes rungLac {
                0% {
                    transform: translate(0) scale(1) rotate(0);
                }

                30% {
                    transform: translate(0) scale(1.1) rotate(-15deg);
                }

                50% {
                    transform: translate(0) scale(1.2) rotate(15deg);
                }

                70% {
                    transform: translate(0) scale(1.3) rotate(-15deg);
                }

                90% {
                    transform: translate(0) scale(1.2) rotate(15deg);
                }

                100% {
                    transform: translate(0) scale(1) rotate(0);
                }
            }

            img {
                transform: translate(0) scale(1) rotate(0);
                animation: rungLac 2s linear infinite forwards;
            }
        }
    }

    &-popup {
        position: absolute;
        min-width: 50rem;
        border: 0.1rem solid $color-gray4;
        box-shadow: 0px 10px 30px 0px rgba(0, 0, 0, 0.1);
        background: $color-white;
        right: 8rem;
        bottom: 0;
        border-radius: var(--radius);
        opacity: 0;
        transform: translate(calc(100% - 24rem), calc(100% - 14rem)) scale(0);
        visibility: hidden;
        pointer-events: none;
        transition: all 0.3s linear;
        &.showed {
            opacity: 1;
            transform: translate(0, 0) scale(1);
            visibility: visible;
            pointer-events: visible;
        }

        @media screen and (max-width: 1200px) {
            right: 6rem;
        }

        @media screen and (max-width: 800px) {
            right: 5rem;
            min-width: 35rem;
        }

        @media screen and (max-width: 460px) {
            right: 5rem;
            min-width: 30rem;
        }

        @media screen and (max-width: 400px) {
            right: 4rem;
            min-width: 25rem;
        }

        @media screen and (max-width: 360px) {
            right: 4rem;
            min-width: 22rem;
        }

        &-wrap {
            padding: 3.2rem;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            @media screen and (max-width: 800px) {
                padding: 2.4rem;
            }

            @media screen and (max-width: 460px) {
                padding: 2.6rem 1.2rem;
            }

            @media screen and (max-width: 360px) {
                padding: 2.8rem 1rem;
            }
        }

        &-ex {
            position: absolute;
            cursor: pointer;
            top: 1rem;
            right: 1rem;

            @media screen and (max-width: 460px) {
                top: 0.5rem;
                right: 1rem;
            }

            @include hover {
                i {
                    color: $color-pri;
                }
            }

            i {
                // font-size: ;
                @include fz-20;
                color: $color-second;
                transition: 0.3s linear;
            }
        }

        &-form {
            &-wrap {
                .form-ip-ip input {
                    border-radius: 30rem;
                    padding: 1.6rem 10rem 1.6rem 1.6rem;
                }
                position: relative;
            }

            &-btn {
                padding: 0rem 1.6rem;
                height: 80%;
                background: $color-pri;
                border-radius: 10rem;
                color: $color-white;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                right: 0.5rem;
                transition: 0.3s linear;
                @include fz-16;
                cursor: pointer;
                @include hover {
                    background: $color-second;
                }

                @media screen and (max-width: 1200px) {
                    padding: 0rem 1.4rem;
                }

                @media screen and (max-width: 800px) {
                    padding: 0rem 1.2rem;
                }

                @media screen and (max-width: 600px) {
                    padding: 0rem 1rem;
                }

                @media screen and (max-width: 460px) {
                    padding: 0rem 0.8rem;
                    @include fz-12;
                }
            }
        }
    }
}
