.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 2002;
  transition: 0.5s linear;
  background-color: $color-white;

  @media screen and (max-width: 1200px) {
    box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.2);
  }

  &-lang {
    &-list {
      display: flex;
      align-items: center;
      column-gap: 1.2rem;
    }

    &-item {
      position: relative;
      &:not(:first-child) {
        &::before {
          content: "|";
          left: -0.8rem;
          top: 50%;
          transform: translateY(-50%);
          position: absolute;
          font-weight: 700;
        }
      }

      &.actived {
        .header-lang-link {
          color: $color-pri !important;
        }
      }
    }

    &-link {
      color: $color-black;
      @include fz-20;

      @include hover {
        color: $color-pri;
      }

      @media screen and (max-width:1200px) {
      @include fz-16;
        
      }
    }
  }

  &-top {
    position: relative;
    transition: all 0.5s ease;
    height: 11rem;
    border-bottom: 0.1rem solid #e3e1e8;
    &-lf {
      display: flex;
      align-items: center;
      column-gap: var(--columnContent4);

      @media screen and (max-width: 1200px) {
        display: none;
      }
    }

    &-wrap {
      display: flex;
      justify-content: space-between;
      height: 100%;
    }

    @media screen and (max-width: 1200px) {
      height: 6rem;
      border-bottom: 0;
      background: $color-pri;
    }
  }

  &-bottom {
    height: 7rem;
    transition: all 0.5s linear;

    @media screen and (max-width: 1200px) {
      height: 5rem;
    }

    @media screen and (max-width: 600px) {
      height: 4rem;
    }

    &-wrap {
      display: flex;
      height: 100%;
      justify-content: center;
      column-gap: var(--columnContent1);
      width: 100%;

      transition: all 0.3s ease;

      .header-mobi {
        display: none;

        @media screen and (max-width: 1200px) {
          display: flex;
        }
      }

      .header-lang {
        display: none;

        @media screen and (max-width: 1200px) {
          display: flex;
        }
      }

      @media screen and (max-width: 1200px) {
        // .header-logo {
        //   display: none;
        // }
        justify-content: space-between;
      }

      .header-box {
        @media screen and (max-width: 1200px) {
          .header-login {
            display: none;
          }
        }
      }
    }
  }

  // @media screen and (max-width:1200px) {
  //   height: 7rem;
  // }

  &-bg {
    padding: 0 4rem;
    height: 48rem;
    display: flex;
    align-items: center;
    transition: 0.5s linear;

    @media screen and (max-width:1200px) {
      display: none;
    }
  }

  &-bn {
    width: 100%;
    height: 40rem;
    overflow: hidden;
    border-radius: var(--radius);
    display: block;
    transition: 0.5s linear;
    img {
      width: 100%;
      height: 100%;

      object-fit: cover;
    }
  }

  &.actived {
    background: $color-white;
    height: auto;
    box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.3);

    .header-bg {
      height: 0;
    }

    .header-bn {
      transform: translateY(-100%);
      opacity: 0;
    }

    .header-logo {
      transform: translate(-50%, -50%) scale(0.8);
    }

    .header-top {
      height: 7rem;
    }

    .header-bottom {
      height: 5rem;
    }

    .menu-list {
      .menu-item {
        .menu-link {
          &::before {
            bottom: 30%;
          }
        }
      }
    }

    .header-banner {
      transform: translateY(-150%);
      opacity: 0;
    }

    @media screen and (max-width: 1200px) {
      height: 7rem;
    }

    @media screen and (max-width: 1200px) {
      & ~ .menu-mb {
        top: 7rem;
        height: calc(100% - 7rem);
      }
    }
  }

  &.no-bn {
    .header-bg {
      display: none;
    }

    &.actived {
      background: $color-white;
      height: auto;
      box-shadow: 0 0 0.3rem rgba($color: #000000, $alpha: 0.3);

      .header-logo {
        transform: translate(-50%, -50%) scale(0.8);
      }

      .header-top {
        height: 7rem;
      }

      .header-bottom {
        height: 5rem;
      }

      .menu-list {
        .menu-item {
          .menu-link {
            &::before {
              bottom: 30%;
            }
          }
        }
      }

      .header-banner {
        transform: translateY(-150%);
        opacity: 0;
      }

      @media screen and (max-width: 1200px) {
        height: 7rem;
      }
    }
  }

  &.success-hd {
    .header-btn {
      .dn {
        display: none;
      }

      .dk {
        display: none;
      }
    }

    .header-user {
      display: flex;
    }

    & ~ .menu-mb {
      .header-user {
        .menu-list {
          display: none;
        }

        .admin_info {
          .admin_info--link {
            display: none;
          }

          .admin_info--if {
            display: flex;
          }
        }
      }

      .menu-mb-bottom {
        opacity: 1;
        pointer-events: visible;
      }
    }
  }

  &-btn {
    display: flex;
    height: 100%;
    align-items: center;
    column-gap: var(--columnContent1);

    .note-text {
      font-weight: 400 !important;
      @media screen and (max-width: 1200px) {
        font-weight: 600 !important;
        display: none;
        &.dn {
          display: block;
          color: $color-white;
        }
      }

      @media screen and (max-width:460px) {
        @include fz-12;
      }
    }

    .btn {
      @media screen and (max-width: 1200px) {
        background: $color-purple2;
        box-shadow: unset !important;
      }

      &-text {
        @media screen and (max-width: 1200px) {
          color: $color-black-2;
          font-weight: 600 !important;
        }
      }
    }
  }

  &-user {
    position: relative;
    height: 100%;
    display: flex;
    align-items: center;
    padding-right: 2rem;
    display: none;
    &::before {
      content: "";
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: 0;
      background-image: url(../assets/images/arrd.svg);
      width: 1.4rem;
      height: 1.4rem;
      background-size: 100% 100%;
      background-repeat: no-repeat;

      @media screen and (max-width: 1200px) {
        filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(93deg) brightness(105%)
          contrast(105%);
      }
    }

    .admin_info--ava {
      pointer-events: none;
      &:before {
        display: none;
      }
    }

    &:hover {
      & > .menu-list {
        opacity: 1;
        visibility: visible;
        transition: all 0.3s linear;
        animation: hienLen2 0.3s linear;
      }
    }

    .menu-list {
      position: absolute;
      top: calc(100% + 0.2rem);
      opacity: 0;
      visibility: hidden;
      left: 0rem;
      display: flex;
      flex-direction: column;
      width: 100%;
      min-width: 20rem;
      height: auto;
      background: $color-white;
      margin: 0;
      box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.1);

      z-index: 2;
      transition: all 0.5s ease;

      @media screen and (max-width: 600px) {
        top: calc(100% + 0rem);
      }

      &::before {
        content: "";
        position: absolute;
        width: 100%;
        height: 2rem;
        background-color: transparent;
        top: -10%;
        left: 0;
      }

      .menu-item {
        padding: 0;

        &.current-menu-item {
          .menu-link {
            color: $color-second;
            font-weight: 600;
          }
        }
      }

      .menu-link {
        width: 100%;
        border-radius: 0;
        padding: 1rem 1.2rem;
        color: $color-text2;

        &::before {
          display: none;
        }
      }
    }

    .admin_info {
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 0.6rem;
      border: none;
      @media screen and (max-width: 360px) {
        max-width: 12rem;
        overflow: hidden;
      }
    }

    .admin_info--ava {
      width: 6rem;
      height: 6rem;

      @media screen and (max-width: 1200px) {
        width: 5rem;
        height: 5rem;
      }

      @media screen and (max-width: 800px) {
        width: 4rem;
        height: 4rem;
      }

      @media screen and (max-width: 460px) {
        width: 3.6rem;
        height: 3.6rem;
      }
    }

    .admin_info--if {
      row-gap: 0.4rem !important;

      @media screen and (max-width: 600px) {
        row-gap: 0.2rem !important;
      }

      .note-text {
        @include fz-14;

        @media screen and (max-width: 1200px) {
          color: $color-white;
        }

        @media screen and (max-width: 460px) {
          @include fz-10;
        }
      }

      .note-sm {
        @include fz-12;

        @media screen and (max-width: 1200px) {
          color: $color-white2;
        }

        @media screen and (max-width: 460px) {
          @include fz-10;
        }
      }
    }
  }

  &-nav {
    height: 100%;
    display: flex;
    align-items: center;
    column-gap: 1.2rem;

    @media screen and (max-width: 1200px) {
      .menu-list {
        display: none;
      }
    }
  }
  &-search {
    position: relative;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;

    &-ic {
      width: 2.4rem;
      height: 2.4rem;
      // border-radius: 50%;
      // background: $color-gray5;
      transition: all 0.5s ease;
      flex-shrink: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;

      // opacity: 0;
      @keyframes anHienSearch {
        0% {
          opacity: 0;
        }

        50% {
          opacity: 0.5;
        }

        100% {
          opacity: 1;
        }
      }

      &.showed {
        display: flex !important;
        animation: anHienSearch 0.5s linear;
      }

      &.hiddened {
        display: none !important;
        animation: anHienSearch 0.5s linear;
      }

      &.open {
        display: flex;
        transform: translateY(-0.1rem);
      }

      &.close {
        display: none;
      }

      @include hover {
        // background: $color-pri;
        img {
          filter: brightness(0) saturate(100%) invert(8%) sepia(64%) saturate(7350%) hue-rotate(258deg) brightness(100%)
            contrast(102%);
        }
      }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;

        filter: brightness(0) saturate(100%) invert(0%) sepia(0%) saturate(0%) hue-rotate(335deg) brightness(104%)
          contrast(107%);
      }
    }

    &-ip {
      width: 30rem;
      display: flex;
      flex-direction: row-reverse;
      height: 5rem;
      // box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.5);
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      right: -33rem;
      opacity: 0;
      background: $color-white;
      visibility: hidden;
      pointer-events: none;
      transition: all 0.3s linear;
      border: 0.1rem solid rgba($color: #000000, $alpha: 0.6);
      z-index: 10;
      clip-path: inset(0 100% 0 0);
      &.showed {
        opacity: 1;
        visibility: visible;
        pointer-events: visible;
        clip-path: inset(0 0 0 0);
      }

      input {
        width: 100%;
        padding: 1.2rem;
        background: $color-white;

        @include fz-14;

        &::placeholder {
          @include fz-14;
        }
      }

      .btn-sub {
        width: fit-content;
        padding: 1.2rem;
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: unset;
        border-radius: 0;
        background: $color-pri;

        @include hover {
          img {
            filter: brightness(0) saturate(100%) invert(94%) sepia(100%) saturate(22%) hue-rotate(46deg)
              brightness(106%) contrast(105%);
          }
        }

        img {
          width: 2.8rem;
          height: 2.8rem;
          object-fit: contain;
          filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7500%) hue-rotate(179deg)
            brightness(105%) contrast(105%);
        }
      }
    }
  }

  &-logo {
    max-width: 17rem;
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    // position: relative;
    overflow: hidden;
    transition: all 0.5s ease;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    .icon {
      width: 100%;
      height: 100%;
    }

    @include hover {
      img {
        transform: scale(0.9);
      }
    }

    img {
      width: 100%;
      height: 100%;
      transition: all 0.3s ease;
      object-fit: contain;

      @media screen and (max-width: 1200px) {
        filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(93deg) brightness(105%)
          contrast(105%);
      }
    }
    @media screen and (max-width: 1400px) {
      max-width: 15rem;
    }

    @media screen and (max-width: 1200px) {
      max-width: 9rem;
      position: relative;
      left: unset;
      transform: unset;
      top: unset;
    }

    @media screen and (max-width: 800px) {
      max-width: 8rem;
    }

    @media screen and (max-width: 600px) {
      max-width: 8rem;
    }

    @media screen and (max-width: 320px) {
      max-width: 7rem;
    }
  }

  &-mobi {
    width: 3.4rem;
    height: 2.4rem;
    position: relative;
    // display: none;
    cursor: pointer;
    margin: auto 0;

    @include hover {
      .line {
        background: $color-pri;
      }
    }

    @media screen and (max-width: 1200px) {
      width: 3.2rem;
      height: 2rem;
    }

    @media screen and (max-width: 600px) {
      width: 2.8rem;
      height: 2rem;
    }

    &.actived {
      .line {
        background: $color-pri;
        &:first-child {
          top: 50%;
          transform: translate(-50%, -50%) rotate(45deg);
          width: 100%;
          transition:
            top 0.3s linear,
            transform 0.3s linear 0.5s;
        }

        &:nth-child(2) {
          opacity: 0;
          transition: opacity 0.1s ease 0.3s;
        }

        &:last-child {
          bottom: 50%;
          transform: translate(-50%, 50%) rotate(-45deg);
          width: 100%;
          transition:
            bottom 0.3s linear,
            transform 0.3s linear 0.5s;
        }
      }
    }

    .line {
      width: 100%;
      display: block;
      height: 0.3rem;
      background-color: $color-black;
      transform: translateX(-50%);
      border-radius: 100rem;
      overflow: hidden;
      position: absolute;
      border-radius: 100rem;
      transition: 0.5s linear;

      @media screen and (max-width: 1200px) {
        background: $color-pri;
      }
      &:first-child {
        top: 0;
        left: 50%;
        width: 100%;

        transition:
          top 0.3s linear 0.5s,
          transform 0.3s linear 0s;
      }

      &:nth-child(2) {
        top: 50%;
        left: 0;
        width: 100%;
        transform: translate(0%, -50%);
        transition: opacity 0.1s ease 0.3s;
      }

      &:last-child {
        width: 100%;
        bottom: 0%;
        left: 50%;
        transition:
          bottom 0.3s linear 0.5s,
          transform 0.3s linear 0s;
      }
    }
  }

  &-box {
    height: 100%;
    display: flex;
    align-items: center;
    column-gap: 1.6rem;

    @media screen and (max-width: 1200px) {
      column-gap: 1.2rem;
    }

    @media screen and (max-width: 800px) {
      column-gap: 0.8rem;
    }

    @media screen and (max-width: 600px) {
      column-gap: 0.6rem;
    }

    .btn {
      flex-shrink: 0;

      width: fit-content;

      @media screen and (max-width: 1200px) {
        min-width: unset;
      }

      // &-ic {
      //   display: none;

      //   @media screen and (max-width: 500px) {
      //     display: flex;
      //   }
      // }

      &-text {
        // @media screen and (max-width: 500px) {
        //   display: none;
        // }
      }

      @media screen and (max-width: 500px) {
        padding: 1.2rem 1.4rem;
      }

      @media screen and (max-width: 360px) {
        padding: 1.2rem 1.3rem;
      }
    }
  }

  &-login {
    height: 100%;
    display: flex;
    align-items: center;
    column-gap: var(--columnContent1);
  }
}
