.admin {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    --row: 2.4rem;

    @media screen and (max-width: 1200px) {
        --row: 1.6rem;
    }

    @media screen and (max-width: 800px) {
        --row: 1.4rem;
    }

    @media screen and (max-width: 600px) {
        --row: 1.2rem;
    }

    @media screen and (max-width: 460px) {
        --row: 1rem;
    }

    &_content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &.page-like {
        .admin_lf {
        }
        .admin_rt--wrap {
            max-width: unset;
            margin: unset;
        }
    }

    &_wrap {
        @media screen and (max-width: 800px) {
            flex-direction: column;
            row-gap: 1.6rem;
        }

        @media screen and (max-width: 600px) {
            flex-direction: column;
            row-gap: 1.4rem;
        }

        @media screen and (max-width: 460px) {
            flex-direction: column;
            row-gap: 1.4rem;
        }
    }

    &_lf {
        width: calc(100% * 3 / 12);

        &--wrap {
            width: 100%;
            height: 100%;
        }

        &--content {
            position: sticky;
            top: var(--size-hd);
            padding: 2.4rem;
            border: 0.1rem solid $color-gray4;
            border-radius: var(--radius);
            overflow: hidden;
            display: flex;
            flex-direction: column;
            row-gap: var(--row);
            transition: all 0.3s linear;
            @include hover {
                border-color: $color-pri;
            }

            @media screen and (max-width: 1200px) {
                top: calc(var(--size-hd) + 2rem);
                padding: 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.6rem;
            }

            @media screen and (max-width: 600px) {
                top: calc(var(--size-hd) + 2rem);
                padding: 1.2rem;
            }

            .admin_info--ava {
                width: 8rem;
                height: 8rem;

                @media screen and (max-width: 600px) {
                    width: 6rem;
                    height: 6rem;
                }
            }
        }

        @media screen and (max-width: 1024px) {
            width: calc(100% * 3.5 / 12);
        }

        @media screen and (max-width: 900px) {
            width: calc(100% * 4 / 12);
        }

        @media screen and (max-width: 800px) {
            width: 100%;
        }
    }

    &_rt {
        width: 100%;
        flex: 1;
        overflow: hidden;

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent3);

            // .like_item--delete {
            //     display: flex;
            // }

            // .like_item--des {
            //     .note-text {
            //         &.content {
            //             display: none;
            //         }

            //         &.title {
            //             padding-right: 1.6rem;
            //         }
            //     }
            // }

            .woocommerce-order {
                .woocommerce-notice {
                    @include fz-20;
                }
            }

            .woocommerce-order-details {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent0);

                h2 {
                    @include fz-20;
                    font-weight: 700;
                    color: $color-black;
                }
            }

            .woocommerce ul.order_details {
                display: flex;
                flex-wrap: wrap;
                column-gap: var(--columnContent2);
                row-gap: var(--rowContent2);
                align-items: start;
                justify-content: space-between;

                @media screen and (max-width: 1200px) {
                    justify-content: start;
                }
            }

            .woocommerce ul.order_details li {
                float: unset !important;
                padding-right: 0 !important;
                border-right: unset !important;
                margin-right: 0 !important;
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent0);
            }

            .woocommerce ul.order_details::after,
            .woocommerce ul.order_details::before {
                display: none;
            }

            .woocommerce-customer-details {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent0);

                h2 {
                    @include fz-20;
                    font-weight: 700;
                    color: $color-black;
                }
            }

            .btn {
                box-shadow: none !important;
            }

            @media screen and (max-width: 1200px) {
                max-width: none;
                margin: unset;
            }

            .info {
                &_item {
                    width: 50%;
                    &:nth-child(8) {
                        width: 100%;
                    }

                    &:last-child {
                        width: 50%;
                    }
                }
            }
        }
    }

    &_info {
        display: flex;
        flex-direction: column;
        row-gap: 1.6rem;
        padding-bottom: var(--row);

        &--if {
            display: flex;
            flex-direction: column;
            row-gap: 0.4rem;

            &-name {
                display: flex;
                align-items: center;
                column-gap: var(--columnContent0);
                .note-lg {
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }

                .note-text {
                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }

                .ic {
                    width: 1.6rem;
                    height: 1.6rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border-radius: 50%;
                    overflow: hidden;
                    position: relative;
                    top: -0.05rem;
                    flex-shrink: 0;
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: contain !important;
                    }
                }
            }

            .note-text {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }

            .note-sm {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }
        }

        &--ava {
            position: relative;
            width: 9rem;
            height: 9rem;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;
            &-img {
                width: 100%;
                height: 100%;
                overflow: hidden;
                position: relative;
                border-radius: 50%;
                flex-shrink: 0;
                cursor: pointer;

                img {
                    width: 100%;
                    height: 100%;
                    display: block;
                    object-fit: cover;
                    object-position: top;
                    border-radius: 50%;
                    overflow: hidden;
                }
            }

            &.border {
                .admin_info--ava-border {
                    display: block;
                }

                .admin_info--ava-img {
                    width: calc(100% - 2rem);
                    height: calc(100% - 2rem);
                }

                &::before {
                    display: none;
                }
            }

            &-border {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                z-index: 1;
                pointer-events: none;
                width: 100%;
                height: 100%;
                border-radius: 50%;
                display: none;
                img {
                    width: 100%;
                    height: 100%;
                    object-fit: contain;
                }
            }

            &.no-change {
                pointer-events: none;

                &::before {
                    display: none;
                }
            }

            &::before {
                content: "";
                background-image: url(../assets/images/upload.svg);
                width: 2.4rem;
                height: 2.4rem;
                border-radius: 50%;
                overflow: hidden;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                position: absolute;
                bottom: 0;
                right: 0;
                z-index: 2;
                @media screen and (max-width: 600px) {
                    width: 2rem;
                    height: 2rem;
                }
            }

            @media screen and (max-width: 600px) {
                width: 6rem;
                height: 6rem;
            }

            input {
                display: none;
            }
        }

        &--link {
            display: flex;
            flex-direction: column;
            row-gap: 0.4rem;
            display: none;
            .note-text {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }

            .note-sm {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
                text-decoration: underline;
            }
        }
    }

    &_list {
        display: flex;
        flex-direction: column;
        row-gap: 1.2rem;
        padding-bottom: var(--row);
        border-bottom: 0.1rem solid $color-gray4;

        @media screen and (max-width: 800px) {
            flex-direction: row;
            flex-wrap: wrap;
            column-gap: 0.6rem;
            row-gap: 0.8rem;
        }

        @media screen and (max-width: 600px) {
            flex-direction: row;
            flex-wrap: wrap;
            column-gap: 0.6rem;
            row-gap: 0.6rem;
        }
    }

    &_item {
        &.actived {
            .admin_link {
                background: $color-white2;

                .note-text {
                    color: $color-text3;
                }
            }
        }

        @media screen and (max-width: 800px) {
            width: calc(100% / 3 - 0.6rem);
        }

        @media screen and (max-width: 600px) {
            width: calc(100% / 2 - 0.6rem);
        }

        @media screen and (max-width: 400px) {
            width: 100%;
        }
    }

    &_link {
        display: flex;
        align-items: center;
        column-gap: 1.2rem;
        padding: 1.2rem 1.6rem;
        border-radius: 0.4rem;
        transition: 0.3s ease;

        @media screen and (max-width: 1200px) {
            column-gap: 0.6rem;
            padding: 1rem 1.2rem;
        }

        @media screen and (max-width: 800px) {
            column-gap: 0.8rem;
            padding: 0.8rem 0.6rem;
        }

        &.out {
            .note-text {
                color: $color-gray3;
            }
            @include hover {
                .note-text {
                    color: $color-white;
                }
                .admin_ic {
                    img {
                        filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7500%) hue-rotate(70deg)
                            brightness(120%) contrast(118%);
                    }
                }
            }
        }

        .note-text {
            line-height: 100%;
            color: $color-text3;
        }

        @include hover {
            background: $color-white2;

            .note-text {
                color: $color-text3;
            }

            // .admin_ic {
            //     img {
            //         filter: brightness(0) saturate(100%) invert(100%) sepia(0%) saturate(7500%) hue-rotate(70deg)
            //             brightness(120%) contrast(118%);
            //     }
            // }
        }
    }

    &_ic {
        width: 2.4rem;
        height: 2.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @media screen and (max-width: 1200px) {
            width: 2rem;
            height: 2rem;
        }
    }

    &_out {
        .admin_link {
            @include hover {
                background: #d71313;
            }
        }
    }
}

.change_pass {
    // max-width: 50rem;
    width: 100%;

    display: flex;
    column-gap: var(--columnContent);

    @media screen and (max-width: 600px) {
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--form {
        width: 55%;

        @media screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &--bg {
        width: calc(100% - var(--columnContent));
        flex: 1;

        @media screen and (max-width: 600px) {
            width: 100%;
        }
    }

    &--img {
        padding-top: calc(100% * 609 / 384);
        width: 100%;
        overflow: hidden;
        border-radius: var(--radius);
        position: relative;
        display: block;

        img {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .sign_form {
        .btn {
            width: fit-content;
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    @media screen and (max-width: 800px) {
        max-width: none;
    }
}

.support {
    --row: 3.2rem;

    @media screen and (max-width: 1200px) {
        --row: 2.4rem;
    }

    @media screen and (max-width: 800px) {
        --row: 1.6rem;
    }

    @media screen and (max-width: 600px) {
        --row: 1.2rem;
    }

    &_list {
        row-gap: var(--row);
    }

    &_item {
        @media screen and (max-width: 460px) {
            width: 100%;
        }
    }

    &_ic {
        width: 4.8rem;
        height: 4.8rem;
        border-radius: 50%;
        background: #fcf9eb;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        transition: all 0.3s linear;
        img {
            width: 2.4rem;
            height: 2.4rem;
            display: block;
            object-fit: cover;
        }

        @media screen and (max-width: 600px) {
            width: 4rem;
            height: 4rem;

            img {
                width: 2rem;
                height: 2rem;
            }
        }
    }

    &_title {
        display: flex;
        flex-direction: column;
        row-gap: 0.4rem;

        .note-text {
            transition: all 0.3s linear;
        }
    }

    &_phone {
        .note-lg {
            transition: all 0.3s linear;

            @media screen and (max-width: 900px) {
                @include fz-14;
            }
        }
    }

    &_link {
        padding: 2.4rem;
        // border: 0.1rem solid $color-gray6;
        border-radius: 1.6rem;
        display: flex;
        flex-direction: column;
        row-gap: var(--row);
        transition: all 0.3s linear;
        overflow: hidden;
        height: 100%;
        @media screen and (max-width: 1200px) {
            padding: 1.6rem;
        }

        @include hover {
            border-color: $color-blue;

            .support_ic {
                background: #ecedfa;

                img {
                    filter: brightness(0) saturate(100%) invert(23%) sepia(81%) saturate(2270%) hue-rotate(227deg)
                        brightness(91%) contrast(91%);
                }
            }

            .support_phone {
                .note-lg {
                    color: $color-pri;
                }
            }

            .support_title {
                .note-text {
                    color: $color-second;
                }
            }
        }

        &--top {
            display: block;
        }

        &--bottom {
            display: flex;
            flex-direction: column;
            row-gap: 1.6rem;
        }
    }
}

.info {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }
    &_btn {
        display: flex;
        align-items: center;
        column-gap: 1.2rem;
    }

    &_tab {
        &--list {
            display: flex;
            column-gap: 3.2rem;
            position: relative;
            width: fit-content;
            // flex-wrap: wrap;
            row-gap: 1.2rem;
            padding-bottom: 0.5rem;

            @media screen and (max-width: 1200px) {
                column-gap: 2.4rem;
            }

            @media screen and (max-width: 800px) {
                column-gap: 2rem;
                padding-bottom: 0.3rem;
                margin-bottom: 0.6rem;
            }

            @media screen and (max-width: 600px) {
                column-gap: 1.2rem;
            }

            &::before {
                content: "";
                width: 100%;
                height: 0.2rem;
                background: #e6e6e6;
                position: absolute;
                bottom: 0rem;
                left: 0;

                @media screen and (max-width: 800px) {
                    bottom: -0.3rem;
                }
            }
        }

        &--item {
            &.actived {
                .info_tab--link {
                    color: $color-text2;
                    &::before {
                        width: 100%;
                        left: 0;
                    }
                }
            }
        }

        &--link {
            color: $color-text;
            font-weight: 700;
            position: relative;
            display: block;
            height: 100%;
            flex-shrink: 0;
            @include fz-16;
            // white-space: nowrap;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            @include hover {
                color: $color-text2;
                &::before {
                    width: 100%;
                    left: 0;
                }
            }

            &::before {
                content: "";
                position: absolute;
                right: 0;
                bottom: -0.5rem;
                width: 0;
                height: 0.2rem;
                background: $color-second;
                transition: all 0.3s linear;

                @media screen and (max-width: 800px) {
                    bottom: -0.6rem;
                }
            }

            @media screen and (max-width: 360px) {
                @include fz-12;
            }
        }
    }

    &_list {
        row-gap: 1.6rem;

        @media screen and (max-width: 1200px) {
            row-gap: 1.4rem;
        }

        @media screen and (max-width: 600px) {
            row-gap: 1.2rem;
        }

        @media screen and (max-width: 460px) {
            row-gap: 0.8rem;
        }
    }

    &_item {
        &:nth-child(7) {
            width: 100%;
        }

        &:last-child {
            width: 100%;
        }

        @media screen and (max-width: 400px) {
            width: 100% !important;
        }
    }

    &_avatar {
        width: 8rem;
        height: 8rem;
        border-radius: 50%;
        position: relative;
        overflow: hidden;
        cursor: pointer;
        img {
            width: 100% !important;
            height: 100% !important;
            object-fit: cover;
        }

        &::before {
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-image: url(../assets/images/camera.svg);
            width: 2.6rem;
            height: 2.6rem;
            background-size: 100% 100%;
            background-repeat: no-repeat;
            z-index: 3;
            // opacity: 0.8;
        }

        &::after {
            content: "";
            background: rgba($color: #000000, $alpha: 0.5);
            inset: 0;
            width: 100%;
            height: 100%;
            position: absolute;
            z-index: 1;
        }

        input {
            display: none;
        }

        @media screen and (max-width: 1200px) {
            width: 7rem;
            height: 7rem;
        }

        @media screen and (max-width: 600px) {
            width: 6rem;
            height: 6rem;
        }
    }
}

.connect {
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: 3rem;

        @media screen and (max-width: 1200px) {
            row-gap: 2.4rem;
        }

        @media screen and (max-width: 800px) {
            row-gap: 2rem;
        }

        @media screen and (max-width: 600px) {
            row-gap: 1.6rem;
        }
    }

    &_list {
        display: flex;
        flex-direction: column;
        row-gap: 1.2rem;
        max-width: 60rem;

        @media screen and (max-width: 800px) {
            max-width: unset;
        }

        .nsl-container-buttons {
            width: 100%;
        }

        div.nsl-container div.nsl-container-buttons a[data-provider="facebook"] {
            .nsl-button-svg-container {
                svg {
                    filter: brightness(0) saturate(100%) invert(39%) sepia(12%) saturate(4916%) hue-rotate(200deg)
                        brightness(96%) contrast(89%);
                }
            }
        }

        .nsl-container .nsl-button-default .nsl-button-label-container {
            flex: none;
            margin: unset;
            @include fz-16;
            padding: unset;
            color: $color-gray3;
            transition: 0.3s ease;
        }

        .nsl-container .nsl-container-buttons {
            padding: 0;
        }

        div.nsl-container-block-fullwidth .nsl-container-buttons a,
        div.nsl-container-block .nsl-container-buttons a {
            margin: 0;
        }

        .nsl-container .nsl-button-default {
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-radius: 10rem;
            border: 0.1rem solid $color-gray4;
            background-color: transparent !important;
            padding: 1.2rem 2.4rem;
            flex-wrap: wrap;
            row-gap: 0.6rem;
            column-gap: 1.2rem;
            transition: 0.3s ease;
            @media screen and (max-width: 1200px) {
                padding: 1rem 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 0.8rem 1.6rem;
            }

            @media screen and (max-width: 600px) {
                padding: 0.6rem 1.2rem;
            }

            @include hover {
                background: $color-second !important;

                .connect_link--lf {
                    .note-text {
                        color: $color-white;
                    }
                }

                .nsl-button-label-container {
                    color: $color-white !important;
                }
            }
        }

        .nsl-container .nsl-button-svg-container {
            padding: 0;
        }
    }

    &_item {
        &.success-link {
            .connect_link--rt {
                .note-text {
                    color: #139a69;
                }
            }
        }
    }

    &_link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 10rem;
        border: 0.1rem solid $color-gray4;
        padding: 1.2rem 2.4rem;
        flex-wrap: wrap;
        row-gap: 0.6rem;
        column-gap: 1.2rem;

        @media screen and (max-width: 1200px) {
            padding: 1rem 2rem;
        }

        @media screen and (max-width: 800px) {
            padding: 0.8rem 1.6rem;
        }

        @media screen and (max-width: 600px) {
            padding: 0.6rem 1.2rem;
        }

        @include hover {
            background: $color-second;

            .connect_link--lf {
                .note-text {
                    color: $color-white;
                }
            }
        }

        &--lf {
            display: flex;
            align-items: center;
            column-gap: 1.2rem;

            .note-text {
                color: $color-text2;
                transition: all 0.3s linear;
            }

            @media screen and (max-width: 1200px) {
                column-gap: 1rem;
            }

            @media screen and (max-width: 800px) {
                column-gap: 0.8rem;
            }

            @media screen and (max-width: 600px) {
                column-gap: 0.6rem;
            }
        }

        &--rt {
            .note-text {
                color: $color-gray3;

                @media screen and (max-width: 460px) {
                    @include fz-12;
                }
            }
        }

        &--ic {
            width: 1.8rem;
            height: 1.8rem;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-shrink: 0;
            transform: translateY(-0.1rem);
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }

            @media screen and (max-width: 1200px) {
