.like {
    width: 100%;
    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
        width: 100%;
        position: relative;
    }

    &_title {
        display: flex;
        align-items: center;
        justify-content: space-between;
        column-gap: 1.2rem;
        &--search {
            max-width: 48rem;
            width: 100%;
            .header-search-ip {
                position: relative;
                width: 100%;
                opacity: 1;
                top: unset;
                right: unset;
                visibility: visible;
                box-shadow: none;
                border-radius: var(--radius);
                pointer-events: visible;
                overflow: hidden;
                height: 5.6rem;
                clip-path: unset;
                transform: unset;
                border: none !important;
                @media screen and (max-width: 1200px) {
                    height: 4.8rem;
                }

                @media screen and (max-width: 800px) {
                    height: 4.2rem;
                }

                @media screen and (max-width: 600px) {
                    height: 4rem;
                }

                input {
                    border: 0.1rem solid $color-gray4;
                    border-radius: 0.4rem;
                    padding-left: 4.6rem;
                    padding-right: 1.6rem;
                    padding-top: 1.6rem;
                    padding-bottom: 1.6rem;
                    transition: all 0.5s ease;
                    background: #f6f5f7;
                    border-radius: var(--radius);
                    display: block;

                    &:focus {
                        border-color: $color-pri;
                    }
                }

                .btn-sub {
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);
                    left: 0.6rem;
                    background: none;

                    img {
                        width: 2rem;
                        height: 2rem;
                        filter: brightness(0) saturate(100%) invert(0%) sepia(2%) saturate(6894%) hue-rotate(333deg)
                            brightness(69%) contrast(92%);
                    }
                }
            }

            @media screen and (max-width: 800px) {
                max-width: 70%;
            }

            @media screen and (max-width: 460px) {
                max-width: unset;
            }
        }

        &--filter {
            min-width: 12rem;
            .select2-container .select2-selection--single .select2-selection__rendered {
                @include fz-16;
                padding-right: 2rem;
                color: $color-black;
            }

            .select2-container--default .select2-selection--single {
                border: none;
                padding: 0;
            }

            .select2-container--default .select2-selection--single .select2-selection__arrow::before {
                right: 0;
                top: 51%;

                @media screen and (max-width: 1200px) {
                    top: 45%;
                }

                @media screen and (max-width: 600px) {
                    top: 40%;
                }
            }
        }

        @media screen and (max-width: 460px) {
            flex-direction: column;
            row-gap: 0.6rem;
            align-items: end;
        }
    }

    &_list {
        row-gap: var(--rowContent);
    }

    &_item {
        @media screen and (max-width: 1024px) {
            width: calc(100% / 2);
        }

        @media screen and (max-width: 320px) {
            width: 100% !important;
        }

        &--delete {
            width: 1.4rem;
            height: 1.4rem;
            display: flex;
            align-items: center;
            justify-content: center;
            position: absolute;
            bottom: 0;
            right: 0;
            display: none;
            i {
                @include fz-16;
            }
        }

        &--tt {
            background: $color-purple2;
            color: $color-black;
            position: absolute;
            transform: translate(0%, 0%);
            @include fz-12;
            z-index: 100;
            padding: 0.2rem 0.4rem;
            // width: 100%;
            // white-space: nowrap;
            border-radius: 0.4rem;
            box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.1);
            display: block;
            box-sizing: border-box;
            opacity: 0;
            // min-width: 50%;
            left: 0;
            width: fit-content;
            pointer-events: none;
            // transform: translate(0%, 100%);

            &.showed {
                opacity: 1;
            }
        }

        &--dm {
            position: absolute;
            top: 1.2rem;
            left: 50%;
            transform: translateX(-50%);
            padding: 0 1.2rem;
            z-index: 3;
            display: flex;
            column-gap: 0.6rem;
            row-gap: 0.4rem;
            flex-wrap: wrap;
            width: 100%;
            align-items: center;
            @media screen and (max-width: 1200px) {
                top: 0.6rem;
                // width: 96%;
                padding: 0 0.6rem;
            }
        }

        &--wrap {
            // overflow: hidden;

            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            transition: 0.15s cubic-bezier(0.39, 0.575, 0.565, 1);
            position: relative;

            @include hover {
                &::before {
                    opacity: 1;
                }
            }

            @keyframes likeHover {
                0% {
                    transform: translate(-50%, -50%) scale(1);
                }
                100% {
                    transform: translate(-50%, -50%) scale(1.1);
                }
            }

            @include hover {
                // transform: scale(1.03);

                .like_item--img {
                    &::after {
                        opacity: 1;
                    }

                    &::before {
                        opacity: 1;
                        transform: translate(-50%, -50%) scale(1);
                    }

                    img {
                        transform: translate(-50%, -50%) scale(1.1);
                    }
                }
            }
        }

        &--des {
            display: flex;
            flex-direction: column;
            row-gap: 0.8rem;

            @media screen and (max-width: 1200px) {
                row-gap: 0.6rem;
            }

            @media screen and (max-width: 800px) {
                row-gap: 0.4rem;
            }
            @media screen and (max-width: 600px) {
                row-gap: 0.2rem;
            }

            .note-text {
                &.title {
                    position: relative;
                    a {
                        display: block;
                        position: relative;
                        display: -webkit-box;
                        -webkit-line-clamp: 2;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    }
                }

                &.content {
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                    display: none;
                }
            }
        }

        &--info {
            display: flex;
            align-items: center;
            justify-content: space-between;
            column-gap: 1.2rem;

            @media screen and (max-width: 1200px) {
                column-gap: 1rem;
            }

            @media screen and (max-width: 800px) {
                column-gap: 0.8rem;
            }

            @media screen and (max-width: 600px) {
                column-gap: 0.6rem;
            }

            @media screen and (max-width: 460px) {
                column-gap: 0.4rem;
            }

            .box-info-author {
                .note-mn {
                    font-weight: 400 !important;
                }

                .note-sm {
                    font-weight: 400 !important;
                }
            }
        }

        &--top {
            position: relative;

            .box-dm {
                @media screen and (max-width: 1200px) {
                    top: 0.5rem;
                    left: 0.5rem;
                }
            }
        }

        &--bottom {
            display: flex;
            flex-direction: column;
            row-gap: 0.4rem;
            // position: relative;
            // @media screen and (max-width: 1200px) {
            //     row-gap: 0.8rem;
            // }

            // @media screen and (max-width: 800px) {
            //     row-gap: 0.6rem;
            // }

            // @media screen and (max-width: 600px) {
            //     row-gap: 0.4rem;
            // }
        }

        &--img {
            padding-top: calc(100% * 164 / 280);
            width: 100%;
            position: relative;
            overflow: hidden;
            border-radius: var(--radius2);
            display: block;
            height: 100%;

            @media screen and (max-width: 400px) {
                padding-top: calc(100% * 200 / 280);
            }

            @media screen and (max-width: 320px) {
                padding-top: calc(100% * 140 / 280);
            }

            &::after {
                content: "";
                position: absolute;
                inset: 0;
                width: 100%;
                height: 100%;
                background: rgba($color: #000000, $alpha: 0.3);
                opacity: 0;
                transition: all 0.3s linear;
                z-index: 1;
            }

            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0);
                border-radius: calc(var(--radius) / 2);
                width: 92%;
                height: 85%;
                border: 0.1rem solid $color-white;
                opacity: 0;
                transition: all 0.3s linear;
                z-index: 2;
                display: none;
            }

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
                transition: all 0.5s ease;
            }
        }

        @media screen and (max-width: 800px) {
            width: calc(100% / 3);
        }

        @media screen and (max-width: 600px) {
            width: calc(100% / 2);
        }
    }

    &_content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent6);

        .page-numbers {
            justify-content: center;
        }
    }
}

