            row-gap: var(--rowContent1);

            .wpcf7-response-output {
                bottom: -3rem !important;
                font-size: 1rem !important;
            }
            form {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent1);
            }

            .box-sw {
                &-wrap {
                }

                &-lf {
                    order: 3;
                }

                &-ct {
                    .note-sm {
                        color: $color-text !important;
                    }
                }

                &-rt {
                    display: block !important;
                }
            }
        }
    }

    &_rt {
        width: 100%;
        flex: 1;

        &--wrap {
        }

        &--img {
            padding-top: calc(100% * 596 / 486);
            width: 100%;
            position: relative;
            display: block;
            overflow: hidden;
            border-radius: var(--radius);

            img {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

.des {
    &_wrap {
        @media screen and (max-width: 460px) {
            flex-direction: column;
            row-gap: var(--rowContent1);
        }
    }

    &_item {
        @media screen and (max-width: 460px) {
            width: 100%;
        }
    }
}
