.breadcrumbs {
    overflow: hidden;
    width: 100%;
    &-wrap {
        padding-top: var(--size-pd);
        // padding-bottom: var(--size-pd);
    }

    &-list {
        @include flex;
        align-items: center;
        column-gap: 2.8rem;
        // padding: 2.4rem 0;

        @media screen and (max-width: 1200px) {
            column-gap: 2.4rem;
        }

        @media screen and (max-width: 600px) {
            column-gap: 1.4rem;
        }

        // @media screen and (max-width: 600px) {
        //     column-gap: 1.6rem;
        // }
    }

    &-item {
        position: relative;
        // padding: 0 1rem;
        height: 2.4rem;
        @include mid-flex;

        &.current {
            a {
                color: $color-pri !important;
                pointer-events: none;

                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;

                display: -webkit-box;
            }
        }

        &:last-child {
            padding: 0 0.05rem;
        }

        &:not(:last-child) {
            &::before {
                content: "";
                background-image: url(../assets/images/icBrcum.svg);
                top: 50%;
                transform: translateY(-50%);
                position: absolute;
                background-repeat: no-repeat;
                background-size: 100% 100%;
                right: -2.1rem;
                width: 1.2rem;
                height: 1.2rem;

                @media screen and (max-width: 1200px) {
                    right: -2rem;
                }

                @media screen and (max-width: 600px) {
                    top: 50%;
                    right: -1.3rem;
                    width: 1.2rem;
                    height: 1.2rem;
                }
            }

            // @media screen and (max-width: 600px) {
            //     right: -0.75rem;
            // }
        }

        a {
            @include fz-16;
            line-height: 1.5;
            color: $color-text2;

            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            letter-spacing: 0.005em;

            // max-width: 9rem;

            img {
                width: 1.4rem;
            }

            @include min(1201px) {
                &:hover {
                    color: $color-pri !important;
                }
            }

            @include max(600px) {
                font-size: 1.2rem;
            }
        }

        &.active {
            a {
                opacity: 1;

                @include min(1201px) {
                    &:hover {
                        color: $color-black;
                    }
                }
            }
        }

        &:not(:last-child) {
            a {
                width: max-content;
            }
        }
    }

    &.pd-t {
        padding-top: 1.5rem;
    }

    &.other {
        // padding: 0.5rem 0;
        // background-color: $color-pri;
        width: 100%;
        // height: 100%;
        a {
            font-weight: 400;
            font-size: 1.6rem;
            line-height: 1.5;

            color: $color-white;
        }

        span {
            color: white;
        }

        .breadcrumbs-list {
            position: relative;
            height: 100%;
            padding: 0;

            // padding-bottom: 1.6rem;

            // &::before {
            //     position: absolute;
            //     content: "";
            //     width: 100vw;
            //     bottom: 0;
            //     left: 50%;
            //     transform: translateX(-50%);
            //     border-bottom: 0.1rem solid #d1d8dc;
            // }
        }

        .breadcrumbs-item {
            a {
                color: $color-text2;
            }

            &:not(:last-child) {
               
            }

            &.current {
                a {
                    color: $color-pri !important;
                    pointer-events: none;
                    font-weight: 600;

                    display: -webkit-box;
                    -webkit-line-clamp: 1;
                    -webkit-box-orient: vertical;
                    overflow: hidden;
                }
            }
        }
    }

    &Sec.section {
        padding-top: var(--pdGen);
        overflow: hidden;
    }
}

.box-breadcrumbs {
    background: $color-pri;
    height: 12.5rem;
    display: flex;
    width: 100%;
    // align-items: center;
    justify-content: center;
    flex-direction: column;

    &-wrap {
        height: 100%;
        position: relative;
        display: flex;
        flex-direction: column;
        row-gap: 0.8rem;
        justify-content: center;

        @media screen and (max-width: 1200px) {
            row-gap: 0.2rem;
        }

        &::before {
            content: "";
            background-image: url(../assets/images/icBr.svg);
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            left: -2rem;
            width: 10rem;
            height: 10rem;
            background-repeat: no-repeat;
            background-size: 100%;
            z-index: 1;
            pointer-events: none;
            @media screen and (max-width: 1200px) {
                width: 6rem;
                height: 6rem;
                left: -1rem;
            }
        }

        .title-sm {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            @media screen and (max-width: 600px) {
                font-size: 2rem;
            }
        }
    }

    @media screen and (max-width: 1200px) {
        height: 8rem;
    }
}

.breadcrumbs-inside {
    position: relative;

    .breadcrumbs {
        position: absolute;
        width: 100%;
        top: 0;
        left: 0;
    }

    .breadcrumbs.other .breadcrumbs-list::before {
        display: none;
    }
}

.breadcrumbs-white {
    .breadcrumbs.other a {
        color: white;

        @include hover {
            color: $color-third;
        }
    }

    .breadcrumbs-item:not(:last-child)::before {
        filter: brightness(0) invert(1);
    }
}
