.popup_main {
    position: fixed;
    top: 50%;
    left: 50%;
    max-width: 98rem;
    width: 100%;
    // max-height: 90vh;
    // overflow-y: auto;
    transform: translate(-50%, -50%);
    background: $color-white;
    z-index: 3000;
    padding: 6rem;
    border-radius: var(--radius);
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.5s linear;

    &.cus {
        padding: 4rem;
        max-width: 78rem;

        @media screen and (max-width: 1200px) {
            max-width: 90%;
            padding: 4rem;
        }

        @media screen and (max-width: 800px) {
            max-width: 90%;
            padding: 3rem;
        }

        @media screen and (max-width: 600px) {
            max-width: 90%;
            padding: 4rem 2rem;
        }

        @media screen and (max-width: 460px) {
            max-width: 95%;
            padding: 3rem 1.2rem;
        }
        .popup_main--exits {
            top: 0;
            right: 0;
        }

        .btn {
            margin: 0 auto;
            box-shadow: none !important;
        }
    }

    &.cus2 {
        padding: 4rem;
        max-width: 78rem;

        @media screen and (max-width: 1200px) {
            max-width: 60%;
            padding: 4rem;
        }

        @media screen and (max-width: 1024px) {
            max-width: 70%;
            padding: 3.2rem;
        }

        @media screen and (max-width: 800px) {
            max-width: 75%;
            padding: 3rem;
        }

        @media screen and (max-width: 600px) {
            max-width: 90%;
            padding: 4rem 2rem;
        }

        @media screen and (max-width: 460px) {
            max-width: 95%;
            padding: 3rem 1.2rem;
        }
        .popup_main--exits {
            top: 0;
            right: 0;
        }

        .btn {
            margin: 0 auto;
            box-shadow: none !important;
        }
        .popup_main--form {
            .contact_rt--item-item {
                width: 50% !important;

                @media screen and (max-width: 460px) {
                    width: 100% !important;

                    &:last-child{
                        width: 50% !important;
                    }


                    &:nth-child(5){
                        width: 50% !important;

                    }
                }
            }
        }

        .wpcf7-response-output{
            bottom: -4rem !important;
            left: 50%;
            transform: translateX(-50%);
            text-align: center;
            position: absolute;
        }
    }

    &.showed {
        opacity: 1;
        visibility: visible;
        pointer-events: visible;
        transform: translate(-50%, -50%) scale(1);

        & ~ .popup_main--modal {
            opacity: 1;
            visibility: visible;
            pointer-events: visible;
        }
    }

    &--content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);
    }

    &--form {
        position: relative;
        .form-ip-ip textarea {
            min-height: 12rem;
        }

        .form-upload-wrap {
            padding: 2rem 2rem;
        }

        .form-upload-img {
            width: 10rem;
            height: 10rem;
        }

        .contact_rt--item-wrap {
            row-gap: var(--rowContent2);
        }
    }

    &--title {
        display: flex;
        flex-direction: column;
        row-gap: 0.4rem;
        .title-lg {
            strong {
                color: $color-yellow;
            }
        }

        .note-text {
            strong {
                color: $color-yellow;
                font-weight: 700;
            }
        }
    }

    &--packet {
        .packet_wrap {
            width: 100%;
            max-width: none;
        }
    }

    &--wrap {
        position: relative;
        max-height: 80vh;
        overflow-y: auto;
        overflow-x: hidden;
        height: 100%;
        padding-right: 0.1rem;
        &::-webkit-scrollbar {
            width: 0.15rem;
        }

        form {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
        }

        .wpcf7-not-valid-tip {
            font-size: 1rem !important;
            left: 1.6rem;
            bottom: 0 !important;
            @media screen and (max-width: 1200px) {
                font-size: 0.8rem !important;
                left: 1.2rem;
            }
        }

        .wpcf7-response-output {
            bottom: 0 !important;
        }
    }

    &--exits {
        width: 4rem;
        height: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        // background: $color-pri;

        position: absolute;
        top: -0.3rem;
        right: -0.5rem;
        cursor: pointer;
        transition: all 0.3s linear;
        @include hover {
            i {
                color: $color-pri;
            }
        }
        i {
            color: $color-black;
            font-size: 2.4rem;

            @media screen and (max-width: 600px) {
                font-size: 2rem;
            }
        }

        @media screen and (max-width: 600px) {
            width: 3rem;
            height: 3rem;
        }
    }

    @media screen and (max-width: 1200px) {
        max-width: 90%;
        padding: 4rem;
    }

    @media screen and (max-width: 800px) {
        max-width: 90%;
        padding: 3rem;
    }

    @media screen and (max-width: 600px) {
        max-width: 90%;
        padding: 4rem 2rem;
    }

    @media screen and (max-width: 460px) {
        max-width: 95%;
        padding: 4rem 1.2rem;
    }
}

.popup_main--modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2999;
    background: rgba($color: #000000, $alpha: 0.5);
    backdrop-filter: blur(1rem);

    opacity: 0;
    visibility: hidden;
    pointer-events: none;

    transition: all 0.5s linear;
}

.contact_map {
    padding-top: var(--size-pdl);

    position: relative;
    overflow: hidden;

    &--iframe {
        padding-top: calc(100% * 600 / 1728);
        width: 100%;
        position: relative;
        overflow: hidden;
        // border: 0.5rem solid $color-pri;

        @media screen and (max-width: 460px) {
            padding-top: 100%;
        }

        iframe {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.form-ip {
    display: flex;
    flex-direction: column;
    row-gap: 0.8rem;

    @media screen and (max-width: 1200px) {
        row-gap: 0.6rem;
    }

    @media screen and (max-width: 600px) {
        row-gap: 0.4rem;
    }

    .wpcf7-form-control-wrap{
        width: 100%;
        height: 100%;
        display: block;
    }

    &.package-ip {
        pointer-events: none;
    }

    &-label {
        label {
            font-weight: 600;
            color: $color-black-2;
            @include fz-16;
            strong {
                color: #cf0011;
            }
        }
    }

    &-ip {
        position: relative;

        .wpcf7-form-control-wrap {
            width: 100%;
            height: 100%;
            display: block;
        }

        .wpcf7-not-valid-tip {
            font-size: 1rem !important;
            left: 1.6rem;
            bottom: 0 !important;
            @media screen and (max-width: 1200px) {
                font-size: 0.8rem !important;
                left: 1.2rem;
            }
        }

        input {
            width: 100%;
            height: 4.8rem;
            padding: 1.6rem;
            border-radius: var(--radius);
            border: 0.1rem solid $color-gray4;
            background: $color-white;
            color: $color-black;
            transition: all 0.3s linear;
            @include fz-14;
            @media screen and (max-width: 1200px) {
                height: 4.2rem;
                padding: 1rem 1.2rem;
            }

            @media screen and (max-width: 800px) {
                height: 4rem;
                padding: 1rem 1rem;
            }

            @media screen and (max-width: 600px) {
                height: 3.6rem;
                padding: 1rem 1rem;
                @include fz-12;
            }
            @media screen and (max-width: 460px) {
                @include fz-12;
            }
            &::placeholder {
                @include fz-14;
                color: #a4a1ab;

                @media screen and (max-width: 600px) {
                    @include fz-12;
                }

                @media screen and (max-width: 460px) {
                    @include fz-12;
                }
            }

            &:focus {
                border-color: $color-pri;
            }
        }

        &.choose-date {
            cursor: pointer;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                width: 2rem;
                height: 2rem;
                background-image: url(../assets/images/calendar.svg);
                background-repeat: no-repeat;
                right: 1.6rem;
                pointer-events: none;
                background-size: 100% 100%;
                @media screen and (max-width: 1200px) {
                    right: 1.2rem;
                    width: 1.8rem;
                    height: 1.8rem;
                }

                @media screen and (max-width: 800px) {
                    right: 1rem;
                    width: 1.6rem;
                    height: 1.6rem;
                }

                @media screen and (max-width: 600px) {
                    right: 1rem;
                    width: 1.4rem;
                    height: 1.4rem;
                }
            }
        }

        textarea {
            width: 100%;
            min-height: 16rem;
            padding: 1rem 1.6rem;
            border-radius: var(--radius);
            border: 0.1rem solid $color-gray4;
            background: $color-white;
            transition: all 0.3s linear;
            color: $color-black;

            @include fz-14;
            &::placeholder {
                @include fz-14;
                color: #a4a1ab;

                @media screen and (max-width: 600px) {
                    @include fz-12;
                }

                @media screen and (max-width: 460px) {
                    @include fz-12;
                }
            }

            &:focus {
                border-color: $color-pri;
            }
            @media screen and (max-width: 1200px) {
                min-height: 16rem;
            }

            @media screen and (max-width: 800px) {
                min-height: 14rem;
            }

            @media screen and (max-width: 600px) {
                min-height: 12rem;
                @include fz-12;
                padding: 1rem;
            }
        }

        &.pass {
            input {
                padding-right: 4.4rem;
            }

            &.actived {
                .hidden-pass {
                    display: none;
                }
                .show-pass {
                    display: block;
                }
            }
        }
    }

    &-upload {
        display: flex;
        cursor: pointer;
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        row-gap: var(--rowContent0);
        width: 100%;
        padding: 1.6rem;
        border-radius: var(--radius);
        border: 0.1rem solid $color-gray4;
        height: 100%;
        &-img {
            padding-top: calc(100% * 400 / 600);
            width: 100%;
            position: relative;
            overflow: hidden;
            display: block;
            max-width: 95%;

            .wpcf7-not-valid-tip{
                left: 50% !important;
                transform: translateX(-50%);
                width: 100%;
                text-align: center;
            }

            @media screen and (max-width: 600px) {
                max-width: 100%;
            }

            input {
                display: none;
            }

            img {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                object-fit: cover;
            }
        }

        &-lb {
            color: #a4a1ab;
            width: 100%;
            overflow: hidden;
            text-align: center;
        }

        @media screen and (max-width: 1200px) {
           
            padding: 1rem 1.2rem;
        }

        @media screen and (max-width: 800px) {
          
            padding: 1rem 1rem;
        }

        @media screen and (max-width: 600px) {
           
            padding: 1rem 1rem;
            @include fz-12;
        }
    }

    &-ic {
        display: flex;
        cursor: pointer;
        align-items: center;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 1.6rem;

        @media screen and (max-width: 1200px) {
            right: 1.2rem;
        }

        @media screen and (max-width: 800px) {
            right: 1rem;
        }

        @include hover {
            i {
                color: $color-pri;
            }
        }

        i {
            @include fz-16;
            font-weight: 300 !important;
            color: #b1b1b1;
            transition: all 0.3s linear;

            @media screen and (max-width: 460px) {
                @include fz-12;
            }
        }

        @keyframes anHienPass {
            0% {
                opacity: 0;
            }

            50% {
                opacity: 0.5;
            }

            100% {
                opacity: 1;
            }
        }

        &.hidden-pass {
            animation: anHienPass 0.5s ease forwards;
        }

        &.show-pass {
            display: none;

            animation: anHienPass 0.5s ease forwards;
        }
    }

    &-op {
        .select2-container--default .select2-selection--single {
            border-radius: var(--radius) !important;
        }
        .wpcf7-form-control-wrap {
            width: 100%;
            height: 100%;
            display: block;
        }

        .wpcf7-not-valid-tip {
            font-size: 1rem !important;
            left: 1.6rem;
            bottom: 0 !important;
            @media screen and (max-width: 1200px) {
                font-size: 0.8rem !important;
                left: 1.2rem;
            }
        }
    }
}

.contact_form {
    width: 100%;
    display: flex;
    flex-direction: column;
    row-gap: 3.2rem;

    @media screen and (max-width: 1200px) {
        row-gap: 2.4rem;
    }

    @media screen and (max-width: 800px) {
        row-gap: 2rem;
    }

    @media screen and (max-width: 600px) {
        row-gap: 1.6rem;
    }

    &.product {
        .contact_form-item {
            &:nth-child(5) {
                width: 100%;
            }

            &:last-child {
                .form-ip-ip {
                    textarea {
                        min-height: 10rem;
                    }
                }
            }
        }
    }

    &-list {
        row-gap: 1.6rem;

        @media screen and (max-width: 1200px) {
            row-gap: 1.4rem;
        }

        @media screen and (max-width: 600px) {
            row-gap: 1.2rem;
        }
    }

    &-item {
        &:last-child {
            width: 100%;
        }

        @media screen and (max-width: 460px) {
            width: 100%;
        }

        .form-ip-label {
            label {
                color: $color-white;
            }
        }

        .form-ip-ip {
            input {
                background: rgba(255, 255, 255, 0.1);
                border-color: #636ed6;
                color: $color-white;
                &::placeholder {
                    color: $color-gray4;
                }

                &:focus {
                    border-color: #b1b7eb;
                }
            }

            textarea {
                background: rgba(255, 255, 255, 0.1);
                border-color: #636ed6;
                color: $color-white;
                &::placeholder {
                    color: $color-gray4;
                }

                &:focus {
                    border-color: #b1b7eb;
                }
            }
        }
    }
}

.gender {
    &-list {
        display: flex;
        align-items: center;
        column-gap: 2.4rem;
    }

    &-item {
        cursor: pointer;
        input {
            display: none;

            &:checked {
                & ~ .box {
                    background: $color-pri;
                    border-color: $color-pri;
                    &::before {
                        opacity: 1;
                    }
                }
            }
        }
        display: flex;
        align-items: center;
        column-gap: 0.8rem;

        @media screen and (max-width: 1200px) {
            column-gap: 0.6rem;
        }

        @media screen and (max-width: 600px) {
            column-gap: 0.4rem;
        }

        p.note-text {
            color: $color-black-2;
        }

        .box {
            width: 2rem;
            height: 2rem;
            border-radius: 50%;
            overflow: hidden;
            flex-shrink: 0;
            border: 0.1rem solid #9b9b9b;
            position: relative;
            transition: all 0.3s linear;
            &::before {
                content: "";
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: calc(100% - 0.6rem);
                height: calc(100% - 0.6rem);
                background: $color-white;
                border-radius: 50%;
                background-size: 100% 100%;
                background-repeat: no-repeat;
                opacity: 0;
                transition: all 0.3s linear;
            }

            @media screen and (max-width: 1200px) {
                width: 1.8rem;
                height: 1.8rem;
            }

            @media screen and (max-width: 800px) {
                transform: translateY(-0.2rem);
            }

            @media screen and (max-width: 600px) {
                width: 1.4rem;
                height: 1.4rem;
            }
        }
    }
}

.form-upload {
    cursor: pointer;

    &.actived {
        .form-upload-wrap {
            border-color: $color-pri;
        }
    }
    &-wrap {
        padding: 5rem;
        border: 0.2rem dashed $color-gray4;
        border-radius: var(--radius);
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);
        transition: 0.3s linear;

        .wpcf7-form-control-wrap {
            display: block;
            width: 100%;
            height: 100%;
        }

        .wpcf7-not-valid-tip {
            bottom: 0 !important;
            left: 50%;
            transform: translateX(-50%);
            font-size: 0.8rem !important;
        }

        input {
            display: none;
        }

        @media screen and (max-width: 1200px) {
            padding: 3rem;
        }

        @media screen and (max-width: 800px) {
            padding: 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.2rem;
        }
    }

    &-title {
        color: $color-pri;
        text-align: center;

        .note-text {
            text-align: center;
            text-transform: uppercase;
        }
    }

    &-content {
        .note-sm {
            color: $color-pri;
            text-align: center;

            strong {
                color: $color-gray3;
            }
        }
    }

    &-img {
        width: 16rem;
        height: 16rem;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;

        @media screen and (max-width: 1200px) {
            width: 12rem;
            height: 12rem;
        }

        @media screen and (max-width: 800px) {
            width: 10rem;
            height: 10rem;
        }

        @media screen and (max-width: 600px) {
            width: 8rem;
            height: 8rem;
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}

.contact {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        @media screen and (max-width: 700px) {
            flex-direction: column;
            row-gap: 1.6rem;
        }
    }

    &_lf {
        width: calc(100% * 5 / 12);

        &--title {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &--wrap {
            padding: 4rem;
            border-radius: var(--radius);
            position: relative;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
            height: 100%;
            min-height: 78rem;
            // &::before {
            //     content: "";
            //     background-image: url(../assets/images/bgct.png);
            //     width: 100%;
            //     position: absolute;
            //     inset: 0;
            //     height: 100%;
            //     z-index: -1;
            // }

            @media screen and (max-width: 700px) {
                min-height: unset;
            }

            .footer-mxh-list {
                column-gap: var(--columnContent1);
            }

            .footer-mxh-link {
                width: 4rem;
                height: 4rem;

                @media screen and (max-width: 1200px) {
                    width: 3.2rem;
                    height: 3.2rem;
                }

                @media screen and (max-width: 600px) {
                    width: 2.6rem;
                    height: 2.6rem;
                }
            }

            @media screen and (max-width: 1200px) {
                padding: 2.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 2rem;
            }

            @media screen and (max-width: 700px) {
                padding: 1.8rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.6rem;
            }

            @media screen and (max-width: 460px) {
                padding: 1.4rem;
            }
        }

        &--bg {
            width: 100%;
            position: absolute;
            inset: 0;
            height: 100%;
            z-index: -1;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                @media screen and (max-width: 700px) {
                    display: none;
                }
            }

            @media screen and (max-width: 700px) {
                background: $color-pri;
            }
        }

        &--list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &--item {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);

            .footer-mxh {
                width: 100%;
                margin: unset;
            }
        }

        @media screen and (max-width: 700px) {
            width: 100%;
        }
    }

    &_rt {
        width: 100%;
        flex: 1;

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent3);
        }

        &--tabs {
            display: flex;
            column-gap: 4rem;

            @media screen and (max-width: 1200px) {
                column-gap: 3rem;
            }

            @media screen and (max-width: 800px) {
                column-gap: 2rem;
            }

            @media screen and (max-width: 600px) {
                column-gap: 1rem;
            }

            @media screen and (max-width: 360px) {
                width: 100%;
                flex-direction: column;
                row-gap: 0.6rem;
            }
        }

        &--tab {
            font-weight: 700;
            @include fz-20;
            color: $color-text;
            transition: 0.3s linear;
            position: relative;
            cursor: pointer;
            width: fit-content;

            @media screen and (max-width: 460px) {
                font-size: 1.6rem;
            }

            @include hover {
                color: $color-text2;

                &::before {
                    background: $color-text2;
                    width: 100%;
                    left: 0;
                }
            }

            &::before {
                content: "";
                width: 0%;
                background: $color-text;
                height: 0.2rem;
                right: 0;
                transition: 0.3s linear;
                bottom: -0.25rem;
                position: absolute;
            }

            &.actived {
                color: $color-text2;

                &::before {
                    width: 100%;
                    background: $color-text2;
                }
            }
        }

        &--list {
        }

        &--item {
            display: none;

            @keyframes anHienCt {
                0% {
                    transform: translateY(10%);
                    opacity: 0;
                }
                100% {
                    transform: translateY(0%);
                    opacity: 1;
                }
            }

            &.showed {
                display: block;
                animation: anHienCt 0.8s linear forwards;
            }

            &-wrap {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent);

                .btn {
                    @media screen and (max-width: 460px) {
                        margin: 0 auto;
                    }
                }
            }

            &-list {
                row-gap: var(--rowContent1);
            }
            &-item {
                &:nth-child(5) {
                    width: 100% !important;
                }

                &:last-child {
                    width: 100% !important;
                }

                @media screen and (max-width: 460px) {
                    width: 100% !important;
                }
            }
        }
    }
}
