.author_nb {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);
    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);

        .btn {
            margin: 0 auto;
        }
    }

    &--list {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--item {
        padding: 5rem;
        border-radius: var(--radius);
        border: 0.1rem solid $color-gray6;
        transition: 0.3s linear;
        background: $color-white;
        overflow: hidden;
        @include hover {
            border-color: $color-pri;
        }

        &.hiddenInfo {
            .author_nb--info-list {
                display: none;
            }
        }

        &.top {
            .box-dm {
                display: flex;
            }
        }

        &-wrap {
            display: flex;
            align-items: center;
            justify-content: space-between;
            column-gap: var(--columnContent1);

            @media screen and (max-width: 800px) {
                flex-direction: column;
                row-gap: var(--rowContent1);
            }

            @media screen and (max-width: 600px) {
                flex-direction: column;
                row-gap: var(--rowContent2);
            }
        }

        @media screen and (max-width: 1200px) {
            padding: 3rem 2rem;
        }

        @media screen and (max-width: 800px) {
            padding: 2.4rem 1.8rem;
        }

        @media screen and (max-width: 600px) {
            padding: 2rem 1.6rem;
        }
    }

    &--lf {
        width: 70%;

        @media screen and (max-width: 800px) {
            width: 100%;
        }

        &-wrap {
            display: flex;
            align-items: center;
            column-gap: var(--columnContent2);

            .admin_info--ava {
                width: 20rem;
                height: 20rem;

                @media screen and (max-width: 800px) {
                    width: 18rem;
                    height: 18rem;
                }

                @media screen and (max-width: 600px) {
                    width: 16rem;
                    height: 16rem;
                }

                &::before {
                    display: none;
                }

                &.border {
                    &::before {
                        display: none;
                    }

                    .admin_info--ava-img {
                        width: calc(100% - 6rem);
                        height: calc(100% - 6rem);
                    }
                }
            }

            @media screen and (max-width: 600px) {
                flex-direction: column;
                row-gap: var(--rowContent1);
                align-items: center;
            }
        }
    }

    &--rt {
        width: 100%;
        flex: 1;
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: 1.6rem;

            @media screen and (max-width: 800px) {
                flex-direction: row-reverse;
                column-gap: var(--columnContent1);
                justify-content: space-between;
                align-items: center;
            }

            @media screen and (max-width: 600px) {
                flex-direction: column;
            }

            .note-sm {
                color: $color-four;
                position: relative;
                padding-left: 3rem;
                transition: none;
                @include hover {
                    color: $color-second;

                    &::before {
                        filter: brightness(0) saturate(100%) invert(19%) sepia(76%) saturate(5212%) hue-rotate(311deg)
                            brightness(72%) contrast(113%);
                    }
                }

                @media screen and (max-width: 600px) {
                    padding-left: 2.4rem;
                }

                &::before {
                    content: "";
                    width: 2.4rem;
                    height: 2.4rem;
                    position: absolute;
                    background-image: url(../assets/images/web.svg);
                    background-size: 100% 100%;
                    top: -0.15rem;
                    left: 0;
                    transition: none;

                    @media screen and (max-width: 600px) {
                        width: 2rem;
                        height: 2rem;
                    }
                }
            }

            .footer-mxh-link {
                width: 4rem;
                height: 4rem;
                @include fz-40;
                @media screen and (max-width: 1200px) {
                    width: 3.2rem;
                    height: 3.2rem;

                @include fz-30;

                }

               
            }

            .footer-mxh-list {
                column-gap: var(--columnContent1);
                flex-wrap: wrap;
                justify-content: start;
                row-gap: var(--rowContent0);

                @media screen and (max-width:600px) {
                justify-content: center;
                    
                }
            }
        }
    }

    &--info {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);

        @media screen and (max-width: 1200px) {
            row-gap: var(--rowContent1);
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &-top {
            display: flex;
            flex-direction: column;
            row-gap: 1rem;

            .admin_info--if {
                &-name {
                    flex-wrap: wrap;
                    row-gap: var(--rowContent0);
                    @media screen and (max-width: 600px) {
                        justify-content: center;
                    }
                    .note-text {
                        @include fz-32;

                        @media screen and (max-width: 600px) {
                            text-align: center;
                        }
                    }

                    .ic {
                        width: 2.4rem;
                        height: 2.4rem;

                        @media screen and (max-width: 600px) {
                            width: 2rem;
                            height: 2rem;
                            top: 0;
                        }
                    }
                }

                @media screen and (max-width: 600px) {
                    align-items: center;
                }

                .note-sm {
                    @media screen and (max-width: 600px) {
                        text-align: center;
                    }
                }
            }

            .box-dm {
                display: none;

                .box-dm-ic {
                    display: flex;
                    position: relative;
                    top: 0.1rem;
                }
            }
        }

        &-bottom {
        }

        &-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &-item {
            width: 100%;
            display: flex;
            align-items: center;
            // justify-content: space-between;
            column-gap: var(--columnContent0);

            .note-sm {
                width: 100%;
                strong {
                    font-weight: 500;
                    color: $color-black-2;
                }

                @media screen and (max-width: 600px) {
                    text-align: center;
                }
            }
        }
    }
}

.author_info {
    &--list {
        display: flex;
        align-items: center;
        column-gap: var(--columnContent1);
        flex-wrap: wrap;
        row-gap: var(--rowContent1);

        @media screen and (max-width: 600px) {
            justify-content: center;
        }
    }

    &--item {
        position: relative;
        padding-left: 3rem;

        @media screen and (max-width: 600px) {
            padding-left: 2.4rem;
        }

        .note-sm {
            color: $color-text;
            white-space: nowrap;
            strong {
                color: $color-black;
                font-weight: 700;
                @include fz-16;
            }
        }

        &.ps {
            &::before {
                content: "";
                width: 2.4rem;
                height: 2.4rem;
                position: absolute;
                top: -0.1rem;
                left: 0;
                background-image: url(../assets/images/ps.svg);
                background-repeat: no-repeat;
                background-size: 100% 100%;

                @media screen and (max-width: 600px) {
                    width: 2rem;
                    height: 2rem;
                }
            }
        }

        &.lk {
            &::before {
                content: "";
                width: 2.4rem;
                height: 2.4rem;
                position: absolute;
                top: -0.1rem;
                left: 0;
                background-image: url(../assets/images/lk.svg);
                background-repeat: no-repeat;
                background-size: 100% 100%;

                @media screen and (max-width: 600px) {
                    width: 2rem;
                    height: 2rem;
                }
            }
        }

        &.nx {
            padding-top: 0 !important;
            padding-bottom: 0 !important;

            &::before {
                content: "";
                width: 2.4rem;
                height: 2.4rem;
                position: absolute;
                top: -0.1rem;
                left: 0;
                background-image: url(../assets/images/nx.svg);
                background-repeat: no-repeat;
                background-size: 100% 100%;

                @media screen and (max-width: 600px) {
                    width: 2rem;
                    height: 2rem;
                }
            }
        }
    }
}

.profile {
    padding-bottom: var(--size-pdl);
    margin-top: -15rem;
    position: relative;
    z-index: 10;

    @media screen and (max-width: 1200px) {
        margin-top: -15rem;
    }

    @media screen and (max-width: 600px) {
        margin-top: var(--size-pd);
    }

    &_content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &_intro {
        padding: 4rem;
        background: $color-white;
        border-radius: var(--radius);
        border: 0.1rem solid $color-gray4;

        @media screen and (max-width: 1200px) {
            padding: 3rem;
        }

        @media screen and (max-width: 800px) {
            padding: 2rem;
        }

        @media screen and (max-width: 600px) {
            padding: 1.2rem;
        }
    }

    &_wrap {
        @media screen and (max-width: 1024px) {
            flex-direction: column;
            row-gap: var(--rowContent2);
        }
    }

    &_lf {
        width: 100%;
        flex: 1;

        &--wrap {
            position: sticky;
            top: 16rem;
        }
    }

    &_rt {
        width: 50%;

        @media screen and (max-width: 1024px) {
            width: 100%;
        }

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);

            // .is-loading-btn.loading {
            //     width: fit-content !important;
            // }

            .monaLoadMoreJS{
                margin:0 auto;
            }
        }

        &--tabs {
            padding: 1.6rem;
            border-radius: var(--radius);
            display: flex;
            align-items: center;
            column-gap: var(--columnContent);
            border: 0.1rem solid $color-gray4;
            width: fit-content;

            @media screen and (max-width: 1200px) {
                padding: 1.4rem;
            }

            @media screen and (max-width: 460px) {
                width: 100%;
                flex-direction: column;
                row-gap: var(--rowContent1);
                align-items: start;
            }
        }

        &--tab {
            cursor: pointer;
            position: relative;

            &::before {
                content: "";
                width: 0%;
                height: 0.1rem;
                background: $color-pri;
                position: absolute;
                bottom: -1.6rem;
                left: 0;
                transition: 0.5s ease;

                @media screen and (max-width: 1200px) {
                    bottom: -1.4rem;
                }

                @media screen and (max-width: 460px) {
                    display: none;
                }
            }

            .note-text {
                transition: 0.5s ease;
                font-weight: 600;
            }

            @include hover {
                .note-text {
                    color: $color-pri;
                }

                &::before {
                    width: 50%;
                }
            }

            &.actived {
                .note-text {
                    color: $color-pri;
                    font-weight: 600;
                }

                &::before {
                    width: 50%;
                }
            }
        }

        &--list {
        }

        &--item {
            display: none;

            &-wrap {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);

                .like_item {
                    width: 50%;

                    @media screen and (max-width: 1024px) {
                        width: calc(100% / 3);
                    }

                    @media screen and (max-width: 600px) {
                        width: calc(100% / 2);
                    }
                }

                .comment_cmt {
                    .btn {
                        @media screen and (max-width: 460px) {
                            margin: 0 auto;
                        }
                    }
                }

                .btn {
                    @media screen and (max-width: 460px) {
                        margin: 0 auto;
                    }
                }
            }

            @keyframes anHien {
                0% {
                    transform: translateY(10%);
                    opacity: 0;
                }
                50% {
                    transform: translateY(5%);
                }
                100% {
                    transform: translateY(0%);
                    opacity: 1;
                }
            }

            &.showed {
                display: block;
                animation: anHien 0.5s linear;
            }
        }
    }
}
