.news_ct {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
    position: relative;

    .container {
        max-width: 168rem;
        padding: 0 1.6rem;
    }

    &--box {
        @media screen and (max-width: 800px) {
            flex-direction: column;
            row-gap: var(--rowContent2);
        }
    }

    &--op {
        display: flex;
        flex-direction: column;

        @media screen and (max-width: 1200px) {
            display: none;
        }

        .control-1-list,
        .control-2-list {
            flex-direction: column;
            row-gap: var(--rowContent0);
        }
    }

    &--wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);
    }

    &--imgs {
        display: flex;
        column-gap: 3.2rem;
    }

    &--img {
        width: 100%;
        height: auto;
        border-radius: var(--radius);
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            display: block;
        }
    }

    &--note {
        padding: 0 1.6rem;
        border-left: 0.3rem solid $color-pri;
        display: flex;
        flex-direction: column;
        row-gap: 0.8rem;

        @media screen and (max-width: 1200px) {
            padding: 0 1.4rem;
            row-gap: 0.6rem;
        }

        @media screen and (max-width: 800px) {
            padding: 0 1.2rem;
            row-gap: 0.4rem;
        }

        @media screen and (max-width: 600px) {
            padding: 0 1rem;
            row-gap: 0.2rem;
        }

        .note-sm {
            color: $color-text3;
        }

        &.cus {
            padding: 1.6rem;
            background: $color-gray5;
            border-radius: var(--radius);
            @media screen and (max-width: 1200px) {
                padding: 1.4rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1rem;
            }

            .note-sm {
                color: $color-text;
            }
        }
    }

    &--content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);

        &.mona-content {
            row-gap: unset;
            p{
                @include fz-18;
                @media screen and (max-width: 600px) {
                    font-size: 1.7rem !important;
                }
            }

           
        }

        .wp-caption {
            width: 100% !important;
        }

        img {
            margin: 0 auto;
            display: block;
        }

        // .artad-vid{
        //     img{
        //         margin: auto;
        //     }
        // }
    }

    &--title {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);

        .like_item--dm {
            position: static;
            transform: unset;
            padding: 0;
        }
    }

    &--lf {
        width: calc(100% * 2 / 12);

        @media screen and (max-width: 1600px) {
            width: calc(100% * 1.5 / 12);
        }

        @media screen and (max-width: 1200px) {
            display: none;
        }

        &-wrap {
            position: relative;
            height: 100%;
        }

        &-box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);
            height: 100%;
            .news_ct--op {
                width: fit-content;
                margin-left: auto;
                padding-top: 15vh;
            }
        }
    }

    &--qc {
        position: sticky;
        top: 14rem;
        display: block;
        &-bg {
            width: 100%;
            padding-top: calc(100% * 596 / 211);
            overflow: hidden;
            border-radius: var(--radius);
            position: relative;
            display: block;
            img {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                object-fit: cover;
            }
        }
    }

    &--ct {
        width: calc(100% * 6.5 / 12);

        @media screen and (max-width: 1200px) {
            width: calc(100% * 7.5 / 12);
        }

        @media screen and (max-width: 900px) {
            width: calc(100% * 7 / 12);
        }

        @media screen and (max-width: 800px) {
            width: 100%;
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            &-content {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent2);
                position: relative;

                img {
                    width: 100%;

                    height: calc(100vw * 220 / 800);
                }

                iframe {
                    width: 100%;

                    height: calc(100vw * 220 / 800);
                }

                @media screen and (max-width: 1200px) {
                    img {
                        width: 100%;

                        height: calc(100vw * 300 / 800);
                    }

                    iframe {
                        width: 100%;

                        height: calc(100vw * 300 / 800);
                    }
                }

                @media screen and (max-width: 800px) {
                    img {
                        width: 100%;

                        height: calc(100vw * 420 / 800);
                    }

                    iframe {
                        width: 100%;

                        height: calc(100vw * 420 / 800);
                    }
                }

                @media screen and (max-width: 360px) {
                    img {
                        width: 100%;
                        height: calc(100vw * 600 / 800);
                    }

                    iframe {
                        width: 100%;
                        height: calc(100vw * 600 / 800);
                    }
                }
            }
        }
    }

    &--rt {
        flex: 1;
        width: 100%;

        &-wrap {
            height: 100%;

            .news_ct--qc {
                position: relative;
                top: unset;
            }

            .news_ct--qc-bg {
                padding-top: calc(100% * 596 / 496);
            }
        }

        &-box {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent4);
            height: 100%;
        }

        &-news {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent2);
            position: sticky;
            top: 14rem;

            @media screen and (max-width: 1200px) {
                position: relative;
                top: unset;
            }
        }

        &-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &-item {
            &:not(:last-child) {
                padding-bottom: var(--rowContent1);
                border-bottom: 0.1rem solid $color-gray4;
            }

            .like_item--des {
                .content {
                    display: none;
                }
            }

            .like_item--wrap {
                row-gap: var(--rowContent0);
                flex-direction: row;
                column-gap: var(--columnContent0);
            }

            .like_item--top {
                width: 35%;
                flex-shrink: 0;

                @media screen and (max-width: 1400px) {
                    width: 32%;
                }

                @media screen and (max-width: 800px) {
                    width: 20%;
                }

                @media screen and (max-width: 460px) {
                    width: 30%;
                }

                @media screen and (max-width: 360px) {
                    width: 35%;
                }
            }

            .like_item--info {
            }

            .like_item--dm {
                position: static;
                transform: unset;
                padding: 0;
            }

            .like_item--bottom {
                flex: 1;
                width: calc(100% - var(--columnContent2));
                // overflow: hidden;
                justify-content: center;
                row-gap: var(--rowContent0);
            }

            .like_item--img {
                // display: none;
                padding-top: calc(100% * 164 / 184);

                @media screen and (max-width: 400px) {
                    padding-top: calc(100% * 200 / 280);
                }

                @media screen and (max-width: 320px) {
                    padding-top: calc(100% * 140 / 280);
                }
            }

            // .like_item--tt{
            //     left: unset;
            //     right: 0;
            // }
        }
    }

    &--utility {
        padding-top: var(--rowContent2);
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);
        border-top: 0.1rem solid $color-gray4;
    }

    &--control {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent1);

        &-wrap {
            display: flex;
            align-items: center;
            column-gap: 1.2rem;
            justify-content: space-between;
            flex-wrap: wrap;
            row-gap: 1.2rem;
        }
    }

    &--keys {
        &-wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
        }

        &-list {
            display: flex;
            align-items: center;
            column-gap: 0.8rem;
            row-gap: 0.8rem;
            flex-wrap: wrap;
        }
    }

    &--maybe {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent0);

        &-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent0);
        }

        &-item {
            padding-left: 2rem;
            position: relative;
            // line-height: 100%;

            &::before {
                content: "";
                position: absolute;
                top: 0.5rem;
                left: 0;
                width: 1.2rem;
                height: 1.2rem;
                border-radius: 50%;
                overflow: hidden;
                background-image: url(../assets/images/dot.svg);
                background-size: 100% 100%;
                background-repeat: no-repeat;
            }
        }

        &-link {
            font-weight: 600;
            color: $color-black-2;
            @include fz-16;
        }
    }
}

.control {
    &-1 {
        &-list {
            display: flex;
            align-items: center;
            column-gap: 0.8rem;
        }

        &-item {
        }
    }

    &-2 {
        &-list {
            display: flex;
            align-items: center;
            column-gap: 0.8rem;
        }

        &-item {
        }
    }

    &-img {
        width: 3.2rem;
        height: 3.2rem;
        flex-shrink: 0;
        border-radius: 50%;
        overflow: hidden;
        transition: 0.3s ease;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        position: relative;
        background: $color-white;
        border: 0.1rem solid $color-gray4;
        &.heart {
            input {
                display: none;

                &:checked {
                    & ~ .ic {
                        opacity: 1;
                    }
                }
            }

            .ic {
                display: block;
                position: absolute;
                width: 100%;
                height: 100%;
                overflow: hidden;
                border-radius: 50%;
                background-image: url(../assets/images/s1.1.svg);
                position: absolute;
                inset: 0;
                background-repeat: no-repeat;
                background-size: 100% 100%;
                z-index: 2;
                opacity: 0;
                transition: 0.3s ease;
            }

            img {
                position: relative;
            }
        }

        @include hover {
            transform: scale(1.2);

            box-shadow: 0 0 0.5rem rgba($color: #000000, $alpha: 0.3);
        }

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            display: block;
            transition: 0.3s ease;
        }
    }
}

.sl_img {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--wrap {
        display: flex;
        flex-direction: column;
        align-items: center;
        // position: relative;
    }

    &--list {
        width: 100%;
        position: relative;
        &-wrap {
            position: relative;
        }

        @media screen and (max-width: 1370px) {
            max-width: 92%;
        }

        @media screen and (max-width: 800px) {
            max-width: 90%;
        }

        @media screen and (max-width: 600px) {
            max-width: none;
        }

        .swiper-slide {
            height: auto;

            &-active {
                // .sl_img--img {
                //     img {
                //         transform: translate(-50%, -50%) scale(1);
                //         opacity: 1;
                //     }
                // }
            }
        }

        .box-navi {
            position: absolute;
            top: 0;
            left: 0;
            padding-top: calc(100% * 400 / 1192);
            @media screen and (max-width: 600px) {
                display: none;
            }
        }

        .btn-navi {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);

            &.prev {
                left: -6.4rem;
                @media screen and (max-width: 1370px) {
                    left: -6rem;
                }

                @media screen and (max-width: 1230px) {
                    left: -5.8rem;
                }

                @media screen and (max-width: 1200px) {
                    left: -5.2rem;
                }

                @media screen and (max-width: 1024px) {
                    left: -4.6rem;
                }

                @media screen and (max-width: 900px) {
                    left: -4.5rem;
                }
            }

            &.next {
                right: -6.4rem;

                @media screen and (max-width: 1370px) {
                    right: -6rem;
                }

                @media screen and (max-width: 1230px) {
                    right: -5.8rem;
                }

                @media screen and (max-width: 1200px) {
                    right: -5.2rem;
                }

                @media screen and (max-width: 1024px) {
                    right: -4.6rem;
                }

                @media screen and (max-width: 900px) {
                    right: -4.5rem;
                }
            }
        }
    }

    &--item {
        height: 100%;
    }

    &--img {
        height: 100%;
        display: block;
        padding-top: calc(100% * 400 / 1192);
        position: relative;
        overflow: hidden;
        border-radius: var(--radius);

        img {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            // opacity: 0.5;
            object-fit: cover;
            transition: all 0.8s linear;
        }
    }
}

.news {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        &--content {
            @media screen and (max-width: 600px) {
                flex-direction: column;
                row-gap: 1.8rem;
            }
        }

        .title-box {
            flex-direction: row !important;
        }
    }

    &_lf {
        width: calc(100% * 9 / 12);

        @media screen and (max-width: 1024px) {
            width: calc(100% * 8.5 / 12);
        }

        @media screen and (max-width: 800px) {
            width: calc(100% * 8 / 12);
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &--wrap {
            display: flex;
            column-gap: var(--columnContent);

            @media screen and (max-width: 1024px) {
                flex-direction: column;
                row-gap: 1.8rem;
            }
        }

        &--lf {
            width: 65%;

            @media screen and (max-width: 1024px) {
                width: 100%;
            }

            &-list {
            }

            &-item {
                .like_item--img {
                    padding-top: calc(100% * 340 / 580);
                }

                .like_item--des {
                    .title {
                        @include fz-24;
                    }

                    .content {
                        display: -webkit-box;
                        -webkit-line-clamp: 4;
                        -webkit-box-orient: vertical;
                        overflow: hidden;
                    }
                }
            }
        }

        &--rt {
            width: calc(100% - var(--columnContent));
            flex: 1;

            @media screen and (max-width: 1024px) {
                width: 100%;
            }

            &-list {
                display: flex;
                flex-direction: column;
                row-gap: var(--rowContent3);

                @media screen and (max-width: 1024px) {
                    flex-direction: row;
                    column-gap: var(--columnContent);
                }

                @media screen and (max-width: 360px) {
                    flex-direction: column;
                    row-gap: 1.2rem;
                }
            }

            &-item {
                // &:not(:last-child){
                //     padding-bottom: var(--rowContent1);
                //     border-bottom: 0.1rem solid $color-gray4;
                // }

                @media screen and (max-width: 1024px) {
                    width: calc(50% - (var(--columnContent) / 2));
                }

                @media screen and (max-width: 360px) {
                    width: 100%;
                }

                .like_item--des {
                    .content {
                        display: none;
                    }
                }

                .like_item--img {
                    @media screen and (max-width: 360px) {
                        padding-top: calc(100% * 150 / 300);
                    }
                }
            }
        }
    }

    &_rt {
        flex: 1;
        width: 100%;

        &--wrap {
            height: 100%;
        }
    }
}

.events_new {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);
}

.nx {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .comment_cmt {
            .btn {
                margin: 0 auto;
            }
        }

        .comment_cmt--bl .comment_cmt--item-control-bottom {
            .btn {
                margin-left: auto !important;
                margin-right: unset !important;
            }
        }
    }
}

.post_lq {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &--content {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);
    }

    &--wrap {
        @media screen and (max-width: 600px) {
            flex-direction: column;
            row-gap: var(--rowContent2);
        }
    }

    &--lf {
        width: calc(100% * 9 / 12);

        @media screen and (max-width: 1200px) {
            width: calc(100% * 8.5 / 12);
        }

        @media screen and (max-width: 800px) {
            width: calc(100% * 8 / 12);
        }

        @media screen and (max-width: 600px) {
            width: 100%;
        }

        &-wrap {
        }

        &-list {
            row-gap: var(--rowContent3);
        }

        &-item {
            .like_item--des .note-text.content {
                display: none;
            }
            .like_item--img {
                @media screen and (max-width: 400px) {
                    padding-top: calc(100% * 200 / 280);
                }

                @media screen and (max-width: 320px) {
                    padding-top: calc(100% * 140 / 280);
                }
            }

            @media screen and (max-width: 320px) {
                width: 100%;
            }
        }
    }

    &--rt {
        width: 100%;
        flex: 1;

        &-wrap {
        }

        &-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent3);
        }

        &-item {
            .like_item--top {
                display: none;
            }

            .like_item--dm {
                position: static;
                transform: translateX(0);
                padding: 0;
            }
            .like_item--des .note-text.content {
                display: none;
            }
        }
    }
}

.post {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pd);

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);
    }

    &_top {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent2);

        .like_title--search {
            max-width: 70rem;
        }
    }

    &_bottom {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);

        .page-numbers {
            justify-content: center;
        }
    }
}
