.banner-main {
    position: relative;
    margin-bottom: var(--size-pd);
    // overflow: hidden;
    // @media screen and (max-width: 1200px) {
    //     background: $color-second;
    // }
    &-bg {
        width: 100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        z-index: -1;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &.cus {
        margin-bottom: 0;
        @media screen and (max-width:600px) {
            margin-bottom: var(--size-pdl);
        }
        .banner-main-wrap {
            align-items: start;
            justify-content: start;

            @media screen and (max-width:600px) {
                align-items: center;
                justify-content: center;
                min-height: 15rem;
            }
        }

        .banner-main-content {
            .title-lg {
                display: none;
            }
            .breadcrumbs-list {
                justify-content: start;
            }
        }
    }

    &-wrap {
        width: 100%;
        min-height: 30rem;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 4rem 0;
        @media screen and (max-width: 1200px) {
            min-height: 25rem;
        }

        @media screen and (max-width: 600px) {
            min-height: 20rem;
        }
    }

    &-content {
        display: flex;
        flex-direction: column;
        row-gap: 0.8rem;

        @media screen and (max-width: 1200px) {
            row-gap: 0.6rem;
        }

        @media screen and (max-width: 800px) {
            row-gap: 0.4rem;
        }

        .title-lg {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-align: center;
            // @media screen and (min-width: 1200px) {
            //     clip-path: inset(0 100% 0 0);
            //     animation: hienChuBg 1.2s 1s linear forwards;
            //     @keyframes hienChuBg {
            //         0% {
            //             clip-path: inset(0 100% 0 0);
            //         }

            //         100% {
            //             clip-path: inset(0 0 0 0);
            //         }
            //     }
            // }

            strong {
                color: $color-pri;
            }
        }

        .breadcrumbs {
            @media screen and (min-width: 1200px) {
                // opacity: 0;
                // @keyframes anHienBr {
                //     0% {
                //         opacity: 0;
                //     }
                //     100% {
                //         opacity: 1;
                //     }
                // }

                // animation: anHienBr 1s 1.8s linear forwards;
            }

            &-list {
                justify-content: center;
            }

            &-item {
                &:not(:last-child) {
                    &::before {
                        filter: brightness(0) saturate(100%) invert(100%) sepia(100%) saturate(0%) hue-rotate(317deg)
                            brightness(103%) contrast(104%);
                    }
                }
                a {
                    color: $color-white;

                    @include hover {
                        color: $color-yellow !important;
                    }
                }

                &.current {
                    a {
                        color: $color-purple2 !important;
                    }
                }
            }

            &-wrap {
                padding-top: 0;
            }
        }
    }
}

.banner-home {
    position: relative;
    margin-bottom: var(--size-pd);

    .banner-main-tt {
        position: absolute;
        bottom: -10%;
        left: 10%;

        @media screen and (max-width: 1600px) {
            bottom: -20%;
        }

        @media screen and (max-width: 800px) {
            display: none;
        }

        &-bg {
            @media screen and (max-width: 1600px) {
                max-width: 23rem;
            }

            @media screen and (max-width: 1200px) {
                max-width: 20rem;
            }

            @media screen and (max-width: 1024px) {
                max-width: 18rem;
            }
        }

        @media screen and (min-width: 1200px) {
            transform: scale(0);
            animation: thuNhobn 0.8s linear forwards;
        }

        @media screen and (max-width: 1200px) {
            transform: scale(1);
        }

        @keyframes thuNhobn {
            0% {
                transform: scale(0);
            }

            50% {
                transform: scale(0.5);
            }
            100% {
                transform: scale(1);
            }
        }
    }

    @keyframes truotLenBh {
        0% {
            transform: translateY(50%);
            opacity: 0;
        }
        100% {
            transform: translateY(0%);
            opacity: 1;
        }
    }

    &-bg {
        width: 100%;
        overflow: hidden;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        z-index: -1;
        &::before {
            content: "";
            position: absolute;
            width: 150%;
            height: 100%;
            top: 0%;
            left: 50%;
            transform: translate(-50%, 0%);
            background: $color-third;
            z-index: -1;
            border-radius: 0 0 100% 100%;

            @media screen and (max-width: 1200px) {
                display: none;
            }
        }

        @media screen and (max-width: 1200px) {
            background: $color-third;
        }
    }

    &-wrap {
        min-height: calc(100vh - var(--size-hd));
        display: flex;

        // align-items: center;

        @media screen and (max-width: 1200px) {
            min-height: calc(60vh - var(--size-hd));
        }

        @media screen and (max-width: 800px) {
            flex-direction: column-reverse;
            row-gap: 1.2rem;
        }

        .btn {
            @media screen and (min-width: 1200px) {
                animation: truotLenBh 0.5s 1s linear forwards;
                transform: translateY(50%);
                opacity: 0;
            }

            @media screen and (max-width: 1200px) {
                transform: translateY(0%);
                opacity: 1;
            }
            @media screen and (max-width: 460px) {
                margin: auto;
            }
        }
    }

    &-title {
        display: flex;
        flex-direction: column;
        row-gap: 0.8rem;

        .title-lg {
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;

            font-size: 4.8rem !important;

            @media screen and (min-width: 1200px) {
                animation: truotLenBh 0.5s linear forwards;
                transform: translateY(50%);
                opacity: 0;
            }

            @media only screen and (max-width: 1200px) {
                font-size: 3.8rem !important;
                transform: translateY(0%);
                opacity: 1;
            }

            @media only screen and (max-width: 800px) {
                font-size: 2.8rem !important;
            }

            @media only screen and (max-width: 600px) {
                font-size: 2.4rem !important;
            }
            @media only screen and (max-width: 460px) {
                font-size: 2.2rem !important;
            }

            strong {
                color: $color-pri;
            }

            @media screen and (max-width: 460px) {
                text-align: center;
            }
        }

        .note-text {
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;

            font-size: 1.6rem !important;

            @media screen and (min-width: 1200px) {
                animation: truotLenBh 0.5s 0.5s linear forwards;
                transform: translateY(50%);
                opacity: 0;
            }

            @media screen and (max-width: 1200px) {
                transform: translateY(0%);
                opacity: 1;
            }

            @media screen and (max-width: 800px) {
                font-size: 1.4rem !important;
            }

            @media screen and (max-width: 460px) {
                text-align: center;
            }
        }
    }

    &-lf {
        display: flex;
        flex-direction: column;
        // align-items: center;
        justify-content: center;

        @media screen and (max-width: 800px) {
            width: 100% !important;
        }

        &-wrap {
            padding: 16rem 0;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);

            @media screen and (max-width: 1200px) {
                padding: 14rem 0;
            }

            @media screen and (max-width: 800px) {
                padding: 2rem 0 4rem;
            }

            @media screen and (max-width: 600px) {
                padding: 2rem 0 3rem;
            }
        }
    }

    &-tt {
        position: relative;
        z-index: 1;

        @media screen and (min-width: 1200px) {
            animation: chayLenBh 1s 1.4s linear forwards;
            opacity: 0;
            pointer-events: none;
        }

        @keyframes chayLenBh {
            0% {
                opacity: 0;
                pointer-events: none;
            }
            50% {
                opacity: 0.5;
                pointer-events: none;
            }
            100% {
                opacity: 1;
                pointer-events: none;
            }
        }

        @media screen and (max-width: 1200px) {
            opacity: 1;
        }

        @media screen and (max-width: 800px) {
            max-width: 60rem;
            margin: auto;
        }

        &-bg {
            max-width: 60rem;
            width: 100%;
            margin: 3rem auto 0;
            clip-path: circle(100% at 45% 4%);
            position: relative;
            z-index: 2;

            @media screen and (max-width: 1600px) {
                max-width: 50rem;
            }

            @media screen and (max-width: 1500px) {
                max-width: 50rem;
            }

            @media screen and (max-width: 1360px) {
                max-width: 45rem;
            }

            @media screen and (max-width: 1200px) {
                max-width: 40rem;
                clip-path: unset;
            }

            @media screen and (max-width: 800px) {
                max-width: 35rem;
                clip-path: unset;
            }

            @media screen and (max-width: 460px) {
                max-width: 30rem;
            }

            @media screen and (max-width: 360px) {
                max-width: 25rem;
            }

            @keyframes lanToa {
                0% {
                    transform: translate(-50%, -50%) scale(0.8);
                    opacity: 0;
                }

                50% {
                    transform: translate(-50%, -50%) scale(1.2);
                    opacity: 0.8;
                }

                100% {
                    transform: translate(-50%, -50%) scale(1.4);
                    opacity: 0;
                }
            }

            &::before {
                content: "";
                width: 40rem;
                height: 40rem;
                border-radius: 50%;
                overflow: hidden;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0.8);
                background: #f7d060;
                opacity: 0;
                animation: lanToa 2s linear infinite forwards;
                z-index: -1;

                @media screen and (max-width: 1200px) {
                    width: 30rem;
                    height: 30rem;
                }

                @media screen and (max-width: 460px) {
                    width: 25rem;
                    height: 25rem;
                }

                @media screen and (max-width: 360px) {
                    width: 20rem;
                    height: 20rem;
                }
            }

            &::after {
                content: "";
                width: 40rem;
                height: 40rem;
                border-radius: 50%;
                overflow: hidden;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%) scale(0.8);
                background: #f7d060;
                opacity: 0;
                animation: lanToa 2s 1.2s linear infinite forwards;
                z-index: -1;

                @media screen and (max-width: 1200px) {
                    width: 30rem;
                    height: 30rem;
                }

                @media screen and (max-width: 460px) {
                    width: 25rem;
                    height: 25rem;
                }

                @media screen and (max-width: 360px) {
                    width: 20rem;
                    height: 20rem;
                }
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                position: relative;
                z-index: 1;
            }
        }

        &-list {
        }

        &-item {
            position: absolute;
            z-index: 2;

            &:first-child {
                bottom: -5%;
                left: 0;
                animation: lenXuong 5s linear infinite forwards;
            }

            &:nth-child(2) {
                top: 20%;
                left: -10%;
                animation: lenXuong 5s 0.5s linear infinite forwards reverse;
                @media screen and (max-width: 1460px) {
                    left: -5%;
                }
            }

            &:nth-child(3) {
                top: 20%;
                right: -10%;

                animation: lenXuong 5s 0s linear infinite forwards;

                @media screen and (max-width: 1460px) {
                    right: -3%;
                }

                @media screen and (max-width: 800px) {
                    right: -10%;
                }

                @media screen and (max-width: 300px) {
                    right: -5%;
                }
            }

            &:last-child {
                bottom: 00%;
                right: -20%;

                animation: lenXuong 5s 0.5s linear infinite forwards reverse;

                @media screen and (max-width: 1460px) {
                    right: -3%;
                }

                @media screen and (max-width: 800px) {
                    right: -10%;
                }

                @media screen and (max-width: 300px) {
                    right: -5%;
                }
            }
        }

        &-ic {
            width: 100%;
            height: 100%;
            max-width: 15rem;

            @media screen and (max-width: 1460px) {
                max-width: 13rem;
            }

            @media screen and (max-width: 1024px) {
                max-width: 8rem;
            }

            @media screen and (max-width: 360px) {
                max-width: 6rem;
            }

            @media screen and (max-width: 300px) {
                max-width: 5rem;
            }
            img {
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    }

    &-rt {
        @media screen and (max-width: 800px) {
            width: 100% !important;
        }
        &-wrap {
            height: 100%;
            display: flex;
            width: 100%;
            flex-direction: column;
            justify-content: end;

            @media screen and (max-width: 800px) {
                margin-top: 6rem;
            }
        }
    }
}

.banner_qc {
    padding: var(--size-pd) 0;
    margin: 0 4rem;

    @media screen and (max-width:1200px) {
        margin: 0 1.5rem;
    }

    &.ani {
        &.showed {
            .banner_qc--wrap {
                clip-path: inset(0 0 0 0);
            }
        }
    }

    &--wrap {
        width: 100%;

        clip-path: inset(0 100% 0 0);
        transition: 1.2s linear;

        @media screen and (max-width:1200px) {
            clip-path: inset(0 0 0 0);
            
        }
    }

    &--img {
        width: 100%;
        height: auto;
        border-radius: var(--radius);
        overflow: hidden;
        img {
            width: 100%;
            height: 100% !important;
            display: block;
            object-fit: cover !important;
            border-radius: var(--radius);
        }
    }
}
