@import url("../assets/font/robonto/stylesheet.css");
@import url("../assets/font/Anton/stylesheet.css");

* {
    box-sizing: border-box;
}

html {
    font-size: 10px;
    scroll-behavior: smooth;

    @media screen and (min-width: 2000px) {
        font-size: 100%;
    }
}

body {
    font-family: "Roboto";
    font-size: 16px;
    line-height: 1.5;
    font-weight: 400;
    -webkit-tap-highlight-color: transparent;
    color: $color-text;
    background: #ffffff;

    @media screen and (min-width: 2000px) {
        font-size: 100%;
    }

    @media screen and (max-width: 800px) {
        font-size: 14px;
    }

    // @media screen and (max-width: 460px) {
    //     font-size: 14px;
    //   }
    &.actived {
        overflow: hidden;
    }
}

a {
    text-decoration: none;
    color: inherit;

    @include transition(0.3s);

    @include min(1200px) {
        &:hover {
            color: $color-pri;
        }
    }
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:active,
input:-webkit-autofill:focus {
    background-color: #ffffff !important;
    color: black !important;
    -webkit-box-shadow: 0 0 0 1000px #ffffff inset !important;
    -webkit-text-fill-color: black !important;
}

button,
select {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-appearance: none;
    cursor: pointer;
}

button {
    padding: 0;
    background-color: transparent;
    cursor: pointer;
}

input,
textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

button,
select,
input,
textarea {
    border: none;
    outline: none;
    box-shadow: none;
    font-family: inherit;
    color: black;
    background: transparent;
    font-family: var(--font-pri);
    &:focus {
        border: none;
        outline: none;
        box-shadow: none;
    }

    &::placeholder {
        font-family: var(--font-pri);
    }

    &:focus-visible {
        border: none;
        outline: none;
        box-shadow: none;
    }
}

::-webkit-scrollbar {
    width: 0.3rem;
}

::-webkit-scrollbar-thumb {
    background: $color-pri;
}

::-webkit-scrollbar-track {
    background: white !important;
    border-radius: 0.4rem !important;
}

@supports (-webkit-touch-action: none) {
    input,
    select,
    textarea {
        font-size: max(16px);
    }
}

.main {
    // min-height: 50rem;
    position: relative;
    // overflow: hidden;
    &.pt {
        padding-top: var(--size-hd);
        transition: 0.25s linear;
    }

    &.pt-2 {
        padding-top: var(--size-hd2);
        transition: 0.25s linear;
    }
}

.container {
    width: 100%;
    max-width: 124.6rem;
    height: 100%;
    margin: 0 auto;
    padding: 0 1.5rem;
}

.no-scroll {
    touch-action: none;
    -webkit-overflow-scrolling: none;
    overflow: hidden;
    overscroll-behavior: none;
}

iframe {
    vertical-align: middle;
}

img {
    max-width: 100%;
    height: auto;
}

.t-center {
    text-align: center;
}

.fw-b {
    font-weight: bold;
}

.fsi {
    font-style: italic;
}

.t-up {
    text-transform: uppercase;
}

.tabBtn {
    cursor: pointer;
}

.gItem {
    cursor: pointer;
}

.global-title {
    max-width: 0;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
}

.fancybox__container {
    z-index: 5000;
}

.title {
    &-lg {
        @include fz-46;
        line-height: 1.4;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }
    }

    &-md {
        @include fz-40;
        line-height: 1.4;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }

        strong {
            color: $color-pri;
        }
    }

    &-sm {
        @include fz-32;
    }

    &-sm2 {
        @include fz-36;
    }

    &-sm3 {
        @include fz-40;
        line-height: 1.3;

        @media screen and (max-width: 800px) {
            line-height: 1.5;
        }
    }

    &-sm4 {
        @include fz-48;
    }

    &-mn {
        @include fz-24;
    }

    &-mn2 {
        @include fz-28;
    }
}

.note {
    &-title {
        font-weight: 700;
        @include fz-16;
    }

    &-text {
        @include fz-16;
        strong {
            font-weight: 700;
        }

        @media screen and (max-width: 460px) {
        }
    }

    &-lg {
        @include fz-20;
        font-weight: 600;

        strong {
            color: $color-pri;
        }
    }

    &-md {
        @include fz-18;
    }

    &-sm {
        @include fz-14;
    }

    &-mn {
        @include fz-12;
    }

    &-ic {
        width: 1.2rem;
        height: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        transition: all 0.3s ease;
        i {
            color: $color-pri;
            font-size: 1.4rem;
        }
    }
}

.cl {
    &-pri {
        color: $color-pri;
    }

    &-white {
        color: $color-white;
    }

    &-yellow {
