    }

    &-text2 {
        color: $color-text2;
    }

    &-text3 {
        color: $color-text3;
    }

    &-gray {
        color: $color-gray;
    }

    &-gray2 {
        color: $color-gray2;
    }

    &-gray3 {
        color: $color-gray3;
    }

    &-gray4 {
