.box-dm {
    color: $color-text2;
    background: $color-white2;
    padding: 0.6rem 1.2rem;
    border-radius: 0.6rem;
    font-weight: 400;
    width: fit-content;
    position: relative;
    display: flex;
    align-items: center;
    column-gap: 0.4rem;
    @include hover {
        background: $color-pri;
        color: $color-white;

        .note-sm {
            color: $color-white;
        }
    }

    @media screen and (max-width: 1200px) {
        padding: 0.4rem 1rem;
        border-radius: 0.4rem;
    }

    @media screen and (max-width: 800px) {
        padding: 0.2rem 0.8rem;
    }

    @media screen and (max-width: 600px) {
        padding: 0.2rem 0.6rem;
    }

    @media screen and (max-width: 460px) {
        padding: 0.2rem 0.6rem;
    }

    &-ic {
        width: 1.4rem;
        height: 1.4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        display: none;
        img {
            width: 100%;
            height: 100%;
            object-fit: contain;
        }

        @media screen and (max-width: 600px) {
            width: 1.2rem;
            height: 1.2rem;
        }
    }

    .note-sm {
        // line-height: 100%;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
        display: flex;
        align-items: center;
        top: 0.1rem;
        position: relative;
        @include fz-12;
        color: $color-black-2;
    }

    &.blue {
        background: $color-blue2;

        .note-sm {
            color: $color-blue !important;
        }
    }

    &.green {
        background: $color-green2;

        .note-sm {
            color: $color-green !important;
        }
    }

    &.red {
        background: $color-red2;

        .note-sm {
            color: $color-red !important;
        }
    }

    &.pup {
        background: $color-purple2;

        .note-sm {
            color: $color-purple !important;
        }
    }

    &.icon {
        .box-dm-ic {
            display: flex;
            position: relative;
        }
    }

    &.yellow {
        background: $color-yellow2;

        .note-sm {
            color: $color-yellow !important;
        }
    }
}

