.title-box {
    display: flex;
    align-items: center;
    column-gap: 1.2rem;
    justify-content: space-between;
    row-gap: 0.8rem;
    overflow: hidden;
    width: 100%;
    @media screen and (max-width: 600px) {
        flex-direction: column;
    }

    .title-md {
        @media screen and (max-width: 600px) {
            text-align: center;
        }
    }
}

.fancybox__thumbs {
    display: none;
}

.pagination-ct {
    width: fit-content;
    margin: 0 auto;
}

.switch {
    .checkbox {
        display: none;
    }

    .slider {
        width: 4rem;
        height: 2rem;
        background-color: #efedf2;
        border-radius: 20rem;
        overflow: hidden;
        display: flex;
        align-items: center;

        transition: 0.3s;

        cursor: pointer;
    }

    .slider::before {
        content: "";
        display: block;
        width: 1.8rem;
        height: 1.8rem;
        background-color: #fff;
        transform: translateX(0.2rem);
        border-radius: 50%;
        transition: 0.3s;
    }

    .checkbox:checked ~ .slider::before {
        transform: translateX(2.1rem);
    }

    .checkbox:checked ~ .slider {
        background: #7b61ff;
    }

    .checkbox:active ~ .slider::before {
        transform: translate(0);
    }
}

.box-sw {
    padding: 2.4rem;
    border: 0.1rem solid $color-gray4;
    border-radius: var(--radius);
    transition: 0.5s linear;

    @media screen and (max-width: 1200px) {
        padding: 1.2rem;
    }

    @include hover {
        border-color: $color-pri;
        background: $color-white;
    }

    &.cus {
        .box-sw-lf {
            display: none;
        }
    }

    &.cus2 {
        .box-sw-rt {
            display: none;
        }

        .box-sw-ic {
            width: 3.2rem;
            height: 3.2rem;
        }

        .box-sw-ct {
            .note-sm {
                color: $color-yellow;
            }
        }
    }

    &-wrap {
        display: flex;
        align-items: center;
        column-gap: var(--columnContent1);
    }

    &-ic {
        width: 6rem;
        height: 6rem;
        overflow: hidden;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: var(--radius);
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        @media screen and (max-width: 1200px) {
            width: 5rem;
            height: 5rem;
        }

        @media screen and (max-width: 600px) {
            width: 4.6rem;
            height: 4.6rem;
        }

        @media screen and (max-width: 460px) {
            width: 4rem;
            height: 4rem;
        }
    }

    &-lf {
        width: fit-content;
    }

    &-ct {
        width: calc(100% - (var(--columnContent1) / 2));
        flex: 1;

        .title-mn {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            @media screen and (max-width: 1200px) {
                @include fz-16;
            }
        }

        .note-sm {
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            @media screen and (max-width: 1200px) {
                @include fz-12;
            }
        }
    }

    &-rt {
        width: fit-content;
    }
}

.select2-dropdown {
    z-index: 3000 !important;
}

.fancybox__container {
    z-index: 6000;
    &.ex {
        @keyframes anHienEx {
            0% {
                opacity: 0;
                visibility: hidden;
            }

            100% {
                opacity: 1;
                visibility: visible;
            }
        }

        // .fancybox__image{
        //     pointer-events: none !important;
        // }

        .fancybox__toolbar {
            width: fit-content !important;
            right: 0 !important;
            left: unset !important;
            opacity: 0;
            visibility: hidden;
            background: none !important;
            animation: anHienEx 0.5s 5s linear forwards !important;
            z-index: 20000 !important;
        }

        .carousel__button.fancybox__button--zoom {
            display: none !important;
        }

        .carousel__button.fancybox__button--fullscreen {
            display: none !important;
        }

        .carousel__button.is-close {
            visibility: hidden;

            animation: anHienEx 0.5s 5s linear forwards !important;
        }

        .carousel__button.fancybox__button--close {
            opacity: 0;
            visibility: hidden;

            animation: anHienEx 0.5s 5s linear forwards !important;
        }
    }
}

.paginations {
