.td {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &_wrap {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent4);
    }

    &_box {
        display: flex;
        flex-direction: column;
        row-gap: var(--rowContent);

        .page-numbers {
            justify-content: center;
        }
    }

    &_title {
        max-width: 65rem;
        margin: 0 auto;

        .title-md {
            text-align: center;
        }
    }

    &_list {
        row-gap: var(--rowContent);
    }

    &_item {
        @media screen and (max-width: 320px) {
            width: 100%;
        }

        &--wrap {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent1);
            padding: 4rem;
            border-radius: var(--radius);
            overflow: hidden;
            border: 0.1rem solid $color-gray4;
            transition: 0.5s linear;
            background: transparent;
            height: 100%;
            justify-content: center;
            @include hover {
                background: $color-white;
                box-shadow: 0px 4px 40px 0px #0b111f14;
            }

            @media screen and (max-width: 1200px) {
                padding: 2rem;
            }

            @media screen and (max-width: 800px) {
                padding: 1.6rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.2rem;
            }

            @media screen and (max-width: 460px) {
                padding:1rem 0.8rem;
            }
        }

        &--top {
            display: flex;
            align-items: center;
            column-gap: var(--columnContent1);
            justify-content: space-between;

            @media screen and (max-width: 768px) {
                flex-direction: column;
                row-gap: var(--rowContent1);
                align-items: start;
            }

            .btn {
                box-shadow: none !important;
                flex-shrink: 0;

                @media screen and (max-width: 768px) {
                    width: 100% !important;
                }

                @media screen and (max-width: 1200px) {
                    width: fit-content;
                    min-width: unset;
                    padding: 0 1.2rem;
                }
                &-text {
                    @media screen and (max-width: 1200px) {
                        @include fz-14;
                    }
                }
            }
        }

        &--title {
            display: flex;
            flex-direction: column;
            row-gap: 0.5rem;

            @media screen and (max-width: 768px) {
                width: 100%;
                align-items: center;
            }

            .box-dm {
                .note-sm {
                    font-weight: 600;
                }
            }

            .title-mn {
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;

                @media screen and (max-width: 768px) {
                    text-align: center;
                }
            }
        }

        &--bottom {
        }

        &--list {
            display: flex;
            align-items: center;
            column-gap: var(--columnContent1);
            row-gap: var(--rowContent1);

            justify-content: space-between;
            flex-wrap: wrap;

            @media screen and (max-width: 460px) {
                row-gap: var(--rowContent0);
            }
        }

        &--item {
            @include fz-12;
            white-space: nowrap;
        }
    }
}

.td_ct {
    padding-top: var(--size-pd);
    padding-bottom: var(--size-pdl);

    &--wrap {
        @media screen and (max-width: 800px) {
            flex-direction: column;
            row-gap: var(--rowContent2);
        }
    }

    &--lf {
        width: calc(100% * 3 / 12);

        @media screen and (max-width: 1200px) {
            width: calc(100% * 3.5 / 12);
        }

        @media screen and (max-width: 800px) {
            width: 100%;
        }

        &-wrap {
            height: 100%;
        }

        &-ic {
            width: 4rem;
            height: 4rem;
            flex-shrink: 0;
            display: flex;
            justify-content: center;
            align-items: center;

            @media screen and (max-width: 1200px) {
                width: 3.2rem;
                height: 3.2rem;
            }

            @media screen and (max-width: 600px) {
                width: 2.6rem;
                height: 2.6rem;
            }

            img {
                width: 100%;
                height: 100%;
                object-fit: contain !important;
            }
        }

        &-box {
            padding: 4rem;
            border-radius: var(--radius);
            border: 0.1rem solid $color-gray4;
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent);
            position: sticky;
            top: 14rem;

            @media screen and (max-width: 1200px) {
                padding: 2rem;
            }

            @media screen and (max-width: 600px) {
                padding: 1.2rem;
            }

            .btn {
                box-shadow: none !important;
            }
        }

        &-list {
            display: flex;
            flex-direction: column;
            row-gap: var(--rowContent4);

            @media screen and (max-width: 800px) {
                flex-direction: row;
                column-gap: var(--columnContent1);
                justify-content: space-between;
                flex-wrap: wrap;
            }

            &-ic {
                width: 4rem;
                height: 4rem;
                flex-shrink: 0;
                display: flex;
                justify-content: center;
                align-items: center;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

        &-item {
            display: flex;
            align-items: center;
            column-gap: var(--columnContent1);
            // justify-content: space-between;

            @media screen and (max-width: 800px) {
                width: calc(50% - (var(--columnContent1) / 2));
            }

            @media screen and (max-width: 360px) {
                width: 100%;
            }
        }

        &-content {
            .note-sm {
                display: -webkit-box;
                -webkit-line-clamp: 1;
                -webkit-box-orient: vertical;
                overflow: hidden;
            }
        }
    }

    &--rt {
        width: 100%;
        flex: 1;

        &-wrap {
        }

        &-content {
            ul {
                list-style: disc;
                margin-left: 2.4rem;
            }
        }
    }
}
