.popup {
  position: fixed;
  @include full;
  z-index: 999;
  visibility: hidden;
  color: $color-black-3;

  &.open {
    visibility: visible;

    .popup-overlay {
      opacity: 1;
      visibility: visible;
    }

    .popup-main {
      opacity: 1;
      visibility: visible;
    }

    .popup-close {
      opacity: 1;
      visibility: visible;
    }
  }

  &-overlay {
    position: absolute;
    background-color: rgba(0, 0, 0, 0.56);
    backdrop-filter: blur(2.5rem);
    @include full;
    z-index: 1;
    @include transitionRe;
    opacity: 0;
    visibility: hidden;
  }

  &-main {
    @include mid;
    z-index: 2;
    width: 83rem;
    max-width: 95%;
    @include transitionRe;
    opacity: 0;
    visibility: hidden;

    @include max(1024px) {
      width: calc(100% - 1.2rem);
      max-width: 100%;
    }
  }

  &-over {
    overflow-y: auto;
    min-height: 40vh;
    max-height: 95vh;
  }

  &-wrapper {
    background: white;
    width: 100%;
    min-height: 40vh;
    position: relative;
    padding: 3rem 1.5rem 3rem;
    // border-radius: 6px;
  }

  &-title {
    @include fz-24;
    font-weight: 700;
    text-transform: uppercase;
    text-align: center;
    margin-bottom: 16px;
  }

  &-close {
    position: absolute;
    top: 1rem;
    right: 1rem;
    cursor: pointer;
    transition: 0.6s ease-in-out;
    width: 3rem;
    height: 3rem;
    @include mid-flex;
    z-index: 1201;
    opacity: 0;
    visibility: hidden;

    &:hover {
      .icon {
        transform: rotate(360deg);
      }
    }

    .icon {
      color: $color-black;
      font-weight: 300;
      font-size: 2.4rem;
      @include transition(0.6s, ease-in-out);
    }
  }

  &-getPriceForm {
    // .form-control
  }
}

.popup_qc {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  // display: none;
  width: 0;
  height: 0;
  position: absolute;

  bottom: 0;
  left: 0;


  &--link2{
    pointer-events: none;
    visibility: hidden;
    position: absolute;
  }



  &--title{
    color: $color-white;
    font-size: 2rem;
    display: flex;
    align-items: end;

    &.hidden{
      display: none;
    }
  }

  &.showed{
    left: unset;
    right: 2.4rem;
    top: 1.6rem;
    opacity: 1;
    visibility: visible;
    width: fit-content;
    height: fit-content;
    z-index: 10000;
    position: fixed;
    @media screen and (max-width:1024px) {
      right: 1.4rem;
      top: 0.8rem;
    }

    .popup_qc--link2{
      pointer-events: visible;
      position: fixed;
      visibility: visible;
      inset: 0;
      width: 100%;
      height: 100%;
      clip-path: polygon(0 0, 0% 100%, 100% 100%, 100% 10%, 0 15%, 90% 10%, 90% 0);
    }
  }
}
