.footer {
  padding-top: 4rem;
  padding-bottom: 0rem;
  background: #F9F8FB;

  border-top: 0.1rem solid $color-gray4;
  @media screen and (max-width: 1200px) {
    padding-top: 3rem;
    padding-bottom: 0rem;
  }

  @media screen and (max-width: 600px) {
    padding-top: 2rem;
    padding-bottom: 0rem;
  }

  &-logo {
    max-width: 18rem;
    display: flex;
    align-items: center;
    justify-content: center;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }

    @media screen and (max-width: 1024px) {
      max-width: 16rem;
    }

    @media screen and (max-width: 800px) {
      max-width: 14rem;
    }

    @media screen and (max-width: 600px) {
      max-width: 12rem;
    }

    @media screen and (max-width: 360px) {
      max-width: 10rem;
    }
  }

  &-info {
    display: flex;
    flex-direction: column;
    row-gap: 1.6rem;

    &-list {
      display: flex;
      flex-direction: column;
      row-gap: 1.2rem;

      @media screen and (max-width: 1024px) {
        row-gap: 1rem;
      }

      @media screen and (max-width: 800px) {
        row-gap: 0.8rem;
      }

      @media screen and (max-width: 600px) {
        row-gap: 0.6rem;
      }
    }

    &-wrap {
      // display: flex;
      // column-gap: 1rem;
      // width: 100%;
      // flex: 1;

      .note-text {
        display: inline-block;
        margin-right: 0.5rem;
      }
    }

    &-item {
      // display: flex;
      // column-gap: 0.6rem;

      .note-text {
        width: fit-content;
      }

      width: 100%;

      .note-sm {
      }
    }

    &-link {
      width: fit-content;
      @include fz-14;
      color: $color-text2;
      flex-shrink: 0;
      @include hover {
        color: $color-pri;
      }
    }

    // &-ic {
    //   width: 2rem;
    //   height: 2rem;
    //   flex-shrink: 0;

    //   img {
    //     width: 100%;
    //     height: 100%;
    //     object-fit: contain;
    //     // filter: brightness(0) saturate(100%) invert(30%) sepia(15%) saturate(824%) hue-rotate(176deg) brightness(97%)
    //     //   contrast(89%);
    //   }
    // }
  }

  .list-address {
    row-gap: 1rem;
    // padding: 0 0.5rem;
    display: flex;
    flex-direction: column;

    @media screen and (max-width: 1200px) {
      row-gap: 0.8rem;
    }

    @media screen and (max-width: 600px) {
      row-gap: 0.6rem;
    }
  }

  .ic-address {
    border: none !important;
    border-radius: unset !important;
    width: 1.6rem;
    // height: 1.8rem;
    height: 1.6rem;
    flex-shrink: 0;
    transform: translateY(0.15rem);
    display: flex;
    align-items: start;
    justify-content: center;

    img {
      width: 100%;
      height: 100%;
      filter: brightness(0) saturate(100%) invert(14%) sepia(100%)
        saturate(3911%) hue-rotate(356deg) brightness(95%) contrast(80%);
    }
  }

  .item-address {
    // align-items: center !important;
    column-gap: 0.8rem;
    flex-direction: row;
    display: flex;

    @media screen and (max-width: 1200px) {
      column-gap: 0.6rem;
    }

    // @media screen and (max-width: 600px) {
    //   column-gap: 0.4rem;
    // }
  }

  .link-address {
    color: $color-text;
    font-weight: 600;
    font-size: 1.4rem;

    @include hover {
      color: $color-pri;
    }
  }

  .menu-list {
    display: flex;
    flex-direction: column;
    margin: 0;
    row-gap: 1.4rem;
    height: auto;

    @media screen and (max-width: 1024px) {
      // flex-direction: row;
      column-gap: 1.2rem;
      row-gap: 1.2rem;
      flex-wrap: wrap;
    }
    @media screen and (max-width: 800px) {
      // row-gap: 1.2rem;
      column-gap: 1rem;
      row-gap: 1.2rem;
    }

    @media screen and (max-width: 600px) {
      column-gap: 1.2rem !important;
      flex-wrap: wrap;
      row-gap: 0.8rem;
    }

    @media screen and (max-width: 600px) {
      column-gap: 1.2rem !important;
      flex-wrap: wrap;
      row-gap: 0.8rem;
    }

    @media screen and (max-width: 320px) {
      flex-direction: column;
      column-gap: 1.2rem;
      row-gap: 0.6rem;
      flex-wrap: wrap;
    }

    .menu-item {
      height: auto;
      @media screen and (max-width: 460px) {
        width: fit-content !important;
      }

      @media screen and (max-width: 320px) {
        width: 100% !important;
      }
      &.current-menu-item {
        .menu-link {
          color: $color-pri;
          font-weight: 400;
        }
      }

      .menu-link {
        padding: 0;
        color: #636069;
        font-weight: 400;
        // height: 0;
        @include fz-14;
        white-space: wrap;
        align-items: start;
        text-transform: none;
        &::before {
          display: none;
        }

        @include hover {
          color: $color-pri;
        }
      }
    }
  }

  &-wrap {
    flex-direction: column;
    row-gap: 4rem;
    display: flex;
    justify-content: center;

    @media screen and (max-width: 1024px) {
      row-gap: 3.2rem;
    }

    @media screen and (max-width: 800px) {
      row-gap: 2.4rem;
    }

    @media screen and (max-width: 600px) {
      row-gap: 1.6rem;
    }
  }

  &-top {
  }

  &-center {
  }

  &-item {
   
    width: calc(100% * 3 / 12);
    &:first-child {
      width: calc(100% * 4 / 12);

      // @media screen and (max-width: 800px) {
      //   width: 100% !important;
      // }

      @media screen and (max-width: 500px) {
        width: 100% !important;
      }
    }

    &:nth-child(2) {
      
      align-items: center;
      .footer-item-wrap {
        // max-width: 22rem;
        width: fit-content;
        margin: 0 auto;

        @media screen and (max-width: 1024px) {
          margin: unset;
          width: 100%;
        }
      }

     

      @media screen and (max-width: 500px) {
        order: 2;
      }
    }

    &:nth-child(3) {
      width: calc(100% * 2.5 / 12);
      @media screen and (max-width: 500px) {
        order: 3;
      }
    }

    &:last-child {
      width: calc(100% * 2.5 / 12);
      .footer-item-wrap {
        align-items: center;

        @media screen and (max-width: 1024px) {
          flex-direction: row;
          column-gap: 1.2rem;
          // width: fit-content;
          justify-content: space-between;
          align-items: end;
        }

        @media screen and (max-width: 800px) {
          column-gap: 1rem;
        }

        @media screen and (max-width: 600px) {
          column-gap: 0.8rem;
        }

        @media screen and (max-width: 500px) {
          width: 100%;
          order: 4 !important;
        }

        @media screen and (max-width: 460px) {
          flex-direction: column;
          column-gap: 0.6rem;

          row-gap: 1.2rem;
          align-items: start;
        }
      }

      .footer-item-content {
        @media screen and (max-width: 1024px) {
          width: fit-content;
        }

        @media screen and (max-width: 600px) {
          row-gap: 1.2rem;
        }
        .note-text {
          text-align: end;
        }
      }
    }

    @media screen and (max-width: 1024px) {
      width: calc(100% / 2) !important;

      &:first-child {
        order: 1;
      }

      &:nth-child(2) {
        order: 3;
      }

      &:nth-child(3) {
        order: 4;
      }

      &:last-child {
        order: 2;
        @media screen and (max-width: 500px) {
          width: 100% !important;
        }
      }
    }

    @media screen and (max-width: 800px) {
      width: calc(100% / 2) !important;
    }

    @media screen and (max-width:500px) {
      &:last-child{
        width: 100%;
        order: 4;
      }
    }

    @media screen and (max-width: 460px) {
      width: 100% !important;
    }

    @media screen and (max-width: 330px) {
      width: 100% !important;
    }

    &-content {
      width: fit-content;
      height: 100%;
      display: flex;
      flex-direction: column;
      row-gap: 1.6rem;
      // height: 100%;

      @media screen and (max-width: 1200px) {
        row-gap: 1.2rem;
      }

      @media screen and (max-width: 1024px) {
        width: 100%;
        row-gap: 0.8rem;
      }

      @media screen and (max-width: 800px) {
        row-gap: 0.6rem;
      }

      // @media screen and (max-width: 600px) {
      //   row-gap: 0.4rem;
      // }

      //  @media screen and (max-width: 460px) {
      //   row-gap: 0.6rem;
      // }

      .note-text {
        width: fit-content;

        strong{
          color: $color-pri;
        }
      }
    }

    &-content2 {
      display: flex;
      flex-direction: column;
      row-gap: var(--rowContent4);
      width: 100%;
      @media screen and (max-width: 500px) {
          flex-direction: row;
          column-gap: var(--columnContent1);
          justify-content: space-between;
          flex-wrap: wrap;
      }

    
    }

    &-box{
      display: flex;
      flex-direction: column;
      row-gap:1rem;
    }

    &-wrap {
      display: flex;
      // justify-content: center;
      height: 100%;
      flex-direction: column;
      @media screen and (max-width: 1024px) {
        justify-content: start;
      }
    }
  }

  &-list {
    row-gap: 2.4rem;

    @media screen and (max-width: 1024px) {
      row-gap: 1.8rem;
    }

    @media screen and (max-width: 800px) {
      row-gap: 1.6rem;
    }

    @media screen and (max-width: 600px) {
      row-gap: 1.4rem;
    }
  }

  &-bottom {
    margin-top: 2.4rem;
    padding: 1rem 0;
    border-top: 0.1rem solid rgba(185, 156, 72, 0.2);

    @media screen and (max-width: 1200px) {
      margin-top: 2rem;
    }

    @media screen and (max-width: 800px) {
      margin-top: 1.8rem;
    }

    @media screen and (max-width: 600px) {
      margin-top: 1.6rem;
    }

    @media screen and (max-width: 460px) {
      margin-top: 1.4rem;
    }

    &-wrap {
      display: flex;
      justify-content: space-between;
      column-gap: 1.2rem;
      @media screen and (max-width: 600px) {
        flex-direction: column;
        row-gap: 0.6rem;
        align-items: center;
      }
    }
  }

  &-mona {
    display: flex;
    align-items: center;
    column-gap: 0.6rem;
    justify-content: center;
    @media screen and (max-width: 360px) {
      flex-direction: column;
      row-gap: 0.5rem;
    }

    &-img {
      max-width: 18rem;
      display: flex;
      align-items: center;
      img {
        width: 100%;
        height: 100%;
      }
    }

    .note-sm {
      color: $color-text2;
      font-weight: 600;
      white-space: nowrap;
    }
  }

  &-alert {
    margin-top: auto;
    @include fz-16;

    .note-sm{
      @media screen and (max-width:600px) {
        text-align: center;
      }
    }
  }

  &-ip {
    input {
      height: 4.4rem;
      border: 0.1rem solid $color-white;
      width: 100%;
      display: flex;
      align-items: center;
      padding: 0rem 1.6rem;
      color: $color-white;
      @include fz-14;
      &::placeholder {
        color: $color-gray;
        @include fz-14;
      }
    }

    &-wrap {
      display: flex;
      flex-direction: column;
      row-gap: 1.2rem;
      margin-bottom: 2.4rem;

      @media screen and (max-width: 1024px) {
        max-width: 80%;
        width: 100%;
      }

      @media screen and (max-width: 600px) {
        max-width: none;
      }

      .btn {
        width: 100%;
        justify-content: center;
      }
    }
  }

  &-pays{
    display: flex;
    align-items: center;
    column-gap: var(--columnContent0);
  }

  &-pay{
    &-img{
      width: auto;
      height: 2.4rem;

      img{
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }

  &-mxh {
    width: fit-content;
    margin-left: 0;

    @media screen and (max-width: 1024px) {
      margin-left: unset;
    }

    &-list {
      display: flex;
      column-gap: var(--columnContent2);

     
    }

    &-item {
    }

    &-link {
      width: 2.4rem;
      height: 2.4rem;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 0%;
      overflow: hidden;
      transition: all 0.3s ease;

      @include hover {
        transform: scale(1.3);
      }

      

      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }
}
